import { adDataSdkBase } from "./adDataSdkBase";
import { adDataSdkZS } from "./adDataSdkZS";
import { adDataSdkQT } from "./adDataSdkQT";
import { adDataSdkPlatType } from "./adDataSdkDef";
import gSdk from "../platformsdk/platform";
import { SdkHelper } from "../../YAHD_LAYA_SDK/SdkHelper";

export class adDataSdkFactory {
    public sdk: adDataSdkBase = null;
    private _ptype: adDataSdkPlatType = adDataSdkPlatType.None;

    constructor() {
    }

    init(params?: any) {
        SdkHelper.getInstacne().InitAd();
    }

    get isQT() { return this._ptype == adDataSdkPlatType.QT }
    get isZS() { return this._ptype == adDataSdkPlatType.ZS }
}

const adSdk = new adDataSdkFactory();
export default adSdk;