import SdkTools from "../tools/SdkTools";
import BannerController from "../ads/bannerAd/BannerController";

class NavigateTest {

    private static instance: NavigateTest

    public nativeIcon = null;

    public navigateSettle = null;

    /**
     * 测试互推横幅
     */
    public navigateBanner = null;

    /**
     * 测试互推九宫格
     */
    public navigatePortal = null;

    /**
     * 测试互推九宫格UI资源
     */
    public NavigateBoxPortalUIInfo = null;

    /**
     * 测试互推九宫格UI资源加载错误次数
     */
    public navigatePortalErrorTimes: number = 0;

    /**
     * 存储 是否调用过展示测试banner
     */
    public testBannerOnShow: boolean = false;

    /**
    * NavigateTest 单例
    */
    public static getInstance(): NavigateTest {
        if (!NavigateTest.instance) {
            NavigateTest.instance = new NavigateTest()
        }
        return NavigateTest.instance
    }


    /**
     * 展示测试互推ICON
     */
    public showNavigateIcon(width: number, height: number, x: number, y: number) {
        if (this.nativeIcon) {
            console.log("ASCSDK", "Test 已存在互推ICON return");
            return;
        }

        console.log("ASCSDK", "Test showNavigateIcon==================");

        this.nativeIcon = new Laya.Sprite();
        this.nativeIcon.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NavigateIconRes/iconBg.png");
        this.nativeIcon.width = 152;
        this.nativeIcon.height = 152;
        this.nativeIcon.scaleX = width / 152;
        this.nativeIcon.scaleY = height / 152;
        this.nativeIcon.x = x;
        this.nativeIcon.y = y;
        Laya.stage.addChild(this.nativeIcon);
        this.nativeIcon.zOrder = 29999;
    }
    /**
     * 隐藏测试互推ICON
     */
    public hideNavigateIcon() {
        if (this.nativeIcon) {
            console.log("ASCSDK", 'Test hideNavigateIcon====================');
            this.nativeIcon.removeSelf();
            this.nativeIcon = null
        } else {
            console.log("ASCSDK", "Test 不存在互推ICON return");
            return;
        }
    }


    /**
     * 展示测试结算互推
     */
    public showNavigateSettle(type, x, y) {
        if (this.navigateSettle) {
            console.log("ASCSDK", "Test 已存在结算互推 return");
            return;
        }
        BannerController.getInstance().hideBanner();
        console.log("ASCSDK", "Test showNavigateSettle==================");
        var self = this;
        switch (type) {
            case 1:
                {
                    self.navigateSettle = new Laya.Sprite();
                    self.navigateSettle.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NewNavigateSettleRes/navigateSettle1Test.png");
                    Laya.stage.addChild(this.navigateSettle);
                    this.navigateSettle.width = Laya.stage.width * 9 / 10;
                    this.navigateSettle.height = this.navigateSettle.width * 2 / 3;
                    // 横坐标默认居中,纵坐标默认贴近底部
                    this.navigateSettle.x = Laya.stage.width / 2 - this.navigateSettle.width / 2;
                    this.navigateSettle.y = Laya.stage.height - this.navigateSettle.height - y;
                    self.navigateSettle.zOrder = 30000;

                }
                break;
            // case 2:
            //     {
            //         self.navigateSettle = new Laya.Sprite();
            //         self.navigateSettle.x = 0;
            //         self.navigateSettle.y = 0;
            //         self.navigateSettle.zOrder = 30000;

            //         Laya.stage.addChild(self.navigateSettle);

            //         var navigateSettleLeft = new Laya.Sprite();
            //         navigateSettleLeft.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestNavigateSettleLeft.png");
            //         if (Laya.stage.width < Laya.stage.height) {
            //             navigateSettleLeft.width = Laya.stage.width * 0.2;
            //             navigateSettleLeft.height = Laya.stage.width / 0.39;
            //         }
            //         else {
            //             navigateSettleLeft.height = Laya.stage.height - 200;
            //             navigateSettleLeft.width = Laya.stage.height * 0.39;
            //         }
            //         navigateSettleLeft.x = 0;
            //         navigateSettleLeft.y = Laya.stage.height / 2 + navigateSettleLeft.height / 2;
            //         self.navigateSettle.addChild(navigateSettleLeft);

            //         var navigateSettleRight = new Laya.Sprite();
            //         navigateSettleRight.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestNavigateSettleRight.png");
            //         if (Laya.stage.width < Laya.stage.height) {
            //             navigateSettleRight.width = Laya.stage.width * 0.2;
            //             navigateSettleRight.height = Laya.stage.width / 0.39;
            //         }
            //         else {
            //             navigateSettleRight.height = Laya.stage.height - 200;
            //             navigateSettleRight.width = Laya.stage.height * 0.39;
            //         }
            //         navigateSettleRight.x = Laya.stage.width - navigateSettleRight.width;
            //         navigateSettleRight.y = Laya.stage.height / 2 + navigateSettleLeft.height / 2;
            //         self.navigateSettle.addChild(navigateSettleRight);
            //     }
            //     break;
            case 3:
                {
                    self.navigateSettle = new Laya.Sprite();
                    self.navigateSettle.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestNavigateSettleType3.png");
                    if (Laya.stage.width < Laya.stage.height) {
                        self.navigateSettle.width = Laya.stage.width * 0.904;
                        self.navigateSettle.height = self.navigateSettle.width * 0.317;
                    }
                    else {
                        self.navigateSettle.height = Laya.stage.height / 5;
                        self.navigateSettle.width = self.navigateSettle.height / 0.317;
                    }
                    self.navigateSettle.x = x - self.navigateSettle.width / 2;
                    self.navigateSettle.y = y - self.navigateSettle.height / 2;
                    self.navigateSettle.zOrder = 30000;
                    Laya.stage.addChild(self.navigateSettle);
                }
                break;
            case 4:
                {
                    this.navigateSettle = new Laya.Sprite();
                    this.navigateSettle.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestNavigateBoxBanner.png");
                    if (Laya.stage.width < Laya.stage.height) {
                        this.navigateSettle.width = Laya.stage.width;
                        this.navigateSettle.height = this.navigateSettle.width * 13 / 35;
                    } else {
                        this.navigateSettle.height = Laya.stage.height * 0.4;
                        this.navigateSettle.width = this.navigateSettle.height * 35 / 13;
                    }
                    this.navigateSettle.x = Laya.stage.width / 2 - this.navigateSettle.width / 2;
                    this.navigateSettle.y = Laya.stage.height - this.navigateSettle.height - y;
                    this.navigateSettle.zOrder = 29999;
                    Laya.stage.addChild(this.navigateSettle);
                }
                break;
            default:
                console.log("ASCSDK", "无该种类型的结算互推:" + type);
                break;
        }
    }
    /**
     * 隐藏测试结算互推
     */
    public hideNavigateSettle() {
        if (this.navigateSettle) {
            console.log("ASCSDK", 'Test hideNavigateSettle====================');
            this.navigateSettle.removeSelf();
            this.navigateSettle = null
        } else {
            console.log("ASCSDK", "Test 不存在测试结算互推 return");
            return;
        }
    }


    /**
     * 展示测试互推盒子横幅
     */
    public showNavigateBoxBanner() {
        if (this.navigateBanner) {
            console.log("ASCSDK", "Test 已存在测试互推盒子横幅 return");
            return;
        }
        BannerController.getInstance().hideBanner();
        console.log("ASCSDK", 'Test showNavigateBoxBanner====================');

        let self = this;
        self.navigateBanner = new Laya.Sprite();
        self.navigateBanner.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestNavigateBoxBanner.png");
        if (Laya.stage.width < Laya.stage.height) {
            self.navigateBanner.width = Laya.stage.width;
            self.navigateBanner.height = Laya.stage.height * 0.22;
        } else {
            self.navigateBanner.width = Laya.stage.width / 2;
            self.navigateBanner.height = Laya.stage.height * 0.4;
            self.navigateBanner.x = Laya.stage.width / 2 - self.navigateBanner.width / 2;
        }
        self.navigateBanner.y = Laya.stage.height - self.navigateBanner.height;
        self.navigateBanner.zOrder = 29999;
        Laya.stage.addChild(self.navigateBanner);
    }
    /**
     * 隐藏测试互推盒子横幅
     */
    public hideNavigateBoxBanner() {
        if (this.navigateBanner) {
            console.log("ASCSDK", 'Test hideNavigateBoxBanner====================');
            this.navigateBanner.removeSelf();
            this.navigateBanner = null;
        } else {
            console.log("ASCSDK", "Test 不存在测试互推盒子横幅 return");
            return;
        }
    }


    /**
     * 展示测试互推盒子九宫格
     */
    public showNavigateBoxPortal() {

        this.NavigateBoxPortalUIInfo =
        {
            layerBg: null,
            navigatePortal: null,
            exit: null,
        }
        var urlList = [
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/layerBg.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestNavigateBoxPortal.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/nativeClose.png",
        ];
        let self = this;
        Laya.loader.load(urlList, Laya.Handler.create(this, function () {
            self.NavigateBoxPortalUIInfo.layerBg = urlList[0];
            self.NavigateBoxPortalUIInfo.navigatePortal = urlList[1];
            self.NavigateBoxPortalUIInfo.exit = urlList[2];

            this.testBannerOnShow = BannerController.getInstance().testBannerShow;
            BannerController.getInstance().hideBanner();
            console.log("ASCSDK", 'Test showNavigateBoxPortal====================');

            let layerBg = new Laya.Sprite();
            layerBg.loadImage(self.NavigateBoxPortalUIInfo.layerBg);
            layerBg.width = Laya.stage.width;
            layerBg.height = Laya.stage.height;
            layerBg.zOrder = 30003;
            layerBg.alpha = 0.8;
            Laya.stage.addChild(layerBg);
            layerBg.mouseEnabled = true;

            let navigatePortal = new Laya.Sprite();
            navigatePortal.loadImage(self.NavigateBoxPortalUIInfo.navigatePortal);
            navigatePortal.width = 520;
            navigatePortal.height = 628;
            navigatePortal.x = layerBg.width / 2 - navigatePortal.width / 2;
            navigatePortal.y = layerBg.height / 2 - navigatePortal.height / 2;
            layerBg.addChild(navigatePortal);

            let navigateClose = new Laya.Sprite();
            navigateClose.loadImage(self.NavigateBoxPortalUIInfo.exit);
            navigateClose.width = 50;
            navigateClose.height = 50;
            navigateClose.x = navigatePortal.width - navigateClose.width;
            navigateClose.y = navigatePortal.height * 3 / 32;
            navigatePortal.addChild(navigateClose);
            navigateClose.on('click', this, function () {
                layerBg.removeSelf();
                if (self.testBannerOnShow) {
                    BannerController.getInstance().showBanner();
                }
            });
        }));
    }

}

export default NavigateTest