import sdkConfig from "../../sdkConfig";
import SdkTools from "../../tools/SdkTools"

class BlockTest {
    private static instance: BlockTest

    /**
     * 积木广告白包ICON组件
     */
    public nativeIcon: any = null;

    /**
    * BlockTest 单例
    */
    public static getInstance(): BlockTest {
        if (!BlockTest.instance) {
            BlockTest.instance = new BlockTest()
        }
        return BlockTest.instance
    }
    /**
     * 展示积木广告
     */
    public showBlock(type: string, x: number, y: number, blockSize: number) {
        if (this.nativeIcon) {
            console.log("ASCSDK", 'Test 已存在测试积木广告 return');
            return;
        }

        console.log("ASCSDK", 'Test showBlock==========================');

        this.nativeIcon = new Laya.Sprite();
        this.nativeIcon.loadImage(sdkConfig.ossUrl+"/SDK/SDKImage_1_0/NavigateIconRes/iconBg.png");
        this.nativeIcon.width = 200;
        this.nativeIcon.height = 200;
        this.nativeIcon.x = x + this.nativeIcon.width / 2;
        this.nativeIcon.y = y - this.nativeIcon.height / 2;
        this.nativeIcon.zOrder = 29999;
        Laya.stage.addChild(this.nativeIcon);
    }

    /**
     * 关闭积木广告
     */
    public hideBlock() {
        if (this.nativeIcon) {
            console.log("ASCSDK", "Test hideBlock==========================");
            this.nativeIcon.removeSelf();
            this.nativeIcon = null;
        } else {
            console.log("ASCSDK", "Test 不存在积木广告 return");
            return;
        }
    }

}
export default BlockTest