import SdkTools from "../../tools/SdkTools";

/**
 * NativeTest
 */
class NativeTest {

    private static instance: NativeTest

    public nativeIcon: any = null;
    public nativeImage: any = null;

    /**
    * NativeTest 单例
    */
    public static getInstance(): NativeTest {
        if (!NativeTest.instance) {
            NativeTest.instance = new NativeTest()
        }
        return NativeTest.instance
    }

    /**
     * 创建原生ICON
     */
    public createNativeIcon() {

    }


    /**
     * 展示测试原生ICON
     */
    public showNativeIcon(width, height, x, y) {
        if (this.nativeIcon) {
            console.log("ASCSDK", "Test 已存在原生ICON return");
            return;
        }

        console.log("ASCSDK", 'Test showNativeIcon====================');

        this.nativeIcon = new Laya.Sprite();
        this.nativeIcon.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NavigateIconRes/iconBg.png");

        this.nativeIcon.width = width;
        this.nativeIcon.height = height;
        this.nativeIcon.x = x;
        this.nativeIcon.y = y;
        this.nativeIcon.zOrder = 29999;

        Laya.stage.addChild(this.nativeIcon);

    }
    /**
     * 隐藏测试原生ICON
     */
    public hideNativeIcon() {
        if (this.nativeIcon) {
            console.log("ASCSDK", "Test hideNativeICON==============");
            this.nativeIcon.removeSelf();
            this.nativeIcon = null
        } else {
            console.log("ASCSDK", "Test 不存在原生ICON return");
            return;
        }
    }


    /**
     * 展示测试原生大图
     */
    public showNativeImage(width, height, x, y) {
        if (this.nativeImage) {
            console.log("ASCSDK", "Test 已存在原生大图 return");
            return;
        }
        console.log("ASCSDK", 'Test showNativeImage==================');

        this.nativeImage = new Laya.Sprite();
        this.nativeImage.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/fakeNativeImage.png");
        this.nativeImage.width = width;
        this.nativeImage.height = height;
        this.nativeImage.x = x;
        this.nativeImage.y = y;
        this.nativeImage.zOrder = 29999;

        Laya.stage.addChild(this.nativeImage);
        
    }
    /**
     * 隐藏测试原生大图
     */
    public hideNativeImage() {
        if (this.nativeImage) {
            console.log("ASCSDK", "Test hideNativeImage==============");
            this.nativeImage.removeSelf();
            this.nativeImage = null
        } else {
            console.log("ASCSDK", "Test 不存在原生大图 return");
            return;
        }
    }

}

export default NativeTest