    /**
     * 加载原生插屏UI资源
     */
    public loadNativeInstersRes() {
        console.log("ASCSDK", "开始加载原生插屏资源")
        this.NIUIInfo =
        {
            layerBg: null,
            bg: null,
            button: null,
            exit: null,
            mask: null
        }

        var urlList = [
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/layerBg.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/nativeIntersBg_2.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/touchButton.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/nativeClose.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/mask.png"
        ];

        var self = this;

        var onUIAssetsLoaded = function (callback) {
            console.log("ASCSDK", "原生插屏资源加载成功");
            self.isLoadNIUI = callback;
            self.NIUIInfo.layerBg = urlList[0];
            self.NIUIInfo.bg = urlList[1];
            self.NIUIInfo.button = urlList[2];
            self.NIUIInfo.exit = urlList[3];
            self.NIUIInfo.mask = urlList[4];
        }

        Laya.loader.load(urlList, Laya.Handler.create(this, onUIAssetsLoaded));
    }

