        this.nativeBanner.loadImage(this.NativeBannerUIInfo.bannerBg);

        var width = Laya.stage.width;
        var height = Laya.stage.height;

        if (width < height) {
            this.nativeBanner.width = width;
            this.nativeBanner.height = width * 0.18;
        } else {
            this.nativeBanner.width = width / 2;
            this.nativeBanner.height = this.nativeBanner.width * 0.18;
        }
        this.nativeBanner.x = width / 2 - this.nativeBanner.width / 2;
        this.nativeBanner.y = height - this.nativeBanner.height;
