import AdList from "./AdList";
import gSdk from "../../common/platformsdk/platform";
import { UICfg } from "../../scripts/defines/ResDef";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import FakeExitBtn from "./FakeExitBtn";
import { DialogManager } from "../../common/DialogManager";
import ZsPlayInfo from "./ZsPlayInfo";
import { WxBannerScreen } from "../../common/platformsdk/plat-def";

export default class FullScreeAdView extends Laya.Script {
    private headAdList = null;
    private mainAdList = null;
    private continueBtn = null;
    private fakeExitBtn = null;
    private firstClick: boolean = false;
    constructor() {
        super();
    }

    onAwake() {
        super.onAwake();

        var topUI = this.owner.getChildByName("topUI");
        var head, main;
        if (topUI) {
            head = topUI.getChildByName("headAdList");
            main = topUI.getChildByName("mainAdList");
            this.continueBtn = topUI.getChildByName("continueBtn");
            this.fakeExitBtn = topUI.getChildByName("fakeExitBtn");
        }

        var middleUI = this.owner.getChildByName("middleUI");
        if (middleUI) {
            head = head || middleUI.getChildByName("headAdList");
            main = main || middleUI.getChildByName("mainAdList");
            this.continueBtn = this.continueBtn || middleUI.getChildByName("continueBtn");
            this.fakeExitBtn = this.fakeExitBtn || middleUI.getChildByName("fakeExitBtn");
        }

        if (head) {
            this.headAdList = head.addComponent(AdList);
        }
        if (main) {
            this.mainAdList = main.addComponent(AdList);
        }

        this.fakeExitBtn && this.fakeExitBtn.on(Laya.Event.CLICK, this, this.onOpenListAd);
        this.continueBtn && this.continueBtn.on(Laya.Event.CLICK, this, this.onContinue);

    }

    onDestroy() {
        this.fakeExitBtn && this.fakeExitBtn.off(Laya.Event.CLICK, this, this.onOpenListAd);
        this.continueBtn && this.continueBtn.off(Laya.Event.CLICK, this, this.onContinue);

    }

    onEnable() {
        super.onEnable();
        //移除旧sdk代码
        // if (adSdk.sdk.adConfig.electControl('zs_banner_vertical_enable'))
        //     if (this.owner["url"] != UICfg.fullAd)
        //         gSdk.sdk.hideBannerzs(WxBannerScreen.SCREEN_FULL1);
        //     else
        //         gSdk.sdk.hideBannerzs(WxBannerScreen.SCREEN_FULL2);
        // else
            gSdk.sdk.hideBannerzs(WxBannerScreen.SCREEN_NONE);

        var view = this.owner as Laya.View;
        view.top = 0;
        view.bottom = 0;
        view.left = 0;
        view.right = 0;

        var topUI = this.owner.getChildByName("topUI") as Laya.Box;
        var middleUI = this.owner.getChildByName("middleUI") as Laya.Box;
        var bottomUI = this.owner.getChildByName("bottomUI") as Laya.Box;
        var fullUI = this.owner.getChildByName("fullUI") as Laya.Image;
        if (topUI) {
            topUI.top = 0;
            topUI.centerX = 0;
        }

        if (middleUI) {
            middleUI.centerY = 0;
            middleUI.centerX = 0;
        }

        if (bottomUI) {
            bottomUI.bottom = 0;
            bottomUI.centerX = 0;
        }

        if (fullUI) {
            fullUI.top = -10;
            fullUI.bottom = -10;
            fullUI.left = -10;
            fullUI.right = -10;
        }

        this.initFakeExitBtn();
    }

    initFakeExitBtn() {
        let exitBtn: Laya.Button = this.owner.getChildByName("exitBtn") as Laya.Button;
        if (exitBtn) {
            exitBtn.visible = true;

            if (!exitBtn.getComponent(FakeExitBtn)) {
                exitBtn.addComponent(FakeExitBtn);
            }
        }
    }

    onStart() {
        super.onStart();
        var adType = "endPage";
        if (this.headAdList) {
            this.headAdList.requestAdData(adType, true, ZsPlayInfo.SCROLL_HORIZONTAL, null);
        }

        if (this.mainAdList) {
            Laya.timer.once(5, this, () => {
                this.mainAdList.requestAdData(adType, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, false);
            })
        }

        // if (this.mainAdList) {
        //     this.mainAdList.requestAdData(adType, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, false);
        // }
    }

    onContinue() {
        //移除旧sdk代码
        // if (adSdk.sdk.adConfig.electControl('zs_banner_vertical_enable')) {
        //     if (!this.firstClick) {
        //         this.firstClick = true;
        //         //显示banner
        //         var self = this;
        //         if (gSdk.sdk.isUseZS) {
        //             Laya.timer.once(1000, self, function () {
        //                 if (self.owner["url"] != UICfg.fullAd)
        //                     gSdk.sdk.showBannerzs(WxBannerScreen.SCREEN_FULL1);
        //                 else
        //                     gSdk.sdk.showBannerzs(WxBannerScreen.SCREEN_FULL2);

        //                 Laya.timer.once(1000, self, function () {
        //                     gSdk.sdk.hideBannerzs(WxBannerScreen.SCREEN_NONE);
        //                 });
        //             });
        //         }
        //         else {
        //             let showtime = adSdk.sdk.adConfig.electControl("zs_banner_click_show_time");
        //             let hidetime = adSdk.sdk.adConfig.electControl("zs_banner_click_hide_time");
        //             Laya.timer.once(showtime, self, function () {
        //                 if (self.owner["url"] != UICfg.fullAd)
        //                     gSdk.sdk.showBannerzs(WxBannerScreen.SCREEN_FULL1);
        //                 else
        //                     gSdk.sdk.showBannerzs(WxBannerScreen.SCREEN_FULL2);

        //                 Laya.timer.once(hidetime, self, function () {
        //                     gSdk.sdk.hideBannerzs(WxBannerScreen.SCREEN_NONE);
        //                 });
        //             });
        //         }
        //         return;
        //     }
        // }
        this.closeView();
    }

    closeView() {
        //SceneManager.inst.closeScene(this.owner["url"]);
        if (this.owner["url"] != UICfg.fullAd)
            Laya.stage.event("FULL_SCREEN_AD_CLOSE");
        else
            Laya.stage.event("FULL_SCREEN_AD2_CLOSE");
        DialogManager.Ins.sceneClose(this.owner["url"]);
    }

    onOpenListAd() {
        if (this.owner["url"] != UICfg.fullAd) {
            DialogManager.Ins.sceneOpen(UICfg.fullAd, false);
        }
    }
}