import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import UserData from "../common/data/UserData";
import ControlData from "../common/data/ControlData";
import display from "../common/Display";
export default class NativeAdDialog extends ui.commonUI.NativeAdDialogUI {
    private nativeData;
    constructor() {
        super();
        this.popupEffect = null;
        this.height = Laya.stage.height;
        this.icon.on(Laya.Event.CLICK, this, this.nativeAdClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.closeClick);
        this.continueBtn.on(Laya.Event.CLICK, this, this.closeClick);
        this.goBtn.on(Laya.Event.CLICK, this, this.goClick);
        this.isPopupCenter = false;
        this.zOrder = 128;

        if(display.largeScreen())
        {
            this.spLayertop.y += 120;
        }

        if (gSdk.isOP) {
            // this.goBtn.label = adSdk.sdk.adConfig.checkControl('zs_native_click_switch') ? "查看广告" : "跳过广告";
        }
        else if (gSdk.isVV) {
            this.icon.width = 640;
            this.icon.height = 320;
            this.icon.x = 40;
            this.icon.y = 340;

            this.titleTxt.width = 640;
            this.titleTxt.height = 40;
            this.titleTxt.x = 40;
            this.titleTxt.y = 688;
            this.titleTxt.fontSize = 40;

            this.adBg.width = 712;
            this.adBg.height = 527;
            this.adBg.x = 4;
            this.adBg.y = 239;
            this.adBg.skin = "common/vivo_01.png";

            this.closeBtn.x = 51;
            this.closeBtn.y = 352;
            this.closeBtn.width = 40;
            this.closeBtn.height = 40;
            this.closeBtn.skin = "common/vivo_03.png";

            this.goBtn.x = 188;
            this.goBtn.y = 883;
            this.goBtn.width = 344;
            this.goBtn.height = 174;
            this.goBtn.label = "";
            //移除旧sdk代码
            // this.goBtn.skin = adSdk.sdk.adConfig.checkControl('zs_native_click_switch') ? "common/vivo_04.png" : "common/vivo_05.png";

            this.continueBtn.visible = false;

            this.ImgADTip.visible = true;
            this.ImgADTip.x = 618;
            this.ImgADTip.y = 343;

            this.realTitleTxt.visible = true;
            this.realTitleTxt.x = 40;
            this.realTitleTxt.y = 265;
        }
    }

    onEnable() {
        if (gSdk.isOP) {
            //移除旧sdk代码
            // if (adSdk.sdk.adConfig.checkControl('zs_tool_video')) {
            //     //if (adSdk.sdk.adConfig.getnativebtnswitch()) {
            //     if (ControlData.inst.getnativebtnswitch()) {
            //         this.goBtn.y = 751;
            //         this.continueBtn.y = 901;
            //     }
            // }
        }
    }

    private closeCallbackHandler: Laya.Handler;//关闭回调事件
    onOpened(_data) {
        this.nativeData = gSdk.sdk.getNativeData();
        if (this.nativeData) {
            let url = "";
            if (this.nativeData.imgUrlList && this.nativeData.imgUrlList.length > 0) {
                url = this.nativeData.imgUrlList[0];
            } else if (this.nativeData.iconUrlList && this.nativeData.iconUrlList.length > 0) {
                url = this.nativeData.iconUrlList[0];
            } else if (this.nativeData.icon) {
                url = this.nativeData.icon;
            }
            this.icon.skin = url;
            console.log("MyOPPOAD:icon url = ", this.icon.skin);
            this.titleTxt.text = this.nativeData.desc;
            console.log("MyOPPOAD:titleTxt.text = ", this.nativeData.desc);
            this.realTitleTxt.text = this.nativeData.title;
            console.log("MyOPPOAD:NativeAdDialog.ts--nativeAdShow");
            gSdk.sdk.reportAdShow(this.nativeData);
            //let tstr = Platform.isOP ? "跳过广告" : "查看广告";
            //if(!this.goBtn.label) this.goBtn.label = "查看广告";
        }
        if (_data) {
            this.titleImg.visible = true;
            this.titleImg.skin = _data.iswin ? "main/end-01.png" : "main/end-02.png";
        }
        else {
            this.titleImg.visible = false;
        }
        // this.goBtn.skin="uiview/3-14.png";
        // if(DataModule.Ins.zs_native_click_switch() && DataModule.Ins.isSameVersion()){
        //     this.goBtn.skin = "uiview/3-13.png";
        // }
        //PlayerData.getIns().nativeAdClickTimes++;
        //PlayerData.getIns().save();
        if (gSdk.isOP)
            gSdk.sdk.hide_banner();
    }

    private nativeAdClick() {
        if (this.nativeData) {
            console.log("MyOPPOAD:NativeAdDialog.ts--nativeAdClick");
            gSdk.sdk.reportAdClick(this.nativeData);
        }
        console.log("MyOPPOAD:nativeAdClick", this.nativeData.adId);
        this.closeClick();
    }

    private closeClick() {
        // if (gSdk.isOP) {
        //     gSdk.sdk.showBanner();
        // }
        //else  if(Sdk.getIns().platform=="vivo")Sdk.getIns().showBanner(720,1000);
        this.close();
    }

    private goClick() {
        //移除旧sdk代码
        // if (adSdk.sdk.adConfig.electControl('zs_native_click_switch')) {
        //     this.nativeAdClick();
        // } else {
            this.closeClick();
        // }

    }

    onDisable() {
        // if (this.closeCallbackHandler) {
        //     this.closeCallbackHandler.run();
        // }
        Laya.stage.event("NATIVE_AD_CLOSE");
    }

}