import { AdsType } from "./plat-def";
import { AdsData } from "./Ads-data";

export default class SdkBase {
    protected _adsList: { [key: number]: Array<AdsData> } = {};
    protected version: string = "1.1";
    protected cfg: any = null;
    protected openId: string = "";
    protected bannerhided: boolean = true;
    constructor() { }

    init(params?: any) { }

    get ver() {
        return this.version;
    }

    getLaunchOptionsSync(): any {
        return {}
    }

    getSystemInfoSync(): any {
        return {}
    }

    /**右上角菜单位置信息 */
    getRightMenuRectInfo() {
        return {
            width: 0,
            height: 0,
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
        }
    }
    /**
     * 分享
     */
    share(msg?: string, cb?: Function) { }
    /**
     * 播放震动
     */
    playVibrative(isShort = true) { }

    /** 分包加载(微信) */
    loadSubpackage(resList: Array<string>, callback, progressCall) {
        for (let i = 0; i < resList.length; i++) {
            progressCall(i);
        }
        callback && callback();
    }

    /**小游戏跳转 */
    navigateToMiniProgram(data, success = null, fail = null, complete = null) {
        complete && complete();
    }
    /**小游戏跳转（头条） */
    showMoreGamesModal(datalist, success = null, fail = null, complete = null, showCall = null, closeCall = null) { showCall && showCall(2) }

    //********************************************/
    //**********        头条接口    **************/
    //*******************************************/
    startRecord() { }
    shareRecordVideo(call) { }
    stopRecording() { }

    //********************************************/
    //**********        oppo接口    **************/
    //*******************************************/
    /**上报 */
    reportMonitor() { }
    /**获取原生广告数据 */
    getNativeData() { return null }
    /**点击原生广告 */
    reportAdClick(data: any, adtype: AdsType = AdsType.NATIVE_INSTIL) { }
    reportAdClickex(data: any, adtype: AdsType = AdsType.NATIVE_INSTIL) { }
    /**显示原生广告 */
    reportAdShow(data: any, adstype: AdsType = AdsType.NATIVE_INSTIL) { }
    /**添加桌面图标 */
    installShortcut(success: Function, fail: Function, compelete: Function) { }

    //********************************************/
    //**********        vivo接口    **************/
    //*******************************************/

    setKeepScreenOn() { }


    //*********************************************/
    //***********    关卡接口   *******************/
    //********************************************/
    /**关卡开始 */
    startLevel(level: number) {

    }

    /**关卡结束 event=complete:关卡完成,fail:关卡失败*/
    endLevel(level: number, event = 'complete') {

    }

    //*********************************************/
    //***********    广告接口   *******************/
    //********************************************/
    getAdsData(adType: AdsType, index: number = 0): AdsData {
        let adsList = this._adsList[adType];
        if (!adsList) {
            let ads = new AdsData(adType);
            this._adsList[adType] = [];
            this._adsList[adType].push(ads);
        }
        return this._adsList[adType][index];
    }
    createAdsData(adType: AdsType) {
        let ads = new AdsData(adType);
        !this._adsList[adType] && (this._adsList[adType] = []);
        this._adsList[adType].push(ads);
        return ads;
    }

    /**视频广告 */
    createVideoAds() { }
    preloadVedioAds() { }
    showVideo(ads: AdsData) { ads && ads.close(false); }
    /**插屏广告 */
    createInstalAds() { }
    preloadInstalAds() { }
    showInstal(ads: AdsData) { ads && ads.close(false); }
    /**banner 广告 */
    showBanner(style?: any) { }
    /**删除banner 实例*/
    hideBanner(need_pre_create: boolean = true, style?: any) { }
    /**指色专用 */
    showBannerzs(scene: number, style?: any) { }   //scene 0:首页 1:结算页,更多游戏 2:全屏1 3:全屏2
    hideBannerzs(precreatescene: number, style?: any) { } //precreatescene:预创建所需banner,4不用创建
    setBannerShowCount(count: number = 2) { }
    /**隐藏*/
    hide_banner() { }
    bannerMoveBy(top: number = 80, left: number = 0) { }
    bannerMoveTo(top: number = 0, left: number = 0) { }
    /**格子广告(微信) */
    showGridAd(style?: any) { }
    hideGridAd(need_pre_create: boolean = true, style?: any) { }
    hide_gridAd() { }
    gridMoveBy(top: number = 80, left: number = 0) { }
    gridMoveTo(top: number = 0, left: number = 0) { }
    /**qq 应用盒子广告 */
    showAppBoxAd(params?: any) { }
    /**qq 积木广告 */
    showBlockAd(bShow: boolean = true): boolean { return false; }
    hideBlockAd() { }
    destroyBlockAd() { }

    /**显示原生广告 */
    showNativeAds(ads: AdsData) { }
    /**
     * 显示广告
     * @param adType   //广告类型 
     * @param showFunc  //显示回调
     * @param closeFunc //广告回调
     * @param params    //数据
     */
    showAds(adType: AdsType, showFunc: Function, closeFunc: Function, params: any) {
        let ads = this.getAdsData(adType);
        if (ads.isShow) {
            return;
        }
        ads.setAds(showFunc, closeFunc, params)
        ads.isShow = true;
        if (AdsType.VIDEO == adType) {
            this.showVideo(ads);
        } else if (AdsType.INTERSTITIAL == adType) {
            this.showInstal(ads);
        } else if (AdsType.NATIVE_INSTIL == adType) {
            this.showNativeAds(ads);
        }
    }

    getappId() { return "wxa8b94c906ddbe9dc"; }

    /**
     * 获得用户ID 
     */
    get userId() { return this.openId; }

    /**
     * 指色平台调用
     */
    setUserId(userId) {
        this.openId = userId;
    }

    getopenId(callback) { callback && callback(); }

    /**
     * 是否接入指色
     */
    get isUseZS() {
        return false;
    }

}