import TriggerMgr from "./TriggerMgr";
import GameScene from "./GameScene";
import Utils3D from "../scripts/mgr/Utils3D"
import Constant from "../scripts/mgr/Constant"
import AudioManager from "../common/AudioManager";
export default class GameView extends Laya.Script3D {
    private _gameScene:GameScene = GameScene.getInstance();
    /**场景 */
    public scene: Laya.Scene3D = null;
    private _curIndex:number = 0;
    public onEnable(){
    }

    _t2 = 0;
    //进入触发(必须满足有一模型是刚体且 isTrigger = true)
    public onTriggerEnter(other: Laya.PhysicsComponent): void {
        var name = other.owner.name;
        if(!Utils3D.bStart || !Utils3D.bMouseMoving) return;
        if(name == "NailPart_1" && !other.owner["bHit"]){
            let t1 = Laya.timer.currTimer;
            //if(t1-this._t2 >= 20){
                this.onHitNail(other); 
                this._t2 = Laya.timer.currTimer;
            //}
        }
    }
  
    onHitNail(other){//指甲壳
        var node = other.owner as Laya.Sprite3D;
        other.owner["bHit"] = true;
        if(node){
            AudioManager.inst.playVibrator();
            this._curIndex++;
            Utils3D.playParticle(Utils3D.curTool,true,1,node.transform.position);
            node.destroy();
            this._gameScene.updateProgress(344/20);
            if(this._curIndex >= 20){
                this._curIndex = 0;
                Utils3D.bMouseMoving = false;
                Laya.timer.once(300,this,function(){
                    Utils3D.nSteps++;
                    this._gameScene.onCuting();
                })
            }
        }
    }
    public onTriggerExit(other: Laya.PhysicsComponent): void {   
    }

    public onRestartGame(){
        Utils3D.bStart = false;
    }

        /* 开始碰撞时执行*/
   public onCollisionEnter(collision:laya.d3.physics.Collision):void
   {
       //进入触发(必须满足有一模型是刚体且 isTrigger = false)情况
       console.log("进入触发 onCollision name === ",collision.other.owner.name);
   }
    
}


