import SdkTools, { Game_Platform } from "../tools/SdkTools"

class RecordAndShare {
    private static instance: RecordAndShare

    /**
     * 录屏地址
     */
    private videoPath: any = null;

    /**
     * 录屏回调
     */
    private shareVideoCallback: any = null;

    /**
     * 是否自动停止录屏(即传入的录屏时间达到)
     */
    private isAutoStop: boolean = false;

    /**
     * 录屏对象
     */
    private gameRecorder: any = null;

    /**
     * 更多游戏按钮
     */
    private moreGameButton: any = null;

    /**
     * 是否正在展示更多游戏按钮
     */
    private isShowMoreGames: boolean = false;

    /**
     * 更多游戏测试ICON按钮
     */
    private nativeIcon: any = null;

    /**
     * 更多游戏横幅
     */
    private moreGamesBanner: any = null;

    /**
     * RecordAndShare单例
     */
    public static getInstance(): RecordAndShare {
        if (!RecordAndShare.instance) {
            RecordAndShare.instance = new RecordAndShare()
        }
        return RecordAndShare.instance
    }

    /**
     * 分享图文
     */
    public share(templateId) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    tt.shareAppMessage({
                        templateId: templateId, // 替换成通过审核的分享ID
                        query: "",
                        success() {
                            console.log("ASCSDK", "Tiktok 分享成功");
                        },
                        fail(e) {
                            console.log("ASCSDK", "Tiktok 分享失败");
                        }
                    });
                    break;
                }
            default:
                break;
        }
    }

    /**
     * 开始录屏
     */
    public startGameVideo(duration) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    this.videoPath = null;

                    if (!duration) {
                        duration = 10;
                    }
                    this.gameRecorder = tt.getGameRecorderManager();

                    this.gameRecorder.onStart(res => {
                        console.log("ASCSDK", "Tiktok 开始录屏");
                        // do somethine;
                    });

                    this.gameRecorder.onStop(res => {
                        console.log("ASCSDK", 'Tiktok 录屏结束', res.videoPath);
                        this.videoPath = res.videoPath;
                        if (this.shareVideoCallback) {
                            this.shareVideoCallback(this.videoPath);
                            this.shareVideoCallback = null;
                        } else {
                            this.isAutoStop = true;
                        }
                    });

                    this.gameRecorder.onError(errMsg => {
                        console.log("ASCSDK", "Tiktok 录屏错误:" + JSON.stringify(errMsg));
                    });

                    this.gameRecorder.start({
                        duration: duration
                    });
                    break;
                }
            case Game_Platform.GP_KS:
                {
                    this.videoPath = null;

                    this.gameRecorder = kwaigame.createMediaRecorder();
                    if (this.gameRecorder === null) {
                        console.log("ASCSDK", "当前版本App不支持录屏");
                        return;
                    }

                    this.gameRecorder.onStart(res => {
                        console.log("ASCSDK", "KS 录屏开始");
                    });

                    this.gameRecorder.onStop(res => {
                        console.log("ASCSDK", 'KS 录屏结束', JSON.stringify(res));
                        this.videoPath = res.videoID;
                        this.shareVideoCallback(this.videoPath);
                    });

                    this.gameRecorder.onError((err) => {
                        console.log("ASCSDK", "发生录屏错误", JSON.stringify(err));
                    });

                    this.gameRecorder.start();
                    break;
                }
            default:
                break;
        }
    }

    /**
     * 暂停录屏
     */
    public pauseGameVideo() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    console.log("ASCSDK", "Tiktok 暂停录屏");
                    this.gameRecorder && this.gameRecorder.pause();
                    break;
                }
            case Game_Platform.GP_KS:
                {
                    console.log("ASCSDK", "KS 暂停录屏==================");
                    this.gameRecorder && this.gameRecorder.pause();
                    break;
                }
            default:
                break;
        }
    }

    /**
     * 继续录屏
     */
    public resumeGameVideo() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    console.log("ASCSDK", "Tiktok 继续录屏");
                    this.gameRecorder && this.gameRecorder.resume();
                    break;
                }
            case Game_Platform.GP_KS:
                {
                    console.log("ASCSDK", "KS 继续录屏==================");
                    this.gameRecorder && this.gameRecorder.resume();
                    break;
                }
            default:
                break;
        }
    }

    /**
     * 停止录屏
     */
    public stopGameVideo(callback) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    this.shareVideoCallback = callback;
                    console.log("ASCSDK", "Tiktok stopGameVideo=======================");
                    if (this.isAutoStop) {
                        this.shareVideoCallback(this.videoPath);
                        this.shareVideoCallback = null;
                        this.isAutoStop = false;
                        return;
                    }
                    this.gameRecorder && this.gameRecorder.stop();
                    break;
                }
            case Game_Platform.GP_KS:
                {
                    this.shareVideoCallback = callback;
                    console.log("ASCSDK", "KS stopGameVideo==================");
                    this.gameRecorder && this.gameRecorder.stop();
                    break;
                }
            case Game_Platform.GP_Test:
                callback("0");
                break;
            default:
                break;
        }
    }

    /**
     * 分享录屏
     */
    public shareVideo(title, desc, topics, videoPath, callback) {
        console.log("ASCSDK", "Tiktok 分享录屏", videoPath);
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    if (!videoPath) return;
                    tt.shareAppMessage({
                        channel: "video",
                        title: title,
                        desc: desc,
                        extra: {
                            videoPath: videoPath, // 可替换成录屏得到的视频地址
                            videoTopics: topics, //该字段已经被hashtag_list代替，为保证兼容性，建议两个都填写。
                            hashtag_list: topics,
                        },
                        success() {
                            console.log("ASCSDK", "Tiktok 分享视频成功");
                            callback(true);
                        },
                        fail(e) {
                            console.log("ASCSDK", "Tiktok 分享视频失败:" + JSON.stringify(e));
                            callback(false);
                        },
                    });
                    break;
                }
            case Game_Platform.GP_Test:
                {
                    console.log("ASCSDK", "Test 分享视频成功");
                    callback(true);
                    break;
                }
            case Game_Platform.GP_KS:
                {
                    this.gameRecorder && this.gameRecorder.publishVideo({
                        mouldId: title,
                        video: videoPath,
                        callback: (error) => {
                            if (error != null && error != undefined) {
                                console.log("ASCSDK", "分享录屏失败: " + JSON.stringify(error));
                                if (error.code == -10014) console.log("ASCSDK", "录屏时间过短");
                                callback(false);
                                return;
                            }
                            console.log("ASCSDK", "分享录屏成功");
                            callback(true);
                        }
                    })
                    break;
                }
            default:
                break;
        }
    }

    /**
     * 标记精彩瞬间
     */
    public recordClip(before, after) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    this.gameRecorder && this.gameRecorder.recordClip({
                        timeRange: [before, after],
                        success(r) {
                            console.log("ASCSDK", "Tiktok 裁剪唯一索引", r.index); // 裁剪唯一索引
                        }
                    });
                    break
                }
            default:
                break;
        }
    }

    /**
     * 剪辑精彩瞬间
     */
    public clipVideo(videoPath, callback) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    this.gameRecorder && this.gameRecorder.clipVideo({
                        path: videoPath,
                        success(res) {
                            // 由开始5秒 +最后10秒 拼接合成的视频
                            console.log("ASCSDK", "Tiktok 录屏地址", res.videoPath);
                            callback(true, res.videoPath);
                        },
                        fail(e) {
                            console.error('剪辑失败', e);
                            callback(false, videoPath);
                        }
                    });
                    break;
                }
            default:
                break;
        }
    }

    /**
     * 展示更多游戏
     */
    public showMoreGames(ImageAddress, width, height, x, y) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                {
                    if (tt.getSystemInfoSync().platform != "android") {
                        console.log("ASCSDK", "非安卓手机,不能展示更多游戏按钮 return");
                        return;
                    }

                    if (!SdkTools.getInstance().isversionNewThanEngineVersion("1.23.0")) {
                        console.log("ASCSDK", "平台版本过低,不能展示更多游戏按钮,当前版本:" + tt.getSystemInfoSync().SDKVersion);
                        return;
                    }

                    if (this.isShowMoreGames) {
                        console.log("ASCSDK", "正在展示更多游戏按钮 return");
                        return;
                    }
                    this.isShowMoreGames = true;

                    console.log("ASCSDK", "showMoreGames=====================");

                    let windowWidth = Number(tt.getSystemInfoSync().windowWidth);
                    let windowHeight = Number(tt.getSystemInfoSync().windowHeight);

                    // dp转换为px
                    width = width * (windowWidth / Laya.stage.width)
                    height = height * (windowHeight / Laya.stage.height)
                    x = x * (windowWidth / Laya.stage.width)
                    y = y * (windowHeight / Laya.stage.height)

                    this.moreGameButton = tt.createMoreGamesButton({
                        type: "image",
                        image: ImageAddress,
                        style: {
                            left: x,
                            top: y,
                            width: width,
                            height: height,
                            lineHeight: 40,
                            backgroundColor: "#ff0000",
                            textColor: "#ffffff",
                            textAlign: "center",
                            fontSize: 16,
                            borderRadius: 4,
                            borderWidth: 0,
                            borderColor: "#ff0000",
                        },
                        appLaunchOptions: [
                            {
                                appId: 'ttXXXXXX',
                                query: "foo=bar&baz=qux",
                                extraData: {},
                            }
                        ],
                        onNavigateToMiniGameBox(res) {
                            console.log("跳转到小游戏盒子", res);
                        },
                    });

                    this.moreGameButton.onTap(() => {
                        console.log("ASCSDK", "点击更多游戏");
                    });
                }
                break;
            case Game_Platform.GP_Test:
                {
                    if (this.nativeIcon) {
                        console.log("ASCSDK", "已存在测试更多游戏自定义按钮 return");
                        return;
                    }
                    console.log("ASCSDK", "Test showMoreGames=====================");
                    this.nativeIcon = new Laya.Sprite();
                    this.nativeIcon.loadImage(ImageAddress);
                    this.nativeIcon.width = width;
                    this.nativeIcon.height = height;
                    this.nativeIcon.x = x;
                    this.nativeIcon.y = y;
                    this.nativeIcon.zOrder = 29999;
                    Laya.stage.addChild(this.nativeIcon);
                }
                break;
            default:
                break;
        }
    }
    /**
     * 隐藏更多游戏
     */
    public hideMoreGames() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Test:
                if (this.nativeIcon) {
                    console.log("ASCSDK", "Test hideMoreGames=====================");
                    this.nativeIcon.removeSelf();
                    this.nativeIcon = null;
                } else {
                    console.log("ASCSDK", "Test 不存在更多游戏 return");
                    return;
                }
                break;
            case Game_Platform.GP_Tiktok:
                if (this.moreGameButton) {
                    console.log("ASCSDK", "hideMoreGames=====================");
                    this.isShowMoreGames = false;
                    this.moreGameButton.destroy();
                    this.moreGameButton = null;
                } else {
                    console.log("ASCSDK", "Tiktok 不存在更多游戏 return");
                    return;
                }
                break;
            default:
                break;
        }
    }

    /**
     * 更多游戏
     */
    public showMoreGamesModal() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                if (tt.getSystemInfoSync().platform != "android") {
                    console.log("ASCSDK", "非安卓手机,请隐藏更多游戏按钮 return");
                    return;
                }

                if (!SdkTools.getInstance().isversionNewThanEngineVersion("1.33.0")) {
                    console.log("ASCSDK", "平台版本过低,无法跳入游戏中心,当前版本:" + tt.getSystemInfoSync().SDKVersion);
                    return;
                }

                console.log("ASCSDK", "showMoreGamesModal=====================");

                // 打开互跳弹窗
                tt.showMoreGamesModal({
                    appLaunchOptions: [
                        {
                            appId: "ttXXXXXX",
                            query: "foo=bar&baz=qux",
                            extraData: {},
                        },
                    ],
                    success(res) {
                        console.log("success", res.errMsg);
                    },
                    fail(res) {
                        console.log("fail", res.errMsg);
                    },
                });
                break;
            case Game_Platform.GP_Test:
                console.log("ASCSDK", "showMoreGamesModal=====测试点击更多游戏");
                break;
            default:
                break;
        }
    }

    /**
     * 收藏游戏
     */
    public showFavoriteGuide(type, content, position) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                tt.showFavoriteGuide({
                    type: type,
                    content: content,
                    position: position,
                    success(res) {
                        console.log("ASCSDK", "引导组件展示成功");
                    },
                    fail(res) {
                        console.log("ASCSDK", "引导组件展示失败:" + JSON.stringify(res));
                    },
                });
                break;
            default:
                break;
        }
    }

    /**
     * 展示更多游戏横幅
     */
    public showMoreGamesBanner() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Test:
                console.log("ASCSDK", "Test showMoreGamesBanner===============");
                this.moreGamesBanner = new Laya.Sprite();
                this.moreGamesBanner.loadImage("https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/TestRes/TestMoreGamesBanner.png");
                this.moreGamesBanner.width = 900;
                this.moreGamesBanner.height = 312;
                this.moreGamesBanner.x = Laya.stage.width / 2 - this.moreGamesBanner.width / 2;
                this.moreGamesBanner.y = Laya.stage.height - this.moreGamesBanner.height;
                this.moreGamesBanner.zOrder = 30000;
                Laya.stage.addChild(this.moreGamesBanner);
                break;
            case Game_Platform.GP_Tiktok:
                if (tt.getSystemInfoSync().platform != "android") {
                    console.log("ASCSDK", "非安卓手机,不能展示更多游戏横幅");
                    return;
                }
                if (!SdkTools.getInstance().isversionNewThanEngineVersion("1.67.0")) {
                    console.log("ASCSDK", "平台版本过低,不能展示更多游戏横幅,当前版本:" + tt.getSystemInfoSync().SDKVersion);
                    return;
                }
                console.log("ASCSDK", "Tiktok showMoreGamesBanner===============");
                this.moreGamesBanner = tt.createMoreGamesBanner({
                    style: {
                        left: 0,
                        top: 0,
                        width: 150,
                        height: 40,
                        verticalAlign: "bottom",
                        horizontalAlign: "center"
                    },
                    appLaunchOptions: [
                        {
                            appId: "ttXXXXXX",
                            query: "foo=bar&baz=qux",
                            extraData: {},
                        },
                    ],
                });
                this.moreGamesBanner.onError((err) => {
                    console.log("ASCSDK", "Tiktok 更多游戏横幅出错:", JSON.stringify(err));
                })
                this.moreGamesBanner.show();
                break;
            default:
                break;
        }
    }
    /**
     * 隐藏更多游戏横幅
     */
    public hideMoreGamesBanner() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Test:
                console.log("ASCSDK", "Test hideMoreGamesBanner===============");
                if (this.moreGamesBanner) {
                    this.moreGamesBanner.removeSelf();
                    this.moreGamesBanner = null;
                }
                break;
            case Game_Platform.GP_Tiktok:
                console.log("ASCSDK", "Tiktok hideMoreGamesBanner===============");
                this.moreGamesBanner && this.moreGamesBanner.hide();
                break;
            default:
                break;
        }
    }

}
export default RecordAndShare