import IntersController from "../IntersAd/IntersController";

/**
 * 互推盒子广告
 */
export class BoxBannerVivo {

    private static instance: BoxBannerVivo;

    /**
     * 互推盒子Banner
     */
    public boxBannerAd = null;

    /**
     * 是否加载到互推盒子Banner
     */
    public isLoadBoxBanner = false;

    /**
     * 互推盒子Banner加载失败次数
     */
    public boxBannerErrNum = 0;

    public static getInstance(): BoxBannerVivo {
        if (!BoxBannerVivo.instance) {
            BoxBannerVivo.instance = new BoxBannerVivo();
        }
        return BoxBannerVivo.instance;
    }

    /**
     * 创建VIVO互推盒子Banner广告
     * @param boxBannerId
     */
    public createBoxBanner(boxBannerId) {
        console.log('AD', 'VIVO 互推盒子Banner广告初始化', boxBannerId)
        let id = boxBannerId;

        if(!qg.createBoxBannerAd)
        {
            console.log('AD', 'VIVO 互推盒子Banner不存在', boxBannerId)
            return;
        }

        this.boxBannerAd = qg.createBoxBannerAd({
            posId: boxBannerId
        })

        //监听插屏加载完成
        this.boxBannerAd.onLoad(() => {
            console.log('AD', 'VIVO 互推盒子Banner广告加载完成')
            this.isLoadBoxBanner = true;
        })

        //监听插屏广告错误
        this.boxBannerAd.onError(err => {
            console.log('AD', 'VIVO 互推盒子Banner加载失败：' + JSON.stringify(err))
            this.isLoadBoxBanner = false;
            if (err.errCode != 1008) {
                this.boxBannerErrNum++;
                console.log("AD", "VIVO 互推盒子Banner加载错误次数:" + this.boxBannerErrNum);
                if (this.boxBannerErrNum < 5) {
                    setTimeout(() => {
                        this.createBoxBanner(boxBannerId);
                    }, 30 * 1000);
                }
            }
        })

        //监听插屏广告关闭
        this.boxBannerAd.onClose(() => {
            console.log('AD', 'VIVO 互推盒子Banner广告关闭');
            this.isLoadBoxBanner = false;
            // 系统插屏关闭后10s后再次load
            setTimeout(() => {
                this.createBoxBanner(id);
            }, 30 * 1000);
        })
    }

    /**
     * 获取互推盒子Banner是否可以展示
     */
    public getBoxBannerFlag() {
        return this.isLoadBoxBanner;
    }

    /**
     * 展示互推盒子Banner
     */
    public showBoxBanner() {
        if (this.boxBannerAd) {
            console.log("AD", "VIVO showBoxBanner==================");
            this.boxBannerAd.show().then(() => {
                console.log("AD", "VIVO 互推盒子Banner广告展示成功==================");
            }).catch((err) => {
                console.log("AD", "VIVO 互推盒子Banner展示错误:" + JSON.stringify(err));
            })
        } else {
            console.log("AD", "VIVO 未创建互推盒子Banner广告");
            return;
        }
    }

    /**
     * 隐藏互推盒子Banner
     */
    public hideBoxBanner() {
        if (this.boxBannerAd) {
            console.log("AD", "VIVO hideBoxBanner==================");
            this.boxBannerAd.hide()
        } else {
            console.log("AD", "VIVO 未创建互推盒子Banner广告");
        }
    }
}