import SdkTools from "../../tools/SdkTools"

class VideoTest {
  private static instance: VideoTest

  /**
  * VideoTest 单例
  */
  public static getInstance(): VideoTest {
    if (!VideoTest.instance) {
      VideoTest.instance = new VideoTest()
    }
    return VideoTest.instance
  }

  public showVideo(callback) {

    console.log("ASCSDK", "Test showVideo==================");

    var width = Laya.stage.width;
    var height = Laya.stage.height;
    var layerBg = new Laya.Sprite();
    layerBg.pos(width / 2 - 1280, height / 2 - 1280);
    layerBg.size(2560, 2560);
    Laya.stage.addChild(layerBg);
    layerBg.zOrder = 99999;
    layerBg.loadImage("http://minigame.xplaymobile.com/oppo_native_insters%5ClayerBg.png");

    var titleLabel = new Laya.Label();
    titleLabel.font = "Microsoft YaHei";
    titleLabel.text = "视频播放回调的测试";
    titleLabel.fontSize = 30;
    titleLabel.color = "#FFFFFF";
    titleLabel.x = layerBg.width / 2 - titleLabel.width / 2;
    titleLabel.y = layerBg.height / 2 - titleLabel.height / 2 - 100;
    layerBg.addChild(titleLabel);

    var successLabel = new Laya.Label();
    successLabel.font = "Microsoft YaHei";
    successLabel.text = "播放成功";
    successLabel.fontSize = 30;
    successLabel.color = "#FFFFFF";
    successLabel.x = layerBg.width / 2 - successLabel.width / 2 - 200;
    successLabel.y = layerBg.height / 2 - successLabel.height / 2 + 100;
    layerBg.addChild(successLabel);

    var failedLabel = new Laya.Label();
    failedLabel.font = "Microsoft YaHei";
    failedLabel.text = "播放失败";
    failedLabel.fontSize = 30;
    failedLabel.color = "#FFFFFF";
    failedLabel.x = layerBg.width / 2 - failedLabel.width / 2 + 200;
    failedLabel.y = layerBg.height / 2 - failedLabel.height / 2 + 100;
    layerBg.addChild(failedLabel);

    successLabel.on('click', this, function (event) {
      callback(true);
      layerBg.removeSelf();
    });

    failedLabel.on('click', this, function (event) {
      callback(false);
      layerBg.removeSelf();
    });
  }


}

export default VideoTest  