    //试用
    private videoClick(e: Laya.Event) {
        /*TipManager.Ins.showMsgTip("看完视频了,解锁了新关卡",this);
        EventManager.Instance.event("cut",this.gate);
        this.close();
        return;*/
        SdkHelper.getInstacne().ShowRewardVideo((data: any, isEnd: boolean) => {
            if (!isEnd) {
                TipManager.Ins.showMsgTip("观看完整视频才能获得金币");
            }
            else {
                EventManager.Ins.event("cut", this.gate);
                this.close();
                Utils3D.bTryUse = true;
                Utils3D.bTry = true;
                this._gameScene.tryUseCallF();
            }

        }, null, this);
        if (e) e.stopPropagation();
    }

    private selectClick(e: Laya.Event) {
        this.isGateSelect = !this.isGateSelect;
        console.log("this.isGateSelect", this.isGateSelect)
        this.changeSelectGateSt();
        e.stopPropagation();
    }

    private changeSelectGateSt() {
        if (this.unlockcontrol) {
            this.selectImg.skin = this.isGateSelect ? "common/tips-37.png" : "common/tips-36.png";
            this.passBtn.skin = this.isGateSelect ? "common/tips-33.png" : "common/tips-32.png";
        }
        else {
            this.selectImg.skin = this.isGateSelect ? "common/tips-35.png" : "common/tips-34.png";
            this.passBtn.skin = this.isGateSelect ? "common/tips-32.png" : "common/tips-33.png";
        }
    }

    private closeClick(e: Laya.Event) {
        //if(this.isGateSelect){
        if (this.selectImg.skin == "common/tips-34.png" || this.selectImg.skin == "common/tips-37.png") {
            this.videoClick(null);
            return;
        }
        this.close();
        //this._gameScene.gameBegin();
        Utils3D.bTryUse = false;
        this._gameScene.tryUseCallF();
        e.stopPropagation();
    }

    onClosed() {
        //EventManager.Ins.event("closeunlocktooldlg");
    }

    onDisable() {
        SdkHelper.getInstacne().ShowBanner();
    }

    showItem() {
        this.icon.skin = "res/image/item/1-" + Utils3D.nTryuseScene + ".png";
        //this.icon.x = 230;
        //this.tool2.visible = false;
    }

    private nativeAdClick() {
        if (this.nativeData) {
            console.log("MyOPPOAD:NativeAdDialog.ts--nativeAdClick");
            gSdk.sdk.reportAdClickex(this.nativeData);
        }
        console.log("MyOPPOAD:nativeAdClick", this.nativeData.adId);
    }

    private closenative(e: Laya.Event) {
        this.Img_Native.visible = false;
        e.stopPropagation();
    }
