    /**创建原生插屏 */
    createNative(loadCount = 0, adtype: AdsType = AdsType.NATIVE_INSTIL) {
        console.log('MyOPPOAD:createNative begin');
        let ads = this.getAdsData(adtype);
        console.log('MyOPPOAD:ads', ads);
        let id_len = this.cfg.nativeIds.length;
        if (loadCount >= id_len) return;
        console.log('MyOPPOAD:ads.adsIndex, id_len', ads.adsIndex, id_len);
        this.loadNativeData(ads, oppoCfg.nativeIds[ads.adsIndex % id_len], (ret) => {
            if (null == ret) {
                this.createNative(loadCount++, adtype);
            }
        })

        /*
        let self = this;
        this.getNativeAd((data) => {
            if (data){
                self._nativeData = data[0];
            } 
            else {
                self.getNativeAd((data) => {
                    if (data){
                        self._nativeData = data[0];
                    }else{
                        self._nativeData = null;
                    } 
                }, 1);
            }
        }, 0);
        */

        console.log('MyOPPOAD:createNative _nativeData', JSON.stringify(this._nativeData));
        console.log('MyOPPOAD:createNative end');
    }

