    reportAdClick(data: any, adtype: AdsType = AdsType.NATIVE_INSTIL) {
        console.log("MyOPPOAD:reportAdClickdata", JSON.stringify(data));
        let ads = this.getAdsData(adtype);
        if (ads.adInstance) {
            console.log("MyOPPOAD:reportAdClick", JSON.stringify(data));
            ads.adInstance.reportAdClick({
                adId: data.adId
            })
            ads.adData = null;

            this.createNative();
        }
        else console.log("MyOPPOAD:reportAdClick--ads.adInstance is null");
    }

    reportAdClickex(data: any, adtype: AdsType = AdsType.NATIVE_INSTIL) {
        console.log("MyOPPOAD:reportAdClickdata", JSON.stringify(data));
        let ads = this.getAdsData(adtype);
        if (ads.adInstance) {
            console.log("MyOPPOAD:reportAdClick", JSON.stringify(data));
            ads.adInstance.reportAdClick({
                adId: data.adId
            })
        }
        else console.log("MyOPPOAD:reportAdClick--ads.adInstance is null");
    }
    /**显示原生广告 */
    reportAdShow(data: any, adstype: AdsType = AdsType.NATIVE_INSTIL) {
        console.log("MyOPPOAD:reportAdShowdata", JSON.stringify(data));
        let ads = this.getAdsData(adstype);
        if (ads.adInstance) {
            console.log("MyOPPOAD:reportAdShow", JSON.stringify(data));
            ads.adInstance.reportAdShow({
                adId: data.adId
            })
        }
        else console.log("MyOPPOAD:reportAdShow--ads.adInstance is null");
    }

    showNativeAds(ads: AdsData) {
        if (ads.adInstance && ads.adData) {
            ads.show(true, ads.adData)
        } else {
            ads.show(false);
            this.createNative(0, ads.adType);
        }
    }
