import { ui } from "../ui/layaMaxUI";
import TipManager from "../common/TipManager";
import EventManager from "../common/EventManager";
import gSdk from "../common/platformsdk/platform";
import { AdsType } from "../common/platformsdk/plat-def";
import Utils3D from "../scripts/mgr/Utils3D";

export default class UnlockNewDialog extends ui.commonUI.UnlockNewDialogUI {
    constructor() {
        super();
        this.height = Laya.stage.height;
        //this.bg.height = this.height;
    }
    onEnable() {
        this.unlockBtn.on(Laya.Event.CLICK, this, this.onUnLock);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onCloseClick);
        this.bg.on(Laya.Event.CLICK, this, this.onCloseClick)
        this.ani1.play();
        this.updateUnlockUI();
        this.updateLockData();
        Laya.timer.once(2000,this,function(){
            var self = this;
            if(self){
                this.ani1.stop();
                this.close();
            }
        });
    }       

    private rareGateId = 0;
    private type = 0;
    onOpened(data) {
        console.log('UnlockRareNewDialog', data);
        if (data) {
            this.type = data.type;
            //this.lockImg.skin = GameMgr.Instance.getImgByGateId(data.id);
            this.rareGateId = data.id;
        }
    }

    onCloseClick(e) {
        this.ani1.stop();
        this.close();
        e.stopPropagation();
    }

    onUnLock(e) {
        console.log('onUnLock', this.rareGateId)
        this.close();
        if (this.type == 2) {//立即运行
            //DialogManager.Ins.sceneClose("Views/SkinDialog.scene");
            EventManager.Ins.event("cut", this.rareGateId);
        } else if (this.type == 3) {//立即运行
            //DialogManager.Ins.sceneClose("Views/RareUnlockDialog.scene");
            EventManager.Ins.event("cut", this.rareGateId);
        } else {
            //GameMgr.Instance.setIsRareGate(true);
        }
        e.stopPropagation();
    }

    updateUnlockUI(){ //解锁UI
        var unlockLv = Utils3D._curData["UnlockLV"];
        for(var i = 1;i<4;i++){
            var state = Utils3D._curData["UnlockState"][i-1];
            var index = Utils3D._curData["UnlockIndex"][i-1];
            if(i == 1){
                this.lockImg1.skin = "res/image/item/"+state+"_"+index+".png";
            }else if(i == 2){
                this.lockImg2.skin = "res/image/item/"+state+"_"+index+".png";
            }else{
                this.lockImg3.skin = "res/image/item/"+state+"_"+index+".png";
            }
        }
    }

    updateLockData(){ //解锁数据
        var unlockLv = Utils3D._curData["UnlockLV"];
        if(Utils3D.nCurScene <= Utils3D.nTotalScene){
            if(Utils3D.nCurScene >= unlockLv){
                for(var i = 0;i<3;i++){
                    var state = Utils3D._curData["UnlockState"][i];
                    var index = Utils3D._curData["UnlockIndex"][i];
                    //到达关卡解锁
                    Utils3D.tUnlockData[state][index-1] = 1;
                }
                Laya.LocalStorage.setJSON("UnlockData",Utils3D.tUnlockData);
            }
        }
    }

    onDestroy() {
    }
}