import SdkBase from "./sdk-base";
import { SdkPlat } from "./plat-def";
import SdkTT from "./sdk-tt";
import SdkWx from "./sdk-wx";
import SdkVivo from "./sdk-vivo";
import SdkOppo from "./sdk-oppo";
import SdkQQ from "./sdk-qq";
import SdkBaidu from "./sdk-baidu";
import SdkTools, { Game_Platform } from "../../YAHD_LAYA_SDK/tools/SdkTools";

export class Platform {
    public sdk: SdkBase = null;
    private _ptype: SdkPlat = SdkPlat.None

    constructor() {
    }

    init(params?: any) {
        console.log("platform.ts--init()");

        var platform = SdkTools.getPlatform();
        if (platform == Game_Platform.GP_Test) {
            this.sdk = new SdkBase();
            this._ptype = SdkPlat.None;
        }
        else if (platform == Game_Platform.GP_Oppo) {
            this.sdk = new SdkOppo();
            this._ptype = SdkPlat.OP;
        }
        else if (platform == Game_Platform.GP_Vivo) {
            this.sdk = new SdkVivo();
            this._ptype = SdkPlat.VV;
        }
        else if (platform == Game_Platform.GP_Tiktok) {
            this.sdk = new SdkTT();
            this._ptype = SdkPlat.TT;
        }
    }

    get isWX() { return this._ptype == SdkPlat.WX }
    get isQQ() { return this._ptype == SdkPlat.QQ }
    get isVV() { return this._ptype == SdkPlat.VV }
    get isOP() { return this._ptype == SdkPlat.OP }
    get isTT() { return this._ptype == SdkPlat.TT }
    get isBD() { return this._ptype == SdkPlat.BD }
    isSDK(p: SdkPlat) { return this._ptype == p; }
    isNotSdk(p: SdkPlat) { return this._ptype != p; }
}

const gSdk = new Platform();
export default gSdk;