import BannerController from "../ads/bannerAd/BannerController";
import Network from "../network/Network";
import SdkTools from "../tools/SdkTools";
import LayaUI from "../ui/LayaUI";
import NavigateController from "./NavigateController";

class NavigateWX {

    private static instance: NavigateWX

    /**
     * 互推icon背景
     */
    public navigateBg: any = null;

    /**
     * 互推游戏的信息
     */
    public navigateInfom: any = null;

    /**
     * 互推横幅
     */
    public NavigateBanner: any = null;

    /**
     * 互推横幅资源
     */
    public NavigateBannerUITextures: any = null;

    /**
     * 是否加载到互推横幅资源
     */
    public isLoadNavigateBanner: boolean = false;

    /**
     * 互推九宫格资源
     */
    public NavigatePortalUITextures: any = null;

    /**
     * 互推九宫格背景
     */
    public layerBg: any = null;

    /**
     * 是否加载到互推九宫格资源
     */
    public isLoadNavigatePortal: boolean = false;

    /**
    * NavigateWX 单例
    */
    public static getInstance(): NavigateWX {
        if (!NavigateWX.instance) {
            NavigateWX.instance = new NavigateWX()
        }
        return NavigateWX.instance
    }

    /**
     * 展示互推ICON
     */
    public showNavigateIcon(width, height, x, y) {

        if (this.navigateBg) {
            console.log("ASCSDK", "已存在互推ICON return");
            return;
        }

        console.log("ASCSDK", "showNavigateIcon===========================");

        let UI = LayaUI.getInstance();
        let self = this;

        this.navigateBg = new Laya.Sprite();
        this.navigateBg.width = width;
        this.navigateBg.height = height;
        this.navigateBg.x = x;
        this.navigateBg.y = y;

        this.navigateBg.on('click', this, function () {
            self.jumpToMiniGame(self.navigateInfom);
        });

        Laya.stage.addChild(this.navigateBg);
        this.navigateBg.zOrder = 29999;
        var image = new Laya.Sprite();
        image.width = this.navigateBg.width * 0.84;
        image.height = this.navigateBg.height * 0.84;
        this.navigateBg.addChild(image);

        var title = new Laya.Sprite();
        title.loadImage(UI.navigateUITextures.moreGameTexture);
        title.width = this.navigateBg.width * 0.84;
        title.height = this.navigateBg.height * 0.19;
        title.x = 0;
        title.y = height * 0.65;
        title.zOrder = 20;
        image.addChild(title);

        var mask = new Laya.Sprite();
        mask.loadImage(UI.navigateUITextures.maskTexture, Laya.Handler.create(this, function () {
            image.mask = mask;
        }));
        mask.width = width * 0.84;
        mask.height = height * 0.84;
        mask.x = 0;
        mask.y = 0;

        let updateIconInterval = 0;
        var iconupdate = function () {
            if (!self.navigateBg) { return }
            var textrue = self.updateNavigateIcon();
            image.loadImage(textrue);
            image.scale(width / self.navigateBg.width * 0.84, height / self.navigateBg.height * 0.84);

        }
        // 刷新互推ICON
        updateIconInterval = setInterval(iconupdate, 30 * 1000);
        iconupdate();
    }
    /**
     * 刷新互推ICON
     */
    private updateNavigateIcon() {
        console.log("ASCSDK", "updateNavigateIcon===========================")
        var NavigateList = NavigateController.getInstance().navigateList;
        var self = this;
        var allWeight = 0;
        for (var i = 0; i < NavigateList.length; i++) {
            if (NavigateList[i].sort > 0) {
                allWeight += NavigateList[i].sort;
            }
        }

        var random = Math.floor(Math.random() * allWeight);
        var weightNow = 0;
        let inform = null;
        let index = 0;
        for (let i = 0; i < NavigateList.length; i++) {
            if (NavigateList[i].sort == 0) continue;
            if (random >= weightNow && random < weightNow + NavigateList[i].sort) {
                inform = NavigateList[i];
                index = i;
                console.log("ASCSDK", 'weight', random, NavigateList[i].pushGamePackage);
            }
            weightNow += NavigateList[i].sort;
        }
        if (inform) {
            this.navigateInfom = inform;
            return LayaUI.getInstance().NavigateIconTextureList[index];
        }
    }
    /**
     * 隐藏互推ICON
     * @param inform 
     */
    public hideNavigateIcon() {
        LayaUI.getInstance().navigateIconTimeInterval && clearInterval(LayaUI.getInstance().navigateIconTimeInterval);
        if (this.navigateBg) {
            console.log("ASCSDK", "hideNavigateIcon===========================")
            Laya.stage.removeChild(this.navigateBg);
            this.navigateBg = null;
        } else {
            console.log("ASCSDK", "不存在互推ICON return");
            return;
        }
    }


    /**
     * 创建互推横幅
     */
    public createNavigateBanner() {
        console.log("ASCSDK", "WX 开始加载互推横幅资源");
        var self = this;
        this.NavigateBannerUITextures = {
            navigateBanner: null,
        }
        var settleUIUrlList =
            [
                "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NavigateSettleRes/navigateBanner.png"
            ]

        Laya.loader.load(settleUIUrlList, Laya.Handler.create(this, function () {
            self.NavigateBannerUITextures.navigateBanner = settleUIUrlList[0];
            self.isLoadNavigateBanner = true;
            console.log("ASCSDK", "WX 互推横幅资源加载成功");
        }));
    }
    /**
     * 展示互推横幅
     */
    public showNavigateBanner() {
        if (!this.isLoadNavigateBanner) {
            console.log("ASCSDK", "WX 互推横幅资源未加载完成");
            return;
        }
        if (this.NavigateBanner) {
            console.log("ASCSDK", "WX 已存在互推横幅");
            return;
        }

        let NavigateList = NavigateController.getInstance().navigateList;

        if (NavigateList.length < 5) {
            console.log("ASCSDK", "WX 互推样式的游戏数量小于5个 return");
            return;
        }

        BannerController.getInstance().hideBanner();

        console.log("ASCSDK", "showNavigateBanner================");

        var dataArr = [];
        for (let index = 0; index < NavigateList.length; index++) {
            dataArr[index] = NavigateList[index];
            dataArr[index].index = index;
        }

        var getInfom = function () {
            var allWeight = 0;
            for (var i = 0; i < dataArr.length; i++) {
                allWeight += dataArr[i].sort;
            }

            var random = Math.floor(Math.random() * allWeight);
            var weightNow = 0;
            for (let i = 0; i < dataArr.length; i++) {
                if (random >= weightNow && random < weightNow + dataArr[i].sort) {
                    var inform = dataArr[i];
                    dataArr.splice(i, 1);
                    return inform;
                }
                weightNow += dataArr[i].sort;
            }
        }

        let self = this;

        this.NavigateBanner = new Laya.Sprite();
        this.NavigateBanner.loadImage(this.NavigateBannerUITextures.navigateBanner);
        if (Laya.stage.width < Laya.stage.height) {
            this.NavigateBanner.width = Laya.stage.width;
            this.NavigateBanner.height = this.NavigateBanner.width * 13 / 35;
        } else {
            this.NavigateBanner.height = Laya.stage.height * 0.4;
            this.NavigateBanner.width = this.NavigateBanner.height * 35 / 13;
        }
        this.NavigateBanner.x = (Laya.stage.width - this.NavigateBanner.width) / 2;
        this.NavigateBanner.y = Laya.stage.height - this.NavigateBanner.height;
        Laya.stage.addChild(this.NavigateBanner);

        for (let i = 0; i < 5; i++) {
            let inform = getInfom();

            let icon = new Laya.Sprite();
            icon.loadImage(LayaUI.getInstance().NavigateIconTextureList[inform.index]);
            icon.width = this.NavigateBanner.width / 6.5;
            icon.height = icon.width;
            icon.x = this.NavigateBanner.width / 25 + this.NavigateBanner.width / 5.25 * i;
            icon.y = this.NavigateBanner.height / 2.8;
            this.NavigateBanner.addChild(icon);

            let nameLabel = new Laya.Label();
            nameLabel.color = "#000000";
            let gameName = inform.pushGameName;
            if (inform.pushGameName.length > 4) { gameName = gameName.substring(0, 4) + '...'; }
            nameLabel.text = gameName;
            nameLabel.fontSize = 30;
            nameLabel.align = 'center';
            nameLabel.valign = 'middle';
            nameLabel.overflow = 'scroll';
            nameLabel.width = icon.width;
            nameLabel.height = icon.height * 0.4;
            nameLabel.x = 0;
            nameLabel.y = icon.height;
            icon.addChild(nameLabel);

            icon.on('click', this, function () {
                self.jumpToMiniGame(inform);
            })
        }
    }
    /**
     * 隐藏互推横幅
     */
    public hideNavigateBanner() {
        console.log("ASCSDK", "hideNavigateBanner================");
        if (this.NavigateBanner) {
            this.NavigateBanner.removeSelf();
            this.NavigateBanner = null;
        }
    }


    /**
     * 创建互推九宫格
     */
    public createNavigatePortal() {
        console.log("ASCSDK", "WX 开始加载互推九宫格资源");
        var self = this;
        this.NavigatePortalUITextures = {
            layerBg: null,
            navigatePortal: null,
            navigatePortalClose: null,
        }
        var urlList =
            [
                "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NavigateBox/layerBg.png",
                "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NavigateBox/NavigatePortal.png",
                "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NavigateBox/NavigatePortalClose.png",
            ]
        Laya.loader.load(urlList, Laya.Handler.create(this, function () {
            self.NavigatePortalUITextures.layerBg = urlList[0];
            self.NavigatePortalUITextures.navigatePortal = urlList[1];
            self.NavigatePortalUITextures.navigatePortalClose = urlList[2];
            self.isLoadNavigatePortal = true;
            console.log("ASCSDK", "WX 互推九宫格资源加载成功");
        }));
    }
    /**
     * 展示互推九宫格
     */
    public showNavigatePortal() {
        if (!this.isLoadNavigatePortal) {
            console.log("ASCSDK", "WX 互推九宫格资源未加载完成");
            return;
        }
        if (this.layerBg) {
            console.log("ASCSDK", "WX 已存在互推横幅");
            return;
        }

        let NavigateList = NavigateController.getInstance().navigateList;

        if (NavigateList.length < 9) {
            console.log("ASCSDK", "WX 互推样式的游戏数量小于9个 return");
            return;
        }

        console.log("ASCSDK", "showNavigatePortal================");

        var dataArr = [];
        for (let index = 0; index < NavigateList.length; index++) {
            dataArr[index] = NavigateList[index];
            dataArr[index].index = index;
        }

        var getInfom = function () {
            var allWeight = 0;
            for (var i = 0; i < dataArr.length; i++) {
                allWeight += dataArr[i].sort;
            }

            var random = Math.floor(Math.random() * allWeight);
            var weightNow = 0;
            for (let i = 0; i < dataArr.length; i++) {
                if (random >= weightNow && random < weightNow + dataArr[i].sort) {
                    var inform = dataArr[i];
                    dataArr.splice(i, 1);
                    return inform;
                }
                weightNow += dataArr[i].sort;
            }
        }

        let self = this;

        this.layerBg = new Laya.Sprite();
        this.layerBg.loadImage(this.NavigatePortalUITextures.layerBg);
        this.layerBg.width = Laya.stage.width;
        this.layerBg.height = Laya.stage.height;
        this.layerBg.zOrder = 30003;
        this.layerBg.alpha = 0.8;
        Laya.stage.addChild(this.layerBg);
        this.layerBg.on('click', this, function () {
        });

        let navigatePortal = new Laya.Sprite();
        navigatePortal.loadImage(this.NavigatePortalUITextures.navigatePortal);
        navigatePortal.width = 780;
        navigatePortal.height = 942;
        navigatePortal.x = this.layerBg.width / 2 - navigatePortal.width / 2;
        navigatePortal.y = this.layerBg.height / 2 - navigatePortal.height / 2;
        this.layerBg.addChild(navigatePortal);

        let navigateClose = new Laya.Sprite();
        navigateClose.loadImage(this.NavigatePortalUITextures.navigatePortalClose);
        navigateClose.width = 50;
        navigateClose.height = 50;
        navigateClose.x = navigatePortal.width - navigateClose.width * 1.5;
        navigateClose.y = navigatePortal.height / 2 - navigateClose.height / 2 - navigatePortal.height * 12 / 32 + navigateClose.height / 2;
        navigatePortal.addChild(navigateClose);

        navigateClose.on('click', this, function () {
            console.log("ASCSDK", "用户手动关闭互推九宫格");
            self.layerBg.removeSelf();
            self.layerBg = null;
        });


        for (let i = 0; i < 9; i++) {
            let inform = getInfom();

            let icon = new Laya.Sprite();
            icon.loadImage(LayaUI.getInstance().NavigateIconTextureList[inform.index]);
            icon.width = navigatePortal.width / 5;
            icon.height = icon.width;
            icon.x = navigatePortal.width / 2 - icon.width / 2 - 240 + (i % 3) * 240;
            icon.y = navigatePortal.height / 2 + icon.height / 6 + 180 - Math.floor(i / 3) * 240;
            navigatePortal.addChild(icon);

            let nameLabel = new Laya.Label();
            nameLabel.color = "#000000";
            nameLabel.text = inform.pushGameName;
            nameLabel.fontSize = 30;
            nameLabel.align = 'center';
            nameLabel.valign = 'middle';
            nameLabel.overflow = 'scroll';
            nameLabel.width = icon.width;
            nameLabel.height = icon.height * 0.234 + 20;
            nameLabel.x = 0;
            nameLabel.y = icon.height;
            icon.addChild(nameLabel);

            icon.on('click', this, function () {
                self.jumpToMiniGame(inform);
            })
        }
    }


    /**
     * 互推游戏跳转
     * @param inform 
     */
    private jumpToMiniGame(inform) {
        console.log("ASCSDK", "WX jumpToMiniGame===========================")
        wx.navigateToMiniProgram({
            appId: inform.pushGamePackage,
            path: "?foo=bar",
            success(res) {
                Network.getInstance().statistics(inform);
                // 打开成功
                console.log("ASCSDK", "WX 跳转成功");
            },
            fail(err) {
                console.log("ASCSDK", "WX 跳转失败：" + JSON.stringify(err));
            }
        })
    }

}
export default NavigateWX