/**
 * 互推九宫格广告
 */
export class BoxPortalVivo {
    private static instance: BoxPortalVivo;

    /**
     * 互推盒子九宫格
     */
    public boxPortalAd = null;

    /**
     * 是否加载到互推盒子Banner
     */
    public isLoadBoxPortal = false;

    /**
     * 互推盒子Banner加载失败次数
     */
    public boxPortalErrNum=0;

    public static getInstance():BoxPortalVivo{
        if(!BoxPortalVivo.instance)
        {
            BoxPortalVivo.instance = new BoxPortalVivo();
        }
        return BoxPortalVivo.instance;
    }

    /**
     * 创建VIVO互推盒子九宫格广告
     * @param boxPortalId
     */
    public createBoxPortal(boxPortalId)
    {
        console.log('AD', 'VIVO 互推盒子九宫格广告初始化', boxPortalId)
        let id = boxPortalId;

        if(!qg.createBoxPortalAd)
        {
            console.log('AD', 'VIVO 互推盒子九宫格广告不存在', boxPortalId)
            return;
        }


        this.boxPortalAd = qg.createBoxPortalAd({
            posId: boxPortalId,
            image: '',
            marginTop: Laya.stage.height >> 1
        })

        //监听插屏加载完成
        this.boxPortalAd.onLoad(() => {
            console.log('AD', 'VIVO 互推盒子九宫格广告加载完成')
            this.isLoadBoxPortal = true;
        })

        //监听插屏广告错误
        this.boxPortalAd.onError(err => {
            console.log('AD', 'VIVO 互推盒子九宫格加载失败：' + JSON.stringify(err))
            this.isLoadBoxPortal = false;
            if (err.errCode != 1008) {
                this.boxPortalErrNum++;
                console.log("AD", "VIVO 互推盒子九宫格加载错误次数:" + this.boxPortalErrNum);
                if (this.boxPortalErrNum < 5) {
                    setTimeout(() => {
                        this.createBoxPortal(boxPortalId);
                    }, 30 * 1000);
                }
            }
        })

        //监听插屏广告关闭
        this.boxPortalAd.onClose(() => {
            console.log('AD', 'VIVO 互推盒子九宫格广告关闭');
            this.isLoadBoxPortal = false;
            // 系统插屏关闭后10s后再次load
            setTimeout(() => {
                this.createBoxPortal(id);
            }, 30 * 1000);
        })
    }

    /**
     * 获取互推盒子九宫格可以展示
     */
    public getBoxPortalFlag(){
        return this.isLoadBoxPortal;
    }

    /**
     * 展示互推盒子Banner
     */
    public showBoxPortal(){
        if (this.boxPortalAd) {
            console.log("AD", "VIVO showBoxPortal==================");
            this.boxPortalAd.show().then(() => {
                console.log("AD", "VIVO 互推盒子九宫格广告展示成功==================");
            }).catch((err) => {
                console.log("AD", "VIVO 互推盒子九宫格展示错误:" + JSON.stringify(err));
            })
        } else {
            console.log("AD", "VIVO 未创建互推盒子九宫格广告");
            return;
        }
    }

    /**
     * 隐藏互推盒子九宫格
     */
    public hideBoxPortal(){
        if(this.boxPortalAd)
        {
            console.log("AD", "VIVO hideBoxPortal==================");
            this.boxPortalAd.hide()
        }else {
            console.log("AD", "VIVO 未创建互推盒子九宫格广告");
        }
    }
}