import IntersController from "./ads/IntersAd/IntersController";
import ASCAd from "./ASCAd";
import sdkConfig from "./sdkConfig";
import SdkTools from "./tools/SdkTools";

export class SdkHelper {
    private static instance: SdkHelper;

    public static getInstacne(): SdkHelper {
        if (!SdkHelper.instance) {
            SdkHelper.instance = new SdkHelper();
        }
        return SdkHelper.instance;
    }

    /**
     * 第三方广告模块单例
     * @private
     */
    private _adv!: ASCAd

    /**
     * 视频广告逻辑层面上的回调
     * @private
     */
    private _videoADVLogicCallback: any;

    /**
     * 录屏地址
     * @private
     */
    private _videoPath: any;
    
    public InitAd(callBack = null) {
        this._adv = ASCAd.getInstance();
        this._adv.initAd(this._onADVInited.bind(this, callBack));
        if (!navigator.onLine) this._adv.showToast('当前无网络，请联网后重新尝试');
    }

    private _onADVInited(callBack = null) {
        this.ShowNativePrivacy(() => {
            callBack && callBack();
        });
    }

    public ShowBanner() {
        //this._adv.hideBanner();
        this._adv.showBanner();
    }

    public HideBanner() {
        this._adv.hideBanner();
    }

    /**
     * 展示插屏
     * @param isCheckState 是否正常状态插屏 默认为true状态（true代表 过关 结算 等正常弹出插屏）
     * @param call 插屏被关闭的回调
     */
    public ShowInter(isCheckState: boolean = true, call = null) {
        if (isCheckState == false && !IntersController.getInstance().SW_IsAllInterOpen) return;
        this._adv.hideBanner();
        this._adv.showInters(call);
    }

    /**
     * 页面打开变化
     * @param isOpen
     * @constructor
     */
    public ViewChange(isOpen) {
        if (isOpen) {
            this.ShowInter();
            this.HideBanner();
        } else {
            //this.ShowBanner();
        }
    }

    /**
     * 展示激励视频
     * @param callback
     * @param params
     * @param context
     * @constructor
     */
    public ShowRewardVideo(callback: any, params: any, context: any) {
        if (!this._adv) return;
        if (!this._adv.getVideoFlag()) {
            this._adv.showToast('暂无视频');
            return;
        }
        Laya.SoundManager.setMusicVolume(0);
        Laya.SoundManager.setSoundVolume(0);
        this._videoADVLogicCallback = {
            callback: callback,
            params: params,
            context: context,
        };
        this._adv.showVideo(this._onVideoClose.bind(this));
    }

    private _onVideoClose(isEnd: boolean) {
        if (!this._videoADVLogicCallback) return;
        Laya.SoundManager.setMusicVolume(1);
        Laya.SoundManager.setSoundVolume(1);
        try {
            let callback = this._videoADVLogicCallback.callback;
            let params = this._videoADVLogicCallback.params;
            let context = this._videoADVLogicCallback.context;
            if (callback) callback.call(context, params, isEnd)
        } catch (e) {
            console.error(e);
        } finally {
            this._videoADVLogicCallback = undefined;
        }
    }

    /**展示原生贴片
     * @param node  查看广告节点
     * @param detailNode 查看详情节点
     * @param size
     * @param worldPos
     * @param callBack
     * @param context
     */
    public NativePatch(node, detailNode, size, worldPos, callBack, context) {
        if (this._adv.getImageNativeFlag()) {
            this._adv.showNativePatch(node, detailNode, size.width, size.height, worldPos.x, worldPos.y)
            callBack.apply(context);
        }
    }

    /**
     * 隐藏原生贴片
     */
    public HideNativePatch() {
        this._adv.hideNativePatch();
    }

    /**
     * 创建桌面图标
     * @param imagePath 桌面图标路径
     * @param left  左上角为0
     * @param top   左上角为0
     * @param color
     * @constructor
     */
    public CreateShortcut(left, top, color, imagePath =sdkConfig.ossUrl+ "/SdkConfig/addDesk.png",) {
        this._adv.createShortcut(imagePath, left, top, color);
    }

    /**
     * 隐藏桌面图标
     * @constructor
     */
    public HideShortcut() {
        this._adv.hideShortcut();
    }

    /**
     * 显示互推广告
     * @param type 0--互推Banner  1--互推九宫格
     * @constructor
     */
    public ShowAppBox(type) {
        this._adv.showAppBox(type);
    }

    /**
     * 隐藏互推广告
     * @param type 0--互推Banner  1--互推九宫格
     * @constructor
     */
    public HideAppBox(type) {
        this._adv.hideAppBox(type);
    }

    /**
     * 开始录屏
     */
    public StartGameVideo(time) {
        this._adv.startGameVideo(time);
    }

    /**
     * 停止录屏
     */
    public StopGameVideo() {
        let that = this;
        this._adv.stopGameVideo(videoPath => {
            that._videoPath = videoPath;
        })
    }

    /**
     * 分享录屏
     * @param title  标题（通常是游戏名）
     * @param des  描述
     * @param topics ["",""] 话题  游戏名  抖音小游戏
     * @param callback
     * @constructor
     */
    public ShareVideo(title, des, topics, callback) {
        this._adv.shareVideo(title, des, topics, this._videoPath, callback);
    }

    /**
     * 土司
     * @param str
     * @constructor
     */
    public ShowToast(str: string) {
        this._adv.showToast(str);
    }

    /**
     * 手机震动
     * @constructor
     */
    public PhoneVibrate(type: string) {
        this._adv.phoneVibrate(type)
    }

    /**
     * 展示用户隐私政策
     */
    public ShowNativePrivacy(agreeCall: Function = null, refuseCall: Function = null) {
        this._adv.showNativePrivacy(agreeCall, refuseCall);
    }
}