/**
 * 桌面图标控制器
 */
import SdkTools, {Game_Platform} from "../../tools/SdkTools";
import {ShortcutVivo} from "./ShortcutVivo";

export class ShortcutController {
    /**
     * 桌面图标开关控制器
     */
    SW_ShortcutSwitch: boolean = true;

    /**
     * 加载桌面图标定时器
     */
    loadShortcutCheck;

    private static instance: ShortcutController;

    public static getInstacne(): ShortcutController {
        if (!ShortcutController.instance) {
            ShortcutController.instance = new ShortcutController();
        }
        return ShortcutController.instance;
    }

    /**
     * 创建桌面图标
     * @constructor
     */
    CreateShortcut(imagePath, left, top, color) {
        if (!this.SW_ShortcutSwitch) {
            console.log("ASCAD", "未开启桌面图标！")
        }
        console.log("ASCAD", "ShortcutController 创建桌面图标")
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                ShortcutVivo.getInstacne().CreateShortcut(imagePath, left, top, color);
                break;
            default:
                console.log("AD", "暂时没有开启桌面图标");
                break;
        }
    }

    /**
     * 展示桌面图标
     * @constructor
     */
    ShowShortcut() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                ShortcutVivo.getInstacne().ShowShortcut();
                break;
            default:
                console.log("AD", "暂时没有开启桌面图标");
                break;
        }
    }

    /**
     * 隐藏桌面图标
     * @constructor
     */
    HideShortcut() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                ShortcutVivo.getInstacne().HideShortcut();
                break;
            default:
                console.log("AD", "暂时没有开启桌面图标");
                break;
        }
    }

    getShortcutFlag() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                ShortcutVivo.getInstacne().GetShortcutFlag();
                break
            case Game_Platform.GP_Test:
                return false;
                break;
        }
    }

    /**
     * 检测桌面图标是否加载成功（弃用）
     */
    checkShortcutLoadSucc() {
        if (this.loadShortcutCheck) {
            clearTimeout(this.loadShortcutCheck);
        }
        if (this.SW_ShortcutSwitch && this.getShortcutFlag()) {
            this.ShowShortcut();
        } else {
            console.log("AD", "VIVO 创建桌面图标没有准备好")
            this.loadShortcutCheck = setTimeout(function () {
                this.checkShortcutLoadSucc();
            }.bind(this), 5 * 1000)
        }
    }
}