//游戏主场景
export default class GameScene {
    /**单例*/
    private static _instance: GameScene = null;
    public _scene = null;
    public _camera1: Laya.Camera;
    private JsonPath: string = "res/level/leveldata.json";
    public _JsonData = null;
    _endCamera = null;

    Level_lh = null;
    public mainLayer = null;
    public drawPaper = null; //贴纸
    public drawColor = null; //图案
    public _Arm = null;

    bRestart = false;
    private state1 = new Laya.AnimatorState();
    private state2 = new Laya.AnimatorState();
    camareNode = null;
    cutNode = null;
    swearNode = null;
    millNode = null;
    drawNode = null;
    slider = null;
    node1 = null;
    node2 = null;
    nailPart = null;
    curNail = null;
    curNailAll = null;
    hand = null;
    endPart = null;

    mainNode = null;
    gameNode = null;
    //_tempDay = -1;
    /* 构造函数*/
    constructor() {
    }
    /**
   * 获取单例
   */
    static getInstance() {
        return this._instance || (this._instance = new GameScene())
    }
    static get instance(): GameScene {
        //GameScene.create();
        if (GameScene._instance == null) {
            GameScene._instance = new GameScene();
        }
        return GameScene._instance;
    }

    /* 创建*/
    public static create(): void {
        if (GameScene._instance == null) {
            GameScene._instance = new GameScene();
        }
    }

    //初始化场景
    public initScene(bNext = false): void {
        Utils3D.GetCacheData();
        if (!bNext) {
            Laya.Scene.open("gameUI/LoadSceneView.scene");
        } else {
            //Laya.loader.create(SceneCfg.game,Laya.Handler.create(this,this.onLoadedComplete));
            this.reStartGame();
            this.loadGameState();
        }
    }

    private onLoadedJosn(): void {
        var jsonData: JSON = null;
        jsonData = Laya.Loader.getRes(this.JsonPath);
        this._JsonData = jsonData;
        Utils3D._curData = jsonData["L" + Utils3D.nCurScene.toString()];

        //游戏大步骤
        Utils3D.nGameState = Utils3D._curData["GameState"][Utils3D.nStateSteps - 1];
        Utils3D.nStateLen = Utils3D._curData["GameState"].length;
        if (Utils3D._curData["DrawAt"]) {
            Utils3D.nDrawLen = Utils3D._curData["DrawAt"].length;
        }

        Utils3D.sNailShape = Utils3D._curData["Shape"] - 1;//Math.floor(Math.random() * 3);
    }

    public onLoadedComplete(scene): void {
        //Laya.Stat.show();
        this._scene = scene;
        this.onLoadedJosn();
        //Laya.stage.addChildAt(scene, 0);
        Laya.Scene.open(UICfg.GameStartView);//打开ui
        this.mainNode = this._scene.getChildByName("zhucaidan");
        let cameraM = this.mainNode.getChildByName("Main Camera");
        cameraM.farPlane = 100;
        //this.playWommenAni(true);
        this.gameNode = this._scene.getChildByName("Game");
        this.gameNode.active = false;

        let lhStr = "res/Unity/LayaScene_PlayerMode/Conventional/player.lh"
        Laya.loader.create(lhStr, Laya.Handler.create(this, this.loadPlayer));

        this.camareNode = this.gameNode.getChildByName("CameraAll");
        this.endPart = this.camareNode.getChildByName("Particle") as Laya.Sprite3D;
        this._camera1 = this.camareNode.getChildByName("Main Camera");
        //部分手机不支持hdr导致黑屏 一定要设置为false
        this._camera1.enableHDR = false;
        this.hand = this.gameNode.getChildByName("Hand") as Laya.Sprite3D;
        let slider = this.camareNode.getChildByName("Slide001") as Laya.Sprite3D;
        slider.active = false;

        this.receiveShadows()
    }

    _playerNode
    loadPlayer(node) {
        if (node) {
            this._playerNode = node;
            this.mainNode.addChild(node);
            this.playWommenAni(true);
            this.loadGameState();
        }
    }

    wommen = null;
    clothes1 = null;
    playWommenAni(bShow) {
        if (this.wommen) {
            this.wommen.active = false;
        }
        if (this.clothes1) {
            this.clothes1.active = false;
        }
        let index = Math.floor(Math.random() * 3) + 1;
        //let indexC = Math.floor(Math.random() * 3) + 1;
        this.wommen = this._playerNode.getChildByName("Woman00" + index.toString()) as Laya.Sprite3D;
        this.clothes1 = this._playerNode.getChildByName("Clothes00" + index.toString()) as Laya.Sprite3D;
        if (!bShow) {
            this.wommen.active = false;
            this.clothes1.active = false;
            return;
        }
        this.wommen.active = true;
        this.clothes1.active = true;
        Utils3D.playAnimations(this.wommen, "In");
        Utils3D.playAnimations(this.clothes1, "In");
        Laya.timer.once(3000, this, function () {
            if (this.wommen) {
                Utils3D.playAnimations(this.wommen, "idle");
                Utils3D.playAnimations(this.clothes1, "idle");
            }
        })

        let hand1 = this.hand.getChildByName("Hand") as Laya.SkinnedMeshSprite3D;
        let mat = hand1.skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;
        mat.albedoColor = Constant.HandColor[index - 1];

        let nail = this.hand.getChildByName("Nail") as Laya.SkinnedMeshSprite3D;
        let mat1 = nail.skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;
        mat1.albedoColor = Constant.HandColor[index - 1 + 3];
    }

    loadGameState() {
        Utils3D.nSteps = 1;
        Utils3D.nGameState = Utils3D._curData["GameState"][Utils3D.nStateSteps - 1];
        var lhStr = "res/Unity/LayaScene_LevelMode/Conventional/LvNode" + Utils3D.nGameState.toString() + ".lh";

        if (Utils3D.nGameState > 3) {
            lhStr = "res/Unity/LayaScene_LevelMode/Conventional/LvNode4.lh"
        }

        Laya.loader.create(lhStr, Laya.Handler.create(this, this.onStateLoaded));
    }

    //大步骤模型加载
    private onStateLoaded(node) {
        if (Utils3D.nGameState == Constant.State_Cut) {
            if (this.cutNode) {
                this.cutNode.destroy();
                this.cutNode = null;
            }
            this.cutNode = node.clone() as Laya.Sprite3D;
            this.cutNode.active = true;
            this.nailPart = this.cutNode.getChildByName("NailPart") as Laya.Sprite3D;
            this._scene.addChild(this.cutNode);
            //this.slider.active = true;
            if (Utils3D.bTry) {
                this.initMainLayer();
                Utils3D.bTry = false;
                this.onCuting();
            } else {
                this.onCuting();
            }
        } else if (Utils3D.nGameState == Constant.State_Swear) {
            if (this.swearNode) {
                this.swearNode.destroy();
                this.swearNode = null;
            }
            this.swearNode = node.clone() as Laya.Sprite3D;
            this.node1 = this.swearNode.getChildByName("GameObject") as Laya.Sprite3D;
            this._scene.addChild(this.swearNode);
            if (this.mainLayer) { //没有第一步的情况
                this.onSwearing();
            } else {
                if (Utils3D.bTry) {
                    this.initMainLayer();
                    Utils3D.bTry = false;
                }
            }
        } else if (Utils3D.nGameState == Constant.State_Mill) {
            if (this.millNode) {
                this.millNode.destroy();
                this.millNode = null;
            }
            this.millNode = node.clone() as Laya.Sprite3D;
            this.millNode.active = true;
            // let curNail = this.millNode.getChildByName("Nail_yuan") as Laya.Sprite3D;
            // curNail.active = false
            if (Utils3D.sNailShape > 0) {
                this.curNail = this.millNode.getChildAt(Utils3D.sNailShape - 1) as Laya.Sprite3D;
            } else {
                this.curNail = this.millNode.getChildAt(Utils3D.sNailShape) as Laya.Sprite3D;
            }
            this.curNail.active = true;
            this._scene.addChild(this.millNode);

            this.onMilling();
        } else if (Utils3D.nGameState >= Constant.State_Draw) {
            this.drawNode = node.clone() as Laya.Sprite3D;
            this._scene.addChild(this.drawNode);
            var lhStr = "res/Unity/LayaScene_GameScene/LayaScene_ShipinMode/Conventional/zhuangshiAll.lh";
            Laya.loader.create(lhStr, Laya.Handler.create(this, this.onLoadZhuangshi));
            if (Utils3D.nCurFinger == 1) {
                let nailNode = this.drawNode.getChildByName("nail");
                this.curNailAll = nailNode.getChildAt(Utils3D.sNailShape) as Laya.Sprite3D;
                //nailAll.active = true;
            }
            if (Utils3D.nGameState == Constant.State_Draw) {
                this.onDrawing();
            } else if (Utils3D.nGameState == Constant.State_Draw2) {
                this.onPaperDrawing();
            } else if (Utils3D.nGameState == Constant.state_Beautify) {
                this.onBeautify();
            }

        }
    }

    receiveShadows() {
        let lightNode = this.gameNode.getChildByName("Light");
        var drecLight = lightNode.getChildByName("Directional Light") as Laya.DirectionLight;
        // Use soft shadow.
        drecLight.shadow = true;
        drecLight.shadowDistance = 1.2;
        drecLight.shadowResolution = 2048;
        //模糊等级,越大越高,效果更好，更耗性能
        drecLight.shadowPCFType = 1;
        //生成阴影贴图数量
        drecLight.shadowPSSMCount = 1;

        var floor = this.gameNode.getChildByName("Plane") as Laya.MeshSprite3D;
        floor.meshRenderer.receiveShadow = true;

        let hand = this.hand.getChildByName("Hand") as Laya.SkinnedMeshSprite3D;
        hand.skinnedMeshRenderer.castShadow = true;
    }

    changePlaneColor() {
        var floor = this.gameNode.getChildByName("Plane") as Laya.MeshSprite3D;
        let mat = floor.meshRenderer.material as Laya.BlinnPhongMaterial;
        let r = Math.floor(Math.random() * 4);//0-3
        mat.albedoColor = Constant.PlaneColor[r];
    }

    index = 0;
    _Intervals = 0;
    updateAni() {
        if (Utils3D.bMouseMoving && Utils3D.nGameState == Constant.State_Swear) {
            //console.log("updateAni   Gamestate  ====",Utils3D.nGameState);
            if (this.index <= 90) {
                let named = "Nail" + (this.index - 1);
                let nailed = this.node1.getChildByName(named) as Laya.Sprite3D;
                if (nailed) {
                    nailed.active = false;
                }

                let name = "Nail" + this.index;
                let nail = this.node1.getChildByName(name) as Laya.Sprite3D;
                if (nail) {
                    nail.active = true;
                }
                this.index++;
                //this.onShocking();
                this.updateProgress(344 / 91);
                this.onShocking();
            } else {
                this.index = 0;
                Laya.timer.clear(this, this.updateAni);
                Utils3D.nSteps++;
                this.onSwearing();
            }
        } else if (Utils3D.bMouseMoving && Utils3D.nGameState == Constant.State_Mill) {
            if (this.index < 60) {
                this.index += 1;
                if (Utils3D.nSteps == 6) {
                    //let knife = this.millNode.getChildByName("Knife");
                    //this.playAniByFrames(knife,"Damo2",130,this.index,220);
                    //this.playAniByFrames1(this.curNail,"millNailR",130,this.index,190);
                    this.playAniByFrames1(this.curNail, "millNailR", 130, this.index, Constant.AniFrames[Utils3D.sNailShape][0]);
                } else if (Utils3D.nSteps == 4) {
                    //右边打磨指甲
                    //let knife = this.millNode.getChildByName("Knife");
                    //this.playAniByFrames(knife,"Damo1",40,this.index,220);
                    //this.playAniByFrames1(nail,"",this.index,2);
                    //this.playAniByFrames1(this.curNail,"millNailL",40,this.index,190);
                    this.playAniByFrames1(this.curNail, "millNailL", 40, this.index, Constant.AniFrames[Utils3D.sNailShape][0]);
                }
                this.updateProgress(344 / 120);
                this.onShocking();
            } else {
                this.index = 0;
                Laya.timer.clear(this, this.updateAni);
                Utils3D.nSteps++;
                this.onMilling();
            }
        } else if (Utils3D.nGameState == Constant.State_Draw2) {
            if (Utils3D.bPlayingAni) {
                Utils3D.curTool.transform.localPositionZ += 0.001;
                this.drawPaper.onHitRay();
            } else {
                //Laya.timer.clear(this,this.updateAni);
            }
        }
    }

    onShocking() { //震动
        let t1 = Laya.timer.currTimer;
        if (t1 - this._Intervals >= 30) {
            AudioManager.inst.playVibrator();
            this._Intervals = Laya.timer.currTimer;
        }
    }

    onTouchDown() {
        if (Utils3D.nGameState == Constant.State_Draw2 && !Utils3D.bPlayingAni
            && Utils3D.nSteps == 3) {
            Utils3D.bPlayingAni = true;
            //let bottle = Utils3D.curTool.getChildByName("Bottle001") as Laya.Sprite3D;
            Utils3D.playAnimations(Utils3D.curTool, "Apply1");
            // Utils3D.nSteps++;
            // Laya.timer.once(1000,this,function(){
            //     Utils3D.playAnimations(bottle,"apply",2);
            //     Laya.timer.loop(40,this,this.updateAni);
            //     if(this.mainLayer){
            //         this.mainLayer.hideHandUI();
            //     }
            //     Utils3D.nSteps++;
            // })
        } else if (Utils3D.nGameState == Constant.State_Cut || Utils3D.nGameState == Constant.State_Swear
            || Utils3D.nGameState == Constant.State_Draw) {
            if (!Utils3D.bShowSlider) {
                Utils3D.bShowSlider = true;
                this.showSlider(false)
            }
        } else if (Utils3D.nGameState == Constant.State_Mill) {
            if (!Utils3D.bParticling && this.curNail) {
                let knife = this.curNail.getChildByName("Knife001") as Laya.Sprite3D;
                Utils3D.bParticling = true;
                Utils3D.playParticle(knife, true, 1);
                Utils3D.playParticle(knife, true, 2);
            }

            if (this.mainLayer && Utils3D.nSteps == 3) {
                this.mainLayer.hideHandUI();
            }

            if (Utils3D.nSteps == 3) {
                Utils3D.nSteps++;
            }
        } else if (Utils3D.nGameState == Constant.state_Beautify) {
            if (Utils3D.nSteps == 3) {
                Utils3D.nSteps++;
                this.onBeautify();
            }
        }
    }

    onMouseUp() {
        if (Utils3D.nGameState == Constant.State_Mill && Utils3D.bParticling) {
            if (this.curNail) {
                let knife = this.curNail.getChildByName("Knife001") as Laya.Sprite3D;
                Utils3D.playParticle(knife, false, 1);
                Utils3D.playParticle(knife, false, 2);
            }
            Utils3D.bParticling = false;
        } else if (Utils3D.nGameState == Constant.State_Draw2 && Utils3D.bPlayingAni) {
            Utils3D.playAnimations(Utils3D.curTool, "Apply2");
            Utils3D.bPlayingAni = false;
        }
    }

    public playAniByFrames(node: Laya.Sprite3D, strAni: string, nStart, index, totalF) {
        var ani = node.getComponent(Laya.Animator);
        if (ani && !Utils3D.bGameWin) {
            //创建一个动画动作状态
            this.state1.name = strAni + (index);
            //起始时间与结束时间的设置为0-1的百分比数值） 截取时间点 / 动画的总时长
            this.state1.clipStart = (nStart + index) / totalF;
            this.state1.clipEnd = (nStart + index + 0.5) / totalF;
            //得到默认动画赋值给Clip（getDefaultState默认动画为Unity中animation的数组顺序0下标的动画）
            this.state1.clip = ani.getDefaultState().clip;
            //动画播放是否循环
            this.state1.clip.islooping = false;
            //添加动画状态到动画组件里
            ani.addState(this.state1);
            //播放动画
            ani.play(this.state1.name);

            if (Utils3D.bMouseMoving) {
                //console.log("   AniFrames index === "+index+" Gamestate =  "+Utils3D.nGameState)
            }
        }
    }

    public playAniByFrames1(node: Laya.Sprite3D, strAni: string, nStart, index, totalF) {
        var ani = node.getComponent(Laya.Animator);
        if (ani && !Utils3D.bGameWin) {
            //创建一个动画动作状态
            this.state2.name = strAni + (index);
            //起始时间与结束时间的设置为0-1的百分比数值） 截取时间点 / 动画的总时长
            this.state2.clipStart = (nStart + index) / totalF;
            this.state2.clipEnd = (nStart + index + 0.5) / totalF;
            //得到默认动画赋值给Clip（getDefaultState默认动画为Unity中animation的数组顺序0下标的动画）
            this.state2.clip = ani.getDefaultState().clip;
            //动画播放是否循环
            this.state2.clip.islooping = false;
            //添加动画状态到动画组件里
            ani.addState(this.state2);
            //播放动画
            ani.play(this.state2.name);
        }
    }

    reStartGame() {
        Utils3D._curData = null;
        if (Utils3D.bTryUse) {
            Utils3D._curData = this._JsonData["L" + Utils3D.nTryuseScene.toString()];
        } else {
            Utils3D._curData = this._JsonData["L" + Utils3D.nCurScene.toString()];
            Laya.Scene.open(UICfg.GameStartView);//打开ui
        }

        Utils3D.nStateLen = Utils3D._curData["GameState"].length;
        if (Utils3D._curData["DrawAt"]) {
            Utils3D.nDrawLen = Utils3D._curData["DrawAt"].length;
        }
        Utils3D.sNailShape = Utils3D._curData["Shape"] - 1;
    }

    reSetCamera() {
        // this._camera1.transform.position = new Laya.Vector3(0, 25, 5);
        // this._camera1.transform.localRotationEulerX = -80
        // this._camera1.transform.localRotationEulerY = 0;
    }

    initMainLayer() {
        if (this.mainLayer) {
            this.mainLayer.destroy();
            this.mainLayer = null;
        }
        if (this.mainLayer == null) {
            Laya.Scene.open('gameUI/MainLayer.json', false, null, Laya.Handler.create(this, (scene: Laya.Scene) => {
                this.mainLayer = scene as MainLayer;
                //试用界面
                //Laya.Scene.open(UICfg.UseDialog,false);
                this.gameBegin();
            }));
            //this.mainLayer = new view.MainLayer();
        }

    }

    updateLvPoint(bShow = true) {
        if (this.mainLayer) {
            this.mainLayer.updateLvPoint(bShow);
            if (!bShow) {
                this.mainLayer.showRetButton(false);
            }
        }
    }

    updateProgress(value) {
        if (this.mainLayer) {
            this.mainLayer.updateProgress(value);
        }
    }

    setProgress(num) {
        if (this.mainLayer) {
            this.mainLayer.setProgress(num);
        }
    }

    protected _data: any;
    gameBegin() { //开始游戏
        this.bRestart = false;
        Utils3D.bStart = true;
        this.mainNode.active = false;
        this.gameNode.active = true;
        //this.loadGameState();
        if (Utils3D.nGameState == Constant.State_Cut) {
            this.showSlider(true);
            //Laya.Scene.open(UICfg.UseDialog,false);
        } else if (Utils3D.nGameState == Constant.State_Swear) {
            this.onSwearing(false);
        }
        this.changePlaneColor();
        if (Utils3D.bTryUse) {
            gSdk.sdk.startLevel(Utils3D.nTryuseScene + 1000);
        } else {
            gSdk.sdk.startLevel(Utils3D.nShowScene);
        }
        Laya.timer.once(1200, this, function () {
            Utils3D.bPlayerMove = false;
        })
    }

    changeSwearTexture() {
        var resStr = "res/image/NailColour_" + (Utils3D.nLookSelect + 1) + ".png"//Utils3D._curData["swearPng"];
        var res: Array<any> = [
            { url: resStr, type: Laya.Loader.TEXTURE2D },
        ];
        Laya.loader.create(res, Laya.Handler.create(this, function () {
            let container = this.swearNode.getChildByName("Container") as Laya.Sprite3D;
            let c1 = container.getChildByName("Container001") as Laya.MeshSprite3D;
            let c2 = container.getChildByName("Container002") as Laya.MeshSprite3D;
            let img = Laya.loader.getRes(resStr);
            let nail0 = this.node1.getChildAt(0) as Laya.MeshSprite3D;
            //共享材质替换贴图，其余模型也会换掉
            let mat = nail0.meshRenderer.sharedMaterial as Laya.BlinnPhongMaterial;
            mat.albedoTexture = img;

            let mat1 = c1.meshRenderer.material as Laya.BlinnPhongMaterial;
            let mat11 = c1.meshRenderer.materials[1] as Laya.BlinnPhongMaterial;
            mat11.albedoColor = Constant.ColorVec3[Utils3D.nLookSelect];
            mat1.albedoTexture = img;
            c1.meshRenderer.castShadow = true;
            let mat2 = c2.meshRenderer.material as Laya.BlinnPhongMaterial;
            let mat22 = c2.meshRenderer.materials[1] as Laya.BlinnPhongMaterial;
            mat22.albedoColor = Constant.ColorVec3[Utils3D.nLookSelect];
            mat2.albedoTexture = img;
            c2.meshRenderer.castShadow = true;

            Utils3D.nSteps++;
            this.onSwearing();

        }), Laya.Handler.create(this, function (value) { }, null, false));

    }

    //商店购买道具贴图更换
    changeToolT(node, gamestate) {
        if (node) {
            var index = Utils3D.shopSelect[gamestate.toString()];
            var resStr = "res/image/" + gamestate + "_" + (index + 1) + ".png";
            // if(Utils3D.bTryUse){
            //     let tryState = Utils3D._curData["TryUseTool"][0];
            //     let tryIndex = Utils3D._curData["TryUseTool"][1];
            //     if(tryState == gamestate){
            //         resStr = "res/image/"+gamestate+"_"+tryIndex+".png";
            //     }
            // }
            var res: Array<any> = [{ url: resStr, type: Laya.Loader.TEXTURE2D }];
            Laya.loader.create(res, Laya.Handler.create(this, function () {
                let mat1 = node.meshRenderer.material as Laya.BlinnPhongMaterial;
                let img1 = Laya.loader.getRes(resStr);
                mat1.albedoTexture = img1;
                if (gamestate == 1) {
                    let node2 = node.getChildAt(0) as Laya.MeshSprite3D;
                    let mat2 = node2.meshRenderer.material as Laya.BlinnPhongMaterial;
                    mat2.albedoTexture = img1;
                }
            }), Laya.Handler.create(this, function (value) { }, null, false));

        }
    }

    tryUseCallF() {
        if (!Utils3D.bTryUse) {
            this.initMainLayer();
        } else {
            this.removeNode();
            this.initScene(true);
        }
    }

    onCuting() {
        Utils3D.curTool = this.cutNode.getChildByName("tool") as Laya.Sprite3D;
        Utils3D.curTool.active = true;
        let tool = Utils3D.curTool.getChildAt(0) as Laya.MeshSprite3D;
        if (Utils3D.nSteps == 1) {
            this.nailPart.active = true;
            tool.meshRenderer.castShadow = true;
            tool.addComponent(GameView);
            Utils3D.playAnimations(this.hand, "idle");
            Utils3D.playParticle(Utils3D.curTool, false, 1);
            this.changeToolT(tool, 1);
        } else if (Utils3D.nSteps == 2) {
            Utils3D.playAnimations(tool, "Out");
            Laya.timer.once(500, this, function () {
                if (!Utils3D.bStart || Utils3D.nSteps == 1) return;
                Utils3D.curTool.active = false;
                this.checkFinish();
                if (this.cutNode) {
                    this.cutNode.destroy();
                    this.cutNode = null;
                }
                this.slider.active = false;
                Utils3D.bShowSlider = false;
                this.updateLvPoint();
            })
        }
    }

    //涂抹指甲油
    onSwearing(bDefault = true) {
        let container = this.swearNode.getChildByName("Container") as Laya.Sprite3D;
        let c1 = container.getChildByName("Container001") as Laya.MeshSprite3D;
        let c2 = container.getChildByName("Container002") as Laya.MeshSprite3D;
        let ruler = this.swearNode.getChildByName("Ruler") as Laya.MeshSprite3D;
        if (Utils3D.nSteps == 1) {
            ruler.meshRenderer.castShadow = true;
            this.changeToolT(ruler, 2);
            this.mainLayer.RefreshList();
            if (bDefault) {
                ruler.active = true;
                Utils3D.playAnimations(ruler, "In");
                Laya.timer.once(1000, this, function () {
                    this.showMainList();
                })
            } else { //开始直接涂抹指甲油
                Laya.timer.once(200, this, function () {
                    ruler.active = true;
                    Utils3D.playAnimations(ruler, "In");
                })
                Laya.timer.once(1000, this, function () {
                    this.showMainList();
                })
            }
        } else if (Utils3D.nSteps == 2) {
            container && (container.active = true);
            Laya.timer.once(1000, this, function () {
                if (Utils3D.bStart) {
                    c1 && (c1.active = false);
                    c2 && (c2.active = true);
                    this.onShocking();
                }
            })

            Laya.timer.once(2000, this, function () {
                this.onShocking();
            })

            Laya.timer.once(3000, this, function () {
                if (Utils3D.bStart && Utils3D.nGameState == Constant.State_Swear) {
                    let brushAni = container.getChildByName("Brush") as Laya.Sprite3D;
                    brushAni.active = false;
                    let nailBall = container.getChildByName("NailBall") as Laya.Sprite3D;
                    nailBall.active = false;

                    Utils3D.nSteps++;
                    this.onSwearing();
                }
            })
            //this.mainLayer.handUIMove();

        } else if (Utils3D.nSteps == 3) {
            if (Utils3D.curTool) {
                Utils3D.curTool.destroy();
                Utils3D.curTool = null;
            }
            this.showSlider(true);
            Utils3D.curTool = this.swearNode.getChildByName("Brush") as Laya.MeshSprite3D;
            Utils3D.curTool.active = true;
            let brush = Utils3D.curTool.getChildByName("Brush001") as Laya.MeshSprite3D;
            brush.meshRenderer.castShadow = true;
            this.node1.active = true;
            Laya.timer.loop(20, this, this.updateAni);
        } else if (Utils3D.nSteps == 4) {
            if (this.mainLayer) {
                this.mainLayer.updateTask(false);
            }
            this.showSlider(false);
            let brush = Utils3D.curTool.getChildByName("Brush001") as Laya.MeshSprite3D;
            Utils3D.playAnimations(brush, "Out");
            //Laya.Tween.to(this,{tooltoPosX:0.1},1000,null,Laya.Handler.create(this,function(){
            Laya.timer.once(1000, this, function () {
                if (!Utils3D.bStart || Utils3D.nGameState == 1) return;
                Utils3D.curTool.active = false;
                let container = this.swearNode.getChildByName("Container") as Laya.Sprite3D;
                let ruler = this.swearNode.getChildByName("Ruler") as Laya.Sprite3D;

                Utils3D.playAnimations(container, "Out");
                Utils3D.playAnimations(ruler, "Out");
                //Utils3D.playAnimations(this.node2,"idle");
                Utils3D.nLookSelect = 0;
                Utils3D.bShowSlider = false;
                let nailNode = this.swearNode.getChildByName("NailShow");
                this.curNailAll = nailNode.getChildAt(0) as Laya.Sprite3D;
                Laya.timer.once(1000, this, function () {
                    if (!Utils3D.bStart) return;
                    container.active = false;
                    ruler.active = false;
                    this.checkFinish();
                })
            })
        }
    }

    showMainList() {
        if (this.mainLayer) {
            this.mainLayer.showList(true);
            this.mainLayer.updateTask(true);
        }
    }

    checkFinish() {
        Utils3D.nSteps = 1;
        if (Utils3D.bStart) {
            Utils3D.nStateSteps++;
            if (Utils3D.nStateSteps > Utils3D.nStateLen) {
                //this.GameFinish();
                this.endPart.active = true;
                Laya.timer.once(3000, this, this.showEndOff);
            } else {
                if (gSdk.isWX) {
                    if (this.mainLayer) {
                        this.mainLayer.showCompleteBtn(true);
                    }
                } else {
                    this.loadGameState();
                }
            }
        }
    }

    onMilling() { //打磨指甲
        //let cover = this.millNode.getChildByName("Cover");
        if (Utils3D.nSteps == 1) {
            //cover.active = true;
            this.node1.active = false;
            let knife = this.curNail.getChildByName("Knife001") as Laya.Sprite3D;
            let knife1 = knife.getChildByName("Knife002") as Laya.MeshSprite3D;
            knife1.meshRenderer.castShadow = true;
            this.changeToolT(knife1, 3);
            this.curNail.active = true;
            Utils3D.playParticle(knife, false, 1);
            Utils3D.playParticle(knife, false, 2);
            //Utils3D.playIntervalAni(knife,"knifeIdle",0,10);
            Utils3D.playIntervalAni(this.curNail, "nailIdle", 0, 5);
            Laya.timer.once(500, this, function () {
                Utils3D.nSteps++;
                this.onMilling();
            })
            this.updateLvPoint();
        } else if (Utils3D.nSteps == 2) {
            //knife.active = true;
            //Utils3D.playIntervalAni(knife,"kifeIn",10,40);
            Utils3D.playAnimations(this.curNail, "In");
            if (Utils3D.sNailShape == 3) {
                Utils3D.playAnimations(this.camareNode, "mozhijia");
            }
            Laya.timer.once(1000, this, function () {
                if (!Utils3D.bStart) return;
                Utils3D.nSteps++;
                this.mainLayer.handUIMove();
                this.onMilling();
            })
        } else if (Utils3D.nSteps == 3) {
            Laya.timer.loop(25, this, this.updateAni);
        } else if (Utils3D.nSteps == 5) { //左边打磨
            //Utils3D.playIntervalAni(knife,"LtoR",100,130);
            if (Utils3D.sNailShape < 3) { //需要打磨两边
                Utils3D.playIntervalAni(this.curNail, "LtoR", 100, 130);
                Utils3D.playAnimations(this.camareNode, "mozhijia");
                Utils3D.nSteps++;

                Laya.timer.once(1000, this, function () {
                    if (!Utils3D.bStart) return;
                    Laya.timer.loop(25, this, this.updateAni);
                })
            } else {
                Utils3D.nSteps = 7;
                this.onMilling();
            }
        } else if (Utils3D.nSteps == 7) {
            //out动作包含knife out
            Utils3D.playIntervalAni(this.curNail, "Out", 190, 220);
            Utils3D.playAnimations(this.camareNode, "mozhijiaBack");
            let nailNode = this.millNode.getChildByName("NailShow");
            this.curNailAll = nailNode.getChildAt(Utils3D.sNailShape) as Laya.Sprite3D;
            Laya.timer.once(1000, this, function () {
                this.checkFinish();
            })
        }

    }

    changeAni() {
        if (this.curNail) {
            this.curNail.active = false;
        }

        this.curNailAll.active = true;
        this.node1.active = false;
        Utils3D.playAnimations(this.camareNode, "ShowAll");
        Utils3D.playAnimations(this.hand, "ShowAll");
        Utils3D.playAnimations(this.curNailAll, "ShowAll");
        let handpoint = this.drawNode.getChildByName("HandPoint") as Laya.Sprite3D;
        Utils3D.playAnimations(handpoint, "ShowAll");
        Laya.timer.once(1000, this, function () {
            Utils3D.playAnimations(this.camareNode, "Show");
            let aniStr = "Show" + Utils3D._curData["DrawAt"][Utils3D.nCurFinger - 1] / 2;
            Utils3D.playAnimations(this.hand, aniStr);
            Utils3D.playAnimations(this.curNailAll, aniStr);
            Utils3D.playAnimations(handpoint, aniStr);
        })
    }

    //多个手指切换
    switchFingeAni() {
        Utils3D.playAnimations(this.camareNode, "ShowBack");
        let aniStr = "ShowBack" + Utils3D._curData["DrawAt"][Utils3D.nCurFinger - 2] / 2;
        Utils3D.playAnimations(this.hand, aniStr);
        Utils3D.playAnimations(this.curNailAll, aniStr);
        let handpoint = this.drawNode.getChildByName("HandPoint") as Laya.Sprite3D;
        Utils3D.playAnimations(handpoint, aniStr);
        Laya.timer.once(1000, this, function () {
            let aniStrS = "Show" + Utils3D._curData["DrawAt"][Utils3D.nCurFinger - 1] / 2;
            Utils3D.playAnimations(this.hand, aniStrS);
            Utils3D.playAnimations(this.curNailAll, aniStrS);
            Utils3D.playAnimations(handpoint, aniStrS);
            Utils3D.playAnimations(this.camareNode, "Show");
        })
    }

    changeDrawT() {
        Utils3D.bDrawed = false;
        var resStrT = "res/image/NailPicture_" + (Utils3D.nLookSelect + 1) + ".png";
        var resStrS = "res/image/start_" + Utils3D.nCurFinger + ".png";
        var res: Array<any> = [
            // { url: "res/image/start_1.png", type: Laya.Loader.TEXTURE2D },
            { url: resStrS, type: Laya.Loader.TEXTURE2D },
            { url: resStrT, type: Laya.Loader.TEXTURE2D },
        ];
        Laya.loader.create(res, Laya.Handler.create(this, function () {
            if (this.drawColor == null) {
                this.drawColor = new Coloring1(this._scene, this.drawNode);
            } else {
                this.drawColor.updateMode();
                this.drawColor.getTargetPixs();
            }
        }), Laya.Handler.create(this, function (value) { }, null, false));

        if (Utils3D.curTool) {
            //Utils3D.curTool.destroy();
            Utils3D.curTool = null;
        }
        Utils3D.curTool = this.drawNode.getChildByName("Pen") as Laya.Sprite3D;
        Utils3D.curTool.active = true;

        this.showSlider(true);
    }

    onDrawing() { //图案
        let pen = this.drawNode.getChildByName("Pen") as Laya.Sprite3D;
        let realPen = pen.getChildByName("Pen001") as Laya.MeshSprite3D;
        if (Utils3D.nSteps == 1) {
            if (Utils3D.nCurFinger > 1) {
                this.switchFingeAni();
            } else {
                this.changeAni();
            }
            this.updateLvPoint();
            this.setProgress(0);
            realPen.meshRenderer.castShadow = true;
            if (this.mainLayer) {
                this.mainLayer.RefreshList();
                Laya.timer.once(2000, this, function () {
                    this.showMainList();
                });
            }
        } else if (Utils3D.nSteps == 2) {
            Utils3D.bPlayingAni = false;
            //在镜头之外播放了？
            Utils3D.playAnimations(pen, "Out");
            Utils3D.nSteps++;
            this.onDrawing();
            Laya.timer.once(1000, this, function () {
                pen && (pen.active = false);
            })
        } else if (Utils3D.nSteps == 3) {
            this.stepsOver();
            if (Utils3D.nCurFinger > Utils3D.nDrawLen) {
                this.endPart.active = true;
                Laya.timer.once(3000, this, this.showEndOff);
                this.checkChange();
            } else {
                //this.onDrawing();
                this.checkFingerState();
            }
        }
    }

    //玩家选择贴纸
    changePaperT() {
        //Utils3D.bDrawed = false;
        var resStrS = "res/image/start_" + Utils3D.nCurFinger + ".png";
        var resStrT1 = "res/image/NailSticker_" + (Utils3D.nLookSelect + 1) + ".png";
        var resStrT2 = "res/image/NailSticker2_" + (Utils3D.nLookSelect + 1) + ".png";
        var resStrT3 = Utils3D._curData["targetPng"];
        var res: Array<any> = [
            { url: resStrS, type: Laya.Loader.TEXTURE2D },
            { url: resStrT1, type: Laya.Loader.TEXTURE2D },
            { url: resStrT3, type: Laya.Loader.TEXTURE2D },
            { url: resStrT2, type: Laya.Loader.TEXTURE2D },
        ];

        Laya.loader.create(res, Laya.Handler.create(this, function () {
            if (this.drawPaper == null) {
                this.drawPaper = new Coloring(this._scene, this.drawNode);
            } else {
                this.drawPaper.updateMode();
                this.drawPaper.getTargetPixs();
            }
            Utils3D.nSteps++;
            this.onPaperDrawing();
        }), Laya.Handler.create(this, function (value) { }, null, false));
    }

    onPaperDrawing() { //贴纸
        let atIndex = Utils3D._curData["DrawAt"][Utils3D.nCurFinger - 1];
        let stickNode = this.drawNode.getChildByName("Sticker");
        let sticker = stickNode.getChildAt(atIndex / 2 - 1) as Laya.MeshSprite3D;
        //let bottle = this.drawNode.getChildByName("Bottle") as Laya.Sprite3D;
        let pen = this.drawNode.getChildByName("Pen") as Laya.Sprite3D;
        pen.active = false;

        if (Utils3D.nSteps == 1) {
            sticker.active = false;
            if (Utils3D.nCurFinger > 1) {
                this.switchFingeAni();
            } else {
                this.changeAni();
            }

            this.updateLvPoint();
            this.setProgress(0);
            if (this.mainLayer) {
                this.mainLayer.RefreshList();
                Laya.timer.once(2000, this, function () {
                    this.showMainList();
                });
            }
            console.log("onPaperDrawing  GameState == ", Utils3D.nGameState);
        } else if (Utils3D.nSteps == 2) {
            sticker.active = false;
            if (Utils3D.curTool) {
                Utils3D.curTool = null;
            }

            Utils3D.curTool = this.drawNode.getChildByName("BottleNode") as Laya.Sprite3D;
            sticker.active = true;
            this.curNailAll.active = true;
            //bottle.active = true;
            Utils3D.playAnimations(stickNode, "In");
            Laya.timer.once(800, this, function () {
                Utils3D.curTool && (Utils3D.curTool.active = true);
                Utils3D.playAnimations(Utils3D.curTool, "In");
                Utils3D.nSteps++;
                //this.mainLayer.handUIMove();
            })

            Laya.timer.once(1500, this, function () {
                if (Utils3D.bStart && this.mainLayer) {
                    this.mainLayer.handSlip(true);
                }
                Utils3D.bDrawed = false;
            })

        }
        else if (Utils3D.nSteps == 4) {
            this.setProgress(344);
            Utils3D.bPlayingAni = false;
            Utils3D.playAnimations(stickNode, "Out");
            Utils3D.playAnimations(Utils3D.curTool, "Out");
            Laya.timer.once(1000, this, function () {
                if (Utils3D.curTool) {
                    Utils3D.curTool.transform.localPositionZ = 0.2817621;
                    Utils3D.curTool.active = false;
                    sticker.active = false;
                }
            })

            this.stepsOver();
            Laya.timer.clear(this, this.updateAni);
            if (Utils3D.nCurFinger > Utils3D.nDrawLen) {
                this.endPart.active = true;
                Laya.timer.once(2500, this, this.showEndOff)
                this.checkChange();
            } else {
                this.checkFingerState();
            }
        }
    }

    stepsOver() {
        Utils3D.nCurFinger++;
        Utils3D.nLookSelect = 0;
        Utils3D.nSteps = 1;
        Utils3D.bGameWin = false;
        this.setProgress(344);
        this.mainLayer.updateTask(false);
        this.mainLayer.handSlip(false);
        this.mainLayer.handSlip1(false);
    }

    curZhuangshi = null;
    onBeautify() { //装饰
        //let zsAll = this.drawNode.getChildByName("zhuangshiAll") as Laya.Sprite3D;
        let handpoint = this.drawNode.getChildByName("HandPoint") as Laya.Sprite3D;
        let atIndex = Utils3D._curData["DrawAt"][Utils3D.nCurFinger - 1];
        if (Utils3D.nSteps == 1) {
            if (Utils3D.nCurFinger > 1) {
                this.switchFingeAni();
            } else {
                this.changeAni();
            }
            this.updateLvPoint();
            this.setProgress(0);
            if (this.mainLayer) {
                this.mainLayer.RefreshList();
                Laya.timer.once(2000, this, function () {
                    this.showMainList();
                });
            }
        } else if (Utils3D.nSteps == 2) {
            //zsAll.active = true;
            this.curZhuangshi.active = true;
            Utils3D.playAnimations(this.zsAll, "idle");
            Laya.timer.once(300, this, function () {
                if (Utils3D.bStart && this.mainLayer) {
                    Utils3D.nSteps++;
                    this.mainLayer.handSlip1(true);
                }
            })
        } else if (Utils3D.nSteps == 4) { //饰品移动动画
            Utils3D.playAnimations(this.zsAll, "Move");
            this.showSlider(false);
            Laya.timer.once(670, this, function () {
                if (!Utils3D.bStart) return;
                this.curZhuangshi.active = false;
                let point = handpoint.getChildAt(atIndex / 2 - 1) as Laya.Sprite3D;
                let zhuangshi = this.zsAll.getChildAt(Utils3D.nLookSelect) as Laya.MeshSprite3D;
                let zsc = zhuangshi.clone() as Laya.MeshSprite3D;
                zsc.active = true;
                point.addChild(zsc);

                Utils3D.nSteps++;
                this.onBeautify();
            })

        } else if (Utils3D.nSteps == 5) {
            this.stepsOver();
            if (Utils3D.nCurFinger > Utils3D.nDrawLen) {
                this.endPart.active = true;
                this.checkChange();
                Laya.timer.once(2300, this, this.showEndOff)
            } else {
                //this.onBeautify();
                this.checkFingerState();
            }
        }
    }

    //123个手指情况，全部填满
    checkChange() {
        if (Utils3D._curData["ChangeState"]) {
            if (this.drawColor) {
                this.drawColor.changeOtherT();
            }
            if (this.drawPaper) {
                this.drawPaper.changeOtherT();
            }
            if (this.zsAll) {
                this.changeOtherBeauty();
            }
        }
    }

    changeBeauty() {
        if (this.curZhuangshi) {
            this.curZhuangshi.destroy();
            this.curZhuangshi = null;
        }

        let handpoint = this.drawNode.getChildByName("HandPoint") as Laya.Sprite3D;
        //let atIndex = Utils3D._curData["DrawAt"][Utils3D.nCurFinger-1];
        let zhuangshi = this.zsAll.getChildAt(Utils3D.nLookSelect) as Laya.MeshSprite3D;
        this.curZhuangshi = zhuangshi.clone() as Laya.MeshSprite3D;
        this.zsAll.addChild(this.curZhuangshi);
        //this.drawNode.addChild(this.zsAll);
        Utils3D.nSteps++;
        this.onBeautify();
    }

    changeOtherBeauty() {
        //let curIndex = Utils3D._curData["DrawAt"][Utils3D.nSelectBeauty-1];
        let handpoint = this.drawNode.getChildByName("HandPoint") as Laya.Sprite3D;
        for (var i = 1; i < 6; i++) {
            let state = Utils3D._curData["ChangeState"][i - 1];
            if (state == Constant.state_Beautify) {
                if (Utils3D.findFinger(i * 2) == false) {
                    let point = handpoint.getChildAt(i - 1) as Laya.Sprite3D;
                    let zhuangshi = this.zsAll.getChildAt(Utils3D.nSelectBeauty - 1) as Laya.MeshSprite3D;
                    let zsc = zhuangshi.clone() as Laya.MeshSprite3D;
                    zsc.active = true;
                    point.addChild(zsc)
                }
            }
        }
    }

    zsAll = null;
    onLoadZhuangshi(node) {
        if (this.zsAll) {
            this.zsAll.destroy();
            this.zsAll = null;
        }
        this.zsAll = node as Laya.Sprite3D;
        this.drawNode.addChild(this.zsAll);
    }

    //每个手指的玩法
    checkFingerState() {
        Utils3D.nSteps = 1;
        Utils3D.nStateSteps++;
        Utils3D.nGameState = Utils3D._curData["GameState"][Utils3D.nStateSteps - 1];
        console.log("checkFingerState  nStateSteps = " + Utils3D.nStateSteps + "  玩法nGameState = ", Utils3D.nGameState);
        if (Utils3D.nGameState == Constant.State_Draw) {
            this.onDrawing();
        } else if (Utils3D.nGameState == Constant.State_Draw2) {
            this.onPaperDrawing();
        } else if (Utils3D.nGameState == Constant.state_Beautify) {
            this.onBeautify();
        }
    }

    showEndOff() { //展示
        if (!Utils3D.bStart || Utils3D.nGameState == 1) return;
        //let nailAll = this.drawNode.getChildByName(Utils3D.sNailShape) as Laya.Sprite3D;
        let lightNode = this.gameNode.getChildByName("Light");
        lightNode.transform.localRotationEulerX += 90;
        this.curNailAll.active = true;
        Utils3D.playAnimations(this.curNailAll, "Finish");
        Utils3D.playAnimations(this.hand, "Finish");
        Utils3D.playAnimations(this.hand, "Finish");
        if (this.drawNode) {
            let handpoint = this.drawNode.getChildByName("HandPoint") as Laya.Sprite3D;
            Utils3D.playAnimations(handpoint, "Finish");
        }

        let drawEnd = this.gameNode.getChildByName("DrawEnd");
        drawEnd.active = true;
        this._endCamera = drawEnd.getChildByName("Camera");
        if (Utils3D.curTool) {
            Utils3D.curTool.destroy();
            Utils3D.curTool = null;
        }

        if (gSdk.isTT) {
            gSdk.sdk.stopRecording();
        }

        this.updateLvPoint(false);
        this.mainLayer.setZSADVisible(false);   //弹出ShowOffView前隐藏广告信息---广告被覆盖无法点击
        DialogManager.Ins.sceneOpen(UICfg.ShowOffView, false);
    }

    showEndEffect(bShow) {
        let drawEnd = this.gameNode.getChildByName("DrawEnd");
        let p = drawEnd.getChildByName("Particle");
        if (p) {
            p.active = bShow;
        }
    }

    showSlider(bShow) {
        if (bShow) {
            if (this.slider) {
                this.slider.destroy();
                this.slider = null;
            }
            Utils3D.bShowSlider = false;
            let slider = this.camareNode.getChildByName("Slide001") as Laya.Sprite3D;
            this.slider = slider.clone() as Laya.Sprite3D;
            this.camareNode.addChild(this.slider)
            this.slider.active = bShow
        } else {
            if (this.slider) {
                this.slider.active = bShow;
            }
        }
    }

    //游戏完成
    GameFinish(t = 2000) {
        let self = this;
        Utils3D.bStart = false;
        Utils3D.taskScore = Utils3D.taskScore == 0 ? 10 : Utils3D.taskScore;
        Utils3D.bGameWin = true;//Utils3D.taskScore>0?true:false;
        Utils3D.nSteps = 1;
        Utils3D.bMouseMoving = false;
        if (this.mainLayer) {
            this.mainLayer.destroy();
            this.mainLayer = null;
        }

        if (!Utils3D.bAdtoEnd) {
            this.removeNode();
        }

        //移除旧sdk代码
        // if ((gSdk.isQQ && adSdk.sdk.adConfig.boxCanShow()) || (gSdk.isTT && adSdk.sdk.adConfig.boxCanShow())) {
        //     Laya.stage.once("QQ_CLICK_BOX", self, function () {
        //         self.onCloseFullAd(Utils3D.bGameWin);
        //     });
        //     Laya.timer.once(400, self, function () {
        //         Laya.Scene.open(UICfg.QQBox, false);
        //     })
        //     return;
        // }

        Laya.timer.once(t, this, function (): void {
            //移除旧sdk代码
            // if (gSdk.isWX) {
            //     if (adSdk.sdk.adConfig.isBeforeGameAccount()) {
            //         Laya.stage.once("FULL_SCREEN_AD_CLOSE", self, self.onCloseFullAd, [Utils3D.bGameWin]);
            //         DialogManager.Ins.sceneOpen(UICfg.screenAd, false)
            //         return;
            //     }
            // }
            //Laya.Scene.open("gameUI/ResultView.scene");   
            DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: true })
        })
    }

    onCloseFullAd(result) {
        DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: Utils3D.bGameWin })
    }

    nextGame(bNext) {
        if (bNext) {
            if (Utils3D.bTryUse) {
                gSdk.sdk.endLevel(Utils3D.nTryuseScene + 1000, "complete");
            } else {
                gSdk.sdk.endLevel(Utils3D.nShowScene, "complete");
            }

        } else {
            if (Utils3D.bTryUse) {
                gSdk.sdk.endLevel(Utils3D.nTryuseScene + 1000, "fail");
            } else {
                gSdk.sdk.endLevel(Utils3D.nShowScene, "fail");
            }
        }
        if (bNext && !Utils3D.bTryUse) { //试用关卡数不变
            if (!Utils3D.bPassed) {
                Utils3D.nCurScene++;
                Utils3D.nShowScene = Utils3D.nCurScene;
                if (Utils3D.nCurScene > Utils3D.nTotalScene) {
                    Utils3D.nCurScene = Math.floor(Math.random() * Utils3D.nTotalScene) + 1;
                    Utils3D.bPassed = true;
                    Laya.LocalStorage.setJSON("bPassed", true);
                }
                Laya.LocalStorage.setItem("CurScene", Utils3D.nCurScene.toString());
            } else {
                Utils3D.nShowScene++;
                //30关后随机
                Utils3D.nCurScene = Math.floor(Math.random() * Utils3D.nTotalScene) + 1;
            }
            Laya.LocalStorage.setItem("ShowScene", Utils3D.nShowScene.toString());
        }
        if (this.gameNode) {
            let drawEnd = this.gameNode.getChildByName("DrawEnd");
            drawEnd.active = false;
        }
        let lightNode = this.gameNode.getChildByName("Light");
        lightNode.transform.localRotationEulerX = 0;
        //this.mainNode.active = true;
        //this.gameNode.active = false;
        this.playWommenAni(true);
        this.endPart.active = false;
        Utils3D.bParticling = false;
        //Utils3D.bClickReset = false;
        Laya.timer.clear(this, this.updateAni);
        this.index = 0;
        this._Intervals = 0;
        Utils3D.bStart = false;
        Utils3D.bGameWin = false;
        Utils3D.nSteps = 1;
        Utils3D.nStateLen = 1;
        Utils3D.bMouseMoving = false;
        Utils3D.bPlayingAni = false;
        this.bRestart = true;
        Utils3D.nStateSteps = 1;
        Utils3D.nLookSelect = 0;
        Utils3D.nSweared = 0;
        Utils3D.taskScore = 0;
        Utils3D.nCurFinger = 1;
        Utils3D.bShowSlider = false;
        Utils3D.bTryUse = false;
        Utils3D.bTry = false;
        Utils3D.nShopTab = 1;
        Utils3D.bPlayerMove = true;
        this.drawPaper = null;
        this.drawColor = null;
        Utils3D.nSelectBeauty = 1;
        Utils3D.nSelectDraw = 1;
        Utils3D.nSelectPaper = 1;
        Utils3D.bAdtoEnd = false;
        Laya.timer.clearAll(this);
        if (Utils3D.curTool) {
            Utils3D.curTool.destroy();
            Utils3D.curTool = null;
        }
        if (this.slider) {
            this.slider.destroy();
            this.slider = null;
        }
        this.initScene(this.bRestart);
    }

    removeNode() {
        this.playWommenAni(false);
        this.gameNode.active = false;
        this.mainNode.active = true;
        Utils3D.playAnimations(this.camareNode, "idle");
        Utils3D.playAnimations(this.hand, "idle");

        if (this.cutNode) {
            this.cutNode.destroy();
            this.cutNode = null;
        }

        if (this.swearNode) {
            this.swearNode.destroy();
            this.swearNode = null;
        }

        if (this.millNode) {
            this.millNode.destroy();
            this.millNode = null;
        }

        if (this.drawNode) {
            this.drawNode.destroy();
            this.drawNode = null;
        }

        if (this.curNailAll) {
            this.curNailAll.destroy();
            this.curNailAll = null;
        }

        if (this.zsAll) {
            this.zsAll.destroy();
            this.zsAll = null;
        }
    }


    get tooltoPosX() {
        return Utils3D.curTool.transform.localPositionX;
    }
    set tooltoPosX(value) {
        Utils3D.curTool.transform.localPositionX = value;
    }
}