import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import { AdsType } from "../common/platformsdk/plat-def";
import TipManager from "../common/TipManager";
import GameMgr from "../scripts/mgr/GameMgr";
import { UICfg } from "../scripts/defines/ResDef";
import SceneMgr from "../scripts/mgr/SceneMgr";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
export default class LoadSceneView extends ui.gameUI.LoadSceneViewUI {

    private checkLoadFinishCount: number = 0;
    constructor() {
        super();
        this.height = Laya.stage.height;
        this.bg.height = Laya.stage.height;
        this.graphics.drawRect(-400, -600, 1500, 2600, "#23a8ea");//颜色值
    }

    onAwake(): void {
    }

    checkOnLine() {
        if (!navigator.onLine) {
            let tx: number = Laya.stage.width / 2;
            let ty: number = Laya.stage.height - 200;

            TipManager.Ins.showMsgTip('断网状态下，会影响游戏体验哦~', Laya.stage, tx, ty, { color: '#fff', strokeColor: '#fff', fontSize: 33, width: 500, stroke: 0, font: "Microsoft YaHei" }, 0);
        }
    }

    onEnable(): void {
        this.checkOnLine();//检测是否断网

        //1、初始化
        gSdk.init();

        //2、加载分包
        let subpackage = ['res1', 'res2', 'res3','res4','res5','res6']
        let subpackageTotalProp = subpackage.length / 10;
        if (subpackageTotalProp > 0.5) subpackageTotalProp = 0.5;
        let subpackageProp = subpackageTotalProp / subpackage.length, subpackageIndex = 0;

        let self = this;
        gSdk.sdk.loadSubpackage(subpackage, () => {
            //3、加载场景
            SceneMgr.Ins.init(() => {
                //4、初始化游戏
                GameMgr.Ins.init();
                this.close();
                // Laya.timer.loop(100, this, this.checkLoadFinish);
            }, (value) => {
                self.setProgress(subpackageTotalProp + (value * (1 - subpackageTotalProp)));//加载场景进度
            })
        }, (index) => {
            subpackageIndex++;
            self.setProgress(subpackageProp * subpackageIndex);//加载分包进度
        });

    }

    // checkLoadFinish() {
    //     this.checkLoadFinishCount++;
    //     if (adSdk.sdk.isLoadFinish || this.checkLoadFinishCount > 40) {

    //             // setTimeout(() => {
    //             this.close();
    //             //Laya.Scene.open(UICfg.GameStartView);//打开ui
    //             // }, 100);
    //         Laya.timer.clearAll(this);
    //     }

    // }

    private maxProgress: number = 0;

    //加载进度
    setProgress(value: number): void {
        if (value > this.maxProgress) {
            this.maxProgress = value;
            this.progressBar.width = value * 400;
        }
    }

    onDisable() {

    }

    onDestroy() {
    }

}