    /** 根据速度与角度（弧度）计算出当前移动的向量 
     * hangle
     * speed
     * @param isRadian 是否弧度制。
     * outVector
     * isReverse 是否反向， 默认是 x轴向右为正 z轴向下为正  反向则是空间旋转180度， 就是  x轴向左为正 z轴向上为正
     *  **/
    public static caluMoveVector(angle: number, speed: number, isRadian: boolean = false, outVector?: Laya.Vector3, isReverse: boolean = false): Laya.Vector3 {
        if (!outVector) outVector = new Laya.Vector3();
        outVector.x = 0;
        outVector.y = 0;
        outVector.z = 0;
        var hangle: number = isRadian ? angle : (angle * Math.PI / 180);
        //var tx: number = Math.cos(hangle) * speed;
        //var tz: number = Math.sin(hangle) * speed;
        var tx: number = Math.sin(hangle) * speed;
        var tz: number = Math.cos(hangle) * speed;
        if (isReverse) {
            tx *= -1;
            tz *= -1;
        }
        outVector.x = tx;
        outVector.z = tz;
        return outVector;
    }


    /** 设置3D对象沿着Y轴旋转到指定角度（弧度）
     *  isReverse 是否反向， 默认是 x轴向右为正 z轴向下为正  反向则是空间旋转180度， 就是  x轴向左为正 z轴向上为正
     *  **/
    public static radiansYTo(obj: Laya.Sprite3D, hangle: number, isReverse: boolean = false): void {
        var tx: number = Math.cos(hangle) * 5;
        var tz: number = Math.sin(hangle) * 5;
        if (isReverse) {
            tx *= -1;
            tz *= -1;
        }
        Utils3D.roleLookAtPoint.x = obj.transform.position.x + tx;
        Utils3D.roleLookAtPoint.z = obj.transform.position.z + tz;
        this.roleLookAtPoint.y = obj.transform.position.y;
        obj.transform.lookAt(this.roleLookAtPoint, Utils3D.upVector3, false);
    }

    public static rotationYto(object:Laya.Sprite3D,hangle:number,distance:number,speed:number):void{
        var curRotat = object.transform.localRotationEulerY;
        if(curRotat >= -hangle){return;}
        var aaa = curRotat + hangle;
        var perRotat = -hangle*distance*speed/60
        object.transform.localRotationEulerY = object.transform.localRotationEulerY+perRotat;
    }

