    public static playAnimations(node:Laya.Sprite3D,strAni:string,speed=1){
        if(node){
            var ani = node.getComponent(Laya.Animator);
            if(ani){
                ani.play(strAni,0,0);
                if(speed>1){
                    ani.speed = 0.6;
                }else{
                    ani.speed = 1;
                }
            }
        }
    }

    public static playIntervalAni(node:Laya.Sprite3D,strAni:string,nStar,nEnd){
        var ani = node.getComponent(Laya.Animator);
        if(ani && !Utils3D.bGameWin){
            //创建一个动画动作状态
            this.state1.name = strAni;
            //起始时间与结束时间的设置为0-1的百分比数值） 截取时间点 / 动画的总时长
            this.state1.clipStart = nStar/220;
            this.state1.clipEnd = nEnd/220;
            //得到默认动画赋值给Clip（getDefaultState默认动画为Unity中animation的数组顺序0下标的动画）
            this.state1.clip = ani.getDefaultState().clip;
            //动画播放是否循环
            this.state1.clip.islooping = false;
            //添加动画状态到动画组件里
            ani.addState(this.state1);
            //播放动画
            ani.play(this.state1.name);
        }
    }

