import gSdk from "../../common/platformsdk/platform";
import TipManager from "../../common/TipManager";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";
const INTERVAL_TIME = 5000;
const INTERVAL_DATA = 1000;
export default class ElectItem extends Laya.Script {
    /** @prop {name: itemType,tips:"导出项类型，0:所有,1:前面3个",type: number,default:0} */
    public itemType: number = 0;
    /** @prop {name: looptype,tips:"导出项类型，0:顺序显示,1:倒序显示",type: number,default:0} */
    public looptype: number = 0;

    private elect_list: any = null;
    private _curIdx: number = 0;
    private _iTime: number = 0;      //多少秒轮换icon
    private _idataTime: number = 0;
    private bwork: boolean = false;

    constructor() {
        super();

    }

    onAwake() {
        (this.owner as Laya.Sprite).visible = false;

        this.itemShake();
    }

    onEnable() {
        (this.owner as Laya.Image).on(Laya.Event.CLICK, this, (e: Laya.Event) => {
            if (gSdk.isTT) {
                let listdata = adSdk.sdk.getAdList('defaultData');
                gSdk.sdk.showMoreGamesModal(listdata && listdata.length > 0 ? listdata[0] : [], null, null, null, (code) => {
                    if (code == 1) TipManager.Ins.showMsgTip("打开更多游戏失败");
                });
            }
            else {
                adSdk.sdk.navigateToMiniProgram(this.getData(), navigateSceneType.rotation);
                //gSdk.sdk.navigateToMiniProgram(this.getData(), () => { DataModule.Ins.clickGameReqQiTu(this.getData(), 3) }, () => { });
            }
        });
        (this.owner as Laya.Image).on(Laya.Event.MOUSE_DOWN, this, (e: Laya.Event) => {
            e.stopPropagation();
        });
        (this.owner as Laya.Image).on(Laya.Event.MOUSE_UP, this, (e: Laya.Event) => {
            e.stopPropagation();
        })
    }

    onUpdate() {
        if (!this.bwork) {
            //移除旧sdk代码
            if (gSdk.isWX) {
                // if (gSdk.sdk.isUseZS) {
                //     if (adSdk.sdk.adConfig.isElectOn())
                //         this.bwork = true;
                //     else {
                //         return;
                //     }
                // }
                // else {
                //     if (adSdk.sdk.adConfig.checkControl("zs_jump_switch", true))
                //         this.bwork = true;
                //     else
                //         return;
                // }
            }
            else if (gSdk.isOP) {
                // if (this.looptype == 0 && adSdk.sdk.adConfig.checkControl("zs_click_award_city", true))
                //     this.bwork = true;
                // else
                //     return;
            }
            else if (gSdk.isTT) {
                this.bwork = true;
            }
            else {
                return;
            }
        }

        let dt = Laya.timer.delta;
        if (!this.elect_list || this.elect_list.length == 0) {
            this._idataTime += dt;
            if (this._idataTime >= INTERVAL_DATA) {
                this._idataTime = 0;
                let list = [];
                if (gSdk.isWX && gSdk.sdk.isUseZS)
                    list = adSdk.sdk.getAdList('endPage');
                else
                    list = adSdk.sdk.getAdList('defaultData');
                if (this.itemType == 0) {
                    this.elect_list = list;
                } else if (this.itemType == 1 && list && list.length > 0) {       //取前面3个值
                    let count = list.length < 3 ? list.length : 3;
                    this.elect_list = [];
                    for (let i = 0; i < count; i++) {
                        this.elect_list.push(list[i]);
                    }
                } else {
                    this.elect_list = [];
                }
                //this.elect_list = DataModule.Ins.getGameList();
                if (this.elect_list.length > 0) {
                    if (this.looptype == 1) {
                        this._curIdx = Math.floor(this.elect_list.length / 2);
                        //this._curIdx =  2;
                    }
                    if (this.looptype == 0) {
                        this._curIdx = Math.floor(Math.random() * this.elect_list.length);
                    }
                    (this.owner as Laya.Image).visible = true;
                    this.updateItemUI();
                }
            }
            return;
        }

        this._iTime += dt;
        if (this._iTime >= INTERVAL_TIME) {
            this._iTime = 0;
            //this._curIdx++;
            this._curIdx = Math.floor(Math.random() * this.elect_list.length);
            this.updateItemUI();
        }
    }

    itemShake() {
        Laya.Tween.to(this.owner, { rotation: 10 }, 300, null, Laya.Handler.create(this, () => {
            Laya.Tween.to(this.owner, { rotation: 0 }, 300, null, Laya.Handler.create(this, () => {
                Laya.Tween.to(this.owner, { rotation: -10 }, 300, null, Laya.Handler.create(this, () => {
                    Laya.Tween.to(this.owner, { rotation: 0 }, 300, null, Laya.Handler.create(this, () => {
                        this.itemShake();
                    }))
                }))
            }))
        }))
    }

    private getData() {
        if (!this.elect_list || this.elect_list.length == 0) {
            return null;
        }
        return this.elect_list[this._curIdx % this.elect_list.length];
    }

    updateItemUI() {
        let data = this.getData();
        if (!data) {
            return;
        }
        let timg: Laya.Sprite = this.owner.getChildByName("icon") as Laya.Sprite
        timg.texture = data.link_img || data.app_icon;

        let nameTxt = this.owner.getChildByName('nameTxt') as Laya.Label;
        nameTxt.text = data.link_name || data.app_title;

        let tsp = this.owner.getChildByName("downBg") as Laya.Sprite;
        tsp.graphics.drawRect(0, 0, 168, 54, this.getClor(this._curIdx));
        this.owner.getChildByName("hot")["visible"] = false
    }

    private getClor(_index: number): string {
        let ttt = ["0xb852b6", "0x4294a2", "0x2f67c4", "0xe09031", "0x35bd75", "0xc5645e"];
        return ttt[_index % 6];
    }
}