/**
 * 原生模板广告控制器
 */
import SdkTools, {Game_Platform} from "../../tools/SdkTools";
import IntersVivo from "../IntersAd/IntersVivo";
import IntersTiktok from "../IntersAd/IntersTiktok";
import IntersQQ from "../IntersAd/IntersQQ";
import IntersWX from "../IntersAd/IntersWX";
import {CustomVivo} from "./CustomVivo";
import LayaUI from "../../ui/LayaUI";

export class CustomController {
    private static instance: CustomController

    /**
     * 原生模板开关
     */
    public SW_NativeCustomSwitch: boolean = false;

    /**
     * 原生模板广告ID
     */
    public ID_NativeCustomId: string = "";

    /**
     * 加载原生模板广告定时器
     */
    loadCustomCheck;


    public static getInstance(): CustomController {
        if (!CustomController.instance) {
            CustomController.instance = new CustomController();
        }
        return CustomController.instance;
    }

    /**
     * 创建原生模板
     */
    public createNativeCustom() {
        if (!this.SW_NativeCustomSwitch) {
            console.log("AD", "原生模板开关未开启")
            return;
        }

        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                LayaUI.getInstance().loadNativeCustomRes();
                CustomVivo.getInstance().createNativeCustom(this.ID_NativeCustomId);
                break;
            default:
                break;
        }
    }

    /**
     * 获取原生模板是否可以展示
     */
    public getNativeCustomFlag() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                return CustomVivo.getInstance().getNativeCustomFlag();
            default:
                return false;
        }
    }

    /**
     * 展示原生模板
     */
    public showNativeCustom(call) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                if (this.getNativeCustomFlag()) {
                    CustomVivo.getInstance().showNativeCustom(call);
                } else {
                    this.checkNativeLoadSucc(call);
                }
                break;
            default:
                console.log("AD", "当前平台不存在原生模板广告位！");
                break;
        }
    }

    /**
     * 隐藏原生模板
     */
    public hideNativeCustom() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                CustomVivo.getInstance().hideNativeCustom();
                break;
            default:
                console.log("AD", "当前平台不存在原生模板广告位！");
                break;
        }
    }

    /**
     * 检测原生模板有没有加载成功
     * @private
     */
    private checkNativeLoadSucc(call) {
        if (this.loadCustomCheck) {
            clearTimeout(this.loadCustomCheck)
        }
        if (this.SW_NativeCustomSwitch && this.getNativeCustomFlag()) {
            this.showNativeCustom(call);
        } else {
            this.loadCustomCheck = setTimeout(() => {
                this.checkNativeLoadSucc(call);
            }, 5 * 1000)
        }
    }
}