import SdkTools, {Game_Platform} from "../../tools/SdkTools"
import NavigateController from "../../navigate/NavigateController"
import {BoxBannerVivo} from "./BoxBannerVivo";
import {BoxPortalVivo} from "./BoxPortalVivo";

class BoxController {
    private static instance: BoxController

    /**
     * 盒子广告ID
     */
    public ID_BoxID = "";

    /**
     * 盒子互推Banner广告
     */
    public ID_BoxBannerID = "";

    /**
     * 盒子互推九宫格广告
     */
    public ID_BoxPortalID = "";

    /**
     * 盒子广告开关
     */
    public SW_BoxSwitch: boolean = true;

    /**
     * 盒子广告对象
     */
    public appBoxAd: any = null;

    /**
     * 加载盒子互推定时器
     * @private
     */
    private loadBoxBannerCheck;

    /**
     * 加载互推九宫格定时器
     * @private
     */
    private loadBoxPortalCheck;

    /**
     * BoxController 单例
     */
    public static getInstance(): BoxController {
        if (!BoxController.instance) {
            BoxController.instance = new BoxController()
        }
        return BoxController.instance
    }


    /**
     * 创建盒子广告
     * type 0--BoxBanner  1--BoxPortal
     */
    public createAppBox(type = null) {
        if (!this.SW_BoxSwitch) {
            console.log("AD", "盒子广告开关未开启");
            return;
        }
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
                NavigateController.getInstance().createNavigateBoxPortal(this.ID_BoxID);
                break;
            case Game_Platform.GP_Vivo:
                if (type) {
                    BoxPortalVivo.getInstance().createBoxPortal(this.ID_BoxPortalID);
                } else {
                    BoxBannerVivo.getInstance().createBoxBanner(this.ID_BoxBannerID);
                }
                break;
        }
    }

    /**
     * 获取盒子是否可以展示标志
     * type 0-互推Banner广告   1-互推九宫格广告
     */
    public getBoxFlag(type) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                if (type)  //九宫格
                {
                    return BoxPortalVivo.getInstance().getBoxPortalFlag();
                } else {  //Banner
                    return BoxBannerVivo.getInstance().getBoxBannerFlag()
                }
        }
    }

    /**
     * 展示盒子广告
     * type 0-互推Banner广告   1-互推九宫格广告
     */
    public showAppBox(type) {
        if (!this.SW_BoxSwitch) {
            console.log("AD", "盒子广告开关未开启");
            return;
        }
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                if (type)  //九宫格
                {
                    if (BoxPortalVivo.getInstance().getBoxPortalFlag()) {
                        BoxPortalVivo.getInstance().showBoxPortal();
                    } else {
                        this.checkAppBoxLoadSucc(type);
                    }
                } else {  //Banner
                    if (BoxBannerVivo.getInstance().getBoxBannerFlag()) {
                        BoxBannerVivo.getInstance().showBoxBanner();
                    } else {
                        this.checkAppBoxLoadSucc(type);
                    }
                }
                break;
            case Game_Platform.GP_Test:
                console.log("AD", "Test 没有测试盒子广告============");
                break;
            default:
                break;
        }
    }

    /**
     * 隐藏盒子广告
     * @param type 0-互推Banner广告   1-互推九宫格广告
     */
    public hideAppBox(type) {
        if (!this.SW_BoxSwitch) {
            console.log("AD", "盒子广告开关未开启");
            return;
        }
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Vivo:
                if (type)  //九宫格
                {
                    BoxPortalVivo.getInstance().hideBoxPortal();
                } else {  //Banner
                    BoxBannerVivo.getInstance().hideBoxBanner();
                }
                break;
            case Game_Platform.GP_Test:
                console.log("AD", "Test 没有测试盒子广告============");
                break;
            default:
                break;
        }
    }

    /**
     * 检测互推盒子有没有加载成功
     * @param type 0-互推Banner广告   1-互推九宫格广告
     */
    checkAppBoxLoadSucc(type)
    {
        let str;
        switch (type)
        {
            case 0:
                str ="互推Banner"
                if(this.loadBoxBannerCheck)
                {
                    clearTimeout(this.loadBoxBannerCheck)
                }
                if(this.SW_BoxSwitch&&this.getBoxFlag(type))
                {
                    this.showAppBox(type);
                }else {
                    console.log("AD", "vivo "+str+"广告没有准备好")
                    this.loadBoxBannerCheck = setTimeout(function () {
                        this.checkAppBoxLoadSucc(type);
                    }.bind(this), 5 * 1000)
                }
                break;
            case 1:
                str ="互推九宫格"
                if(this.loadBoxPortalCheck)
                {
                    clearTimeout(this.loadBoxPortalCheck)
                }
                if(this.SW_BoxSwitch&&this.getBoxFlag(type))
                {
                    this.showAppBox(type);
                }else {
                    console.log("AD", "vivo "+str+"广告没有准备好")
                    this.loadBoxPortalCheck = setTimeout(function () {
                        this.checkAppBoxLoadSucc(type);
                    }.bind(this), 5 * 1000)
                }
                break;
        }

    }


}

export default BoxController