import IntersController from "../IntersAd/IntersController";
import LayaUI from "../../ui/LayaUI";

/**
 * 原生模板广告
 */
export class CustomVivo {
    private static instance: CustomVivo;

    /**
     * 原生模板对象
     */
    public nativeCustomAd = null;

    /**
     * 原生模板加载失败次数
     */
    private customLoadErrNum = 0;
    /**
     * 是否加载到原生模板广告
     * @private
     */
    private isLoadNativeCustom = false;

    /**
     * 关闭回调
     * @private
     */
    private closeCall;


    /**
     * CustomVivo 单例
     */
    public static getInstance(): CustomVivo {
        if (!CustomVivo.instance) {
            CustomVivo.instance = new CustomVivo()
        }
        return CustomVivo.instance
    }

    /**
     * 创建原生模板广告
     * @param interId
     */
    public createNativeCustom(interId) {
        console.log("AD", "VIVO 原生模板广告初始化", interId);

        if (!qg.createCustomAd) {
            console.log("AD", "VIVO 原生模板广告不存在", interId);
            return;
        }
        let left;
        let top;
        if (Laya.stage.width > Laya.stage.height) //横板
        {
            left = Laya.stage.width / 2;
            top = 100;
        } else { //竖版
            left = 0;
            top = Laya.stage.height / 2;
        }
        this.nativeCustomAd = qg.createCustomAd({
            posId: interId,
            style: {
                left: left,
                top: top,  //左上角纵坐标
            }
        });

        //监听原生模板广告加载完成
        this.nativeCustomAd.onLoad(() => {
            console.log('AD', 'VIVO 原生模板广告加载完成')
            this.isLoadNativeCustom = true;
        })


        this.nativeCustomAd.onError(err => {
            console.log('AD', 'VIVO 原生模板广告加载失败：' + JSON.stringify(err))
            this.isLoadNativeCustom = false;
            if (err.errCode != 1008) {
                this.customLoadErrNum++;
                console.log("AD", "VIVO 原生模板加载错误次数:" + this.customLoadErrNum);
                if (this.customLoadErrNum < 5) {
                    setTimeout(() => {
                        this.createNativeCustom(interId);
                    }, 1 * 1000);
                }
            }
        });


        //监听插屏广告关闭
        this.nativeCustomAd.onClose(() => {
            console.log('AD', 'VIVO 原生模板广告关闭');
            LayaUI.getInstance().hideNativeCustomUI();
            if (this.closeCall) {
                this.closeCall();
            }
            this.isLoadNativeCustom = false;
            // 原生模板关闭后10s后再次load
            setTimeout(() => {
                this.createNativeCustom(interId);
            }, 1 * 1000);
        })
    }

    /**
     * 获取原生模板是否可以展示
     */
    public getNativeCustomFlag() {
        return this.isLoadNativeCustom;
    }

    /**
     * 展示原生模板
     */
    public showNativeCustom(call) {
        if (this.nativeCustomAd) {
            console.log("AD", "VIVO showNativeCustom==================");
            LayaUI.getInstance().showNativeCustomUI();
            this.nativeCustomAd.show().then(() => {
                this.closeCall = call;
                console.log("AD", "VIVO 原生模板广告展示成功==================");
            }).catch((err) => {
                LayaUI.getInstance().hideNativeCustomUI();
                console.log("AD", "VIVO 原生模板广告展示错误:" + JSON.stringify(err));
            })
        } else {
            console.log("AD", "VIVO 未创建原生模板广告");
            return;
        }
    }

    /**
     * 隐藏原生模板
     */
    public hideNativeCustom() {
        if (this.nativeCustomAd) {
            console.log("AD", "VIVO hideNativeCustom==================");
            this.nativeCustomAd.hide();
        }
    }
}