class sdkConfig {
    private static instance: sdkConfig

    // SDK版本,请勿修改
    public static readonly SDKVersion: string = "version_335_3";
    // APPID,仅在HW(华为)渠道可用.
    public static readonly APPID: string = "0";
    
    public static ver: string = "1.1";

    /**
     * sdkConfig单例
     */
    public static getInstance(): sdkConfig {
        if (!sdkConfig.instance) {
            sdkConfig.instance = new sdkConfig()
        }
        return sdkConfig.instance
    }

    /*
    * 渠道ID 七位 "0"为测试版
    * 0000142 Oppo
    * 0000108 Vivo
    * 0000154 TikTok
    * 0000155 QQ
    * 1234666 Android
    * 1234888 Ios
    * @type {string}
    */
    public channelId: string = "0";
    public engine: string = "laya";

    /**
     * 远端配置文件夹的名称 运营人员提供
     */
    public remoteConfigName: string = "cmqc_zgxfl";

    public static ossUrl: string = "https://cmqc.oss-cn-shenzhen.aliyuncs.com";
}

export default sdkConfig