/**
 * VIVO 版本的创建桌面图标
 */
export class ShortcutVivo {
    private static instance: ShortcutVivo;

    public static getInstacne(): ShortcutVivo {
        if (!ShortcutVivo.instance) {
            ShortcutVivo.instance = new ShortcutVivo();
        }
        return ShortcutVivo.instance;
    }

    private shortcut;

    private shortcutErrNum;

    /**
     * 创建桌面图标
     * @param imagePath 桌面图标路径
     * @param left
     * @param top
     * @param color
     * @constructor
     */
    CreateShortcut(imagePath, left, top, color) {
        if (!qg.createShortcutButton) {
            console.log("ASCAD", "创建桌面图标 暂不支持桌面图标创建！")
        }
        console.log("ASCAD", "开始创建桌面图标")
        this.shortcut = qg.createShortcutButton({
            type: 'image',
            image: imagePath,   //
            style: {
                left: left,
                top: top,
                width: 100,
                height: 100,
                color: color,
            }
        })


        var startHandler = function (data) {
            console.log("ASCAD", "创建桌面图标结果：" + data.msg)
        };
        this.shortcut.onTap(startHandler)

        this.shortcut.onError(err => {
            console.log("AD", "VIVO 桌面快捷方式加载失败：" + JSON.stringify(err))
            this.shortcutErrNum++;
            console.log("AD", "VIVO 桌面快捷方式加载错误次数：" + this.shortcutErrNum)
            if (this.shortcutErrNum < 5) {
                setTimeout(() => {
                    this.CreateShortcut(imagePath, left, top, color);
                }, 30 * 1000)
            }
        })
    }

    /**
     * 是否能创建桌面图标
     * @constructor
     */
    GetShortcutFlag() {
        return this.shortcut;
    }

    /**
     * 展示桌面图标
     * @constructor
     */
    ShowShortcut() {
        if (this.shortcut) {
            console.log("AD", "VIVO showShortcut");
            this.shortcut.show().then(() => {
                console.log("AD", "VIVO 展示桌面图标");
            }).catch((err) => {
                console.log("AD", "VIVO 展示桌面图标展示错误：" + JSON.stringify(err))
            })
        } else {
            console.log("AD", "VIVO 未创建桌面图标")
        }
    }

    /**
     * 隐藏桌面图标
     * @constructor
     */
    HideShortcut() {
        if (this.shortcut) {
            console.log("AD", "VIVO hideShortcut===========")
            this.shortcut.hide();
        } else {
            console.log("AD", "VIVO 未创建桌面图标")
        }
    }
}