import SdkTools, { Game_Platform } from "../tools/SdkTools"

class OtherFunctions {
    private static instance: OtherFunctions

    /**
     * 添加桌面图标开关
     */
    public SW_DesktopSwitch: boolean = false;

    /**
     * 插屏间隔弹桌面图标开关
     */
    public SW_IntersToDesktop: boolean = false;

    /**
     * 自动弹添加桌面次数
     */
    public NUM_DeskAutoMostTimes: number = 0;

    /**
     * 第几次插屏变添加桌面
     */
    public NUM_IntersAddDesktopNumber: number = 0;

    /**
     * OtherFunctions 单例
     */
    public static getInstance(): OtherFunctions {
        if (!OtherFunctions.instance) {
            OtherFunctions.instance = new OtherFunctions()
        }
        return OtherFunctions.instance
    }



    /**
     * 判断渠道是否拥有添加桌面接口
     */
    public hasDesktopFunc() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
            case Game_Platform.GP_Vivo:
            case Game_Platform.GP_Tiktok:
            case Game_Platform.GP_QQ:
            case Game_Platform.GP_HW:
            case Game_Platform.GP_Test:
                return true;
            default:
                return false;
        }
    }
    /**
     * 获取能否添加桌面标志
     */
    public getDeskTopFlag(callback) {
        if (!this.SW_DesktopSwitch) {
            console.log("ASCSDK", "添加桌面图标开关未开启");
            callback(false);
            return;
        }
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
                {
                    qg.hasShortcutInstalled({
                        success: function (res) {
                            // 判断图标未存在时，创建图标
                            if (res == false) {
                                callback(true);
                            }
                        },
                        fail: function (err) {
                            console.log("ASCSDK", JSON.stringify(err));
                        },
                        complete: function () { }
                    })
                    break;
                }
            case Game_Platform.GP_Vivo:
                {
                    qg.hasShortcutInstalled({
                        success: function (status) {
                            if (status) {
                                console.log("ASCSDK", 'VIVO 已创建桌面图标');
                                callback(false);
                            } else {
                                console.log("ASCSDK", 'VIVO 未创建桌面图标')
                                callback(true);
                            }
                        }
                    })
                    break;
                }
            case Game_Platform.GP_Tiktok:
                {
                    tt.checkShortcut({
                        success: function (res) {
                            if (!res.status.exist || res.status.needUpdate) {
                                console.log("ASCSDK", "checkShortcut res:", JSON.stringify(res));
                                console.log("ASCSDK", 'Tiktok 未创建桌面图标或图标需要更新');
                                callback(true);
                            } else {
                                console.log("ASCSDK", 'Tiktok 已创建桌面图标');
                                callback(false);
                            }
                        },
                        fail: function (res) {
                            console.log("ASCSDK", "Tiktok 拒绝添加桌面图标：", JSON.stringify(res.errMsg));
                            callback(false);
                        }
                    })
                }
                break;
            case Game_Platform.GP_QQ:
                callback(true);
                break;
            case Game_Platform.GP_HW:
                hbs.hasInstalled({
                    success: function (res) {
                        if (res) {
                            console.log("ASCSDK", "HW 已创建桌面图标");
                            callback(false);
                        } else {
                            console.log("ASCSDK", "HW 未创建桌面图标");
                            callback(true);
                        }
                    },
                    fail: function (data) {
                        console.log("ASCSDK", "未知错误:" + data);
                        callback(false);
                    }
                })
                break;
            case Game_Platform.GP_Test:
            default:
                callback(true);
                break;
        }
    }
    /**
     * 添加桌面
     */
    public addDeskTop(callback) {
        if (!this.SW_DesktopSwitch) {
            console.log("ASCSDK", "添加桌面图标开关未开启");
            callback(false);
            return;
        }
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
                {
                    qg.installShortcut({
                        success: function () {
                            console.log("ASCSDK", 'OPPO 创建桌面图标成功')
                            // 执行用户创建图标奖励
                            callback(true);
                        },
                        fail: function (err) {
                            console.log("ASCSDK", 'OPPO 创建桌面图标失败:' + JSON.stringify(err));
                            callback(false);
                        },
                        complete: function () { }
                    })
                    break;
                }
            case Game_Platform.GP_Vivo:
                {
                    qg.installShortcut({
                        success: function () {
                            console.log("ASCSDK", 'VIVO 创建桌面图标成功')
                            callback(true);
                        },
                        fail: function (err) {
                            console.log("ASCSDK", 'VIVO 创建桌面图标失败:' + JSON.stringify(err));
                            callback(false);
                        },
                        complete: function () { }
                    })
                    break;
                }
            case Game_Platform.GP_QQ:
                {
                    qq.saveAppToDesktop({
                        success: function () {
                            console.log("ASCSDK", 'QQ 创建桌面图标成功')
                            // 执行用户创建图标奖励
                            callback(true);
                        },
                        fail: function (err) {
                            console.log("ASCSDK", 'QQ 创建桌面图标失败:' + JSON.stringify(err));
                            callback(false);
                        },
                        complete: function () { }
                    })
                    break;
                }
            case Game_Platform.GP_Tiktok:
                tt.addShortcut({
                    success: function (res) {
                        console.log("ASCSDK", "Tiktok 添加桌面成功");
                        callback(true);
                    },
                    fail: function (res) {
                        console.log("ASCSDK", "Tiktok 添加桌面失败:" + JSON.stringify(res));
                        callback(false);
                    }
                })
                break;
            case Game_Platform.GP_HW:
                hbs.install({
                    success: function (res) {
                        console.log("ASCSDK", "HW 添加桌面成功");
                        callback(true);
                    },
                    fail: function (erromsg, errocode) {
                        console.log("ASCSDK", "HW 添加桌面失败:" + JSON.stringify(erromsg));
                        callback(false);
                    }
                });
                break;
            default:
                callback(true);
                break;
        }
    }


    /**
     * 展示系统提示
     */
    public showToast(msg: string, duration?: number) {
        if (duration == undefined || duration == null || duration == 0) duration = 1500;
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
                {
                    console.log('ASCSDK', 'OPPO showToast：', msg);
                    qg.showToast({
                        title: msg,
                        duration: duration
                    })
                    break;
                }
            case Game_Platform.GP_Vivo:
                {
                    console.log('ASCSDK', 'VIVO showToast：', msg);
                    qg.showToast({
                        message: msg,
                        duration: duration == 1 ? 1 : 0
                    })
                    break;
                }
            case Game_Platform.GP_Tiktok:
                {
                    console.log('ASCSDK', 'Tiktok showToast：', msg);
                    tt.showToast({
                        title: msg,
                        duration: duration
                    })
                    break;
                }
            case Game_Platform.GP_QQ:
                {
                    console.log('ASCSDK', 'QQ showToast：', msg);
                    qq.showToast({
                        title: msg,
                        duration: duration
                    })
                    break;
                }
            case Game_Platform.GP_Test:
                console.log('ASCSDK', 'Test 打包后会有真实效果:', msg);
                break;
            default:
                break;
        }
    }


    /**
     * 手机震动
     */
    public phoneVibrate(type: string) {
        console.log("ASCSDK", '手机震动，需要在打包后会有真实效果', type);
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
                {
                    if (type == 'short') {
                        qg.vibrateShort({
                            success: function (res) { },
                            fail: function (res) { },
                            complete: function (res) { }
                        })
                    }
                    if (type == 'long') {
                        qg.vibrateLong({
                            success: function (res) { },
                            fail: function (res) { },
                            complete: function (res) { }
                        })
                    }
                    break;
                }
            case Game_Platform.GP_Vivo:
            case Game_Platform.GP_XM:
                {
                    if (type == 'short') {
                        qg.vibrateShort()
                    }
                    if (type == 'long') {
                        qg.vibrateLong()
                    }
                    break;
                }
            case Game_Platform.GP_Tiktok:
                {
                    if (type == 'long') {
                        tt.vibrateLong({
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'Tiktok vibrateLong调用失败');
                            }
                        });
                    }
                    else if (type == 'short') {
                        tt.vibrateShort({
                            success(res) {
                                //console.log(`${res}`);
                            },
                            fail(res) {
                                console.log("ASCSDK", 'Tiktok vibrateShort调用失败');
                            }
                        });
                    }
                    break;
                }
            case Game_Platform.GP_QQ:
                {
                    if (type == 'long') {
                        qq.vibrateLong({
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'QQ vibrateLong调用失败', JSON.stringify(res));
                            }
                        });
                    }
                    else if (type == 'short') {
                        qq.vibrateShort({
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'QQ vibrateShort调用失败', JSON.stringify(res));
                            }
                        });
                    }
                    break;
                }
            case Game_Platform.GP_WX:
                {
                    if (type == 'long') {
                        wx.vibrateLong({
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'WX vibrateLong调用失败', JSON.stringify(res));
                            }
                        });
                    }
                    else if (type == 'short') {
                        wx.vibrateShort({
                            type: "heavy",
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'WX vibrateShort调用失败', JSON.stringify(res));
                            }
                        });
                    }
                    break;
                }
            case Game_Platform.GP_BL:
                {
                    if (type == 'long') {
                        bl.vibrateLong({
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'BL vibrateLong调用失败', JSON.stringify(res));
                            }
                        });
                    }
                    else if (type == 'short') {
                        bl.vibrateShort({
                            success(res) {
                            },
                            fail(res) {
                                console.log("ASCSDK", 'BL vibrateShort调用失败', JSON.stringify(res));
                            }
                        });
                    }
                    break;
                }
            case Game_Platform.GP_Test:
                console.log("ASCSDK", 'Test 手机震动，需要在打包后会有真实效果', type);
                break;
            default:
                break;
        }
    }


    /**
     * 数据上报
     */
    reportMonitor() {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Oppo:
                {
                    if (qg.getSystemInfoSync().platformVersionCode < 1060) {
                        return;
                    }
                    qg.reportMonitor('game_scene', 0)
                    break;
                }
            default:
                break;
        }
    }


    /**
     * 事件上报
     */
    public reportAnalytics(params, data) {
        switch (SdkTools.getPlatform()) {
            case Game_Platform.GP_Tiktok:
                tt.reportAnalytics(params, data);
                break;
            default:
                break;
        }
    }


}

export default OtherFunctions 