import sdkConfig from "../../sdkConfig";
import SdkTools from "../../tools/SdkTools"

class BannerTest {
    private static instance: BannerTest

    public bannerUI = null;
    public bannerFakeBg = null;

    /**
     * BannerTest 单例
     */
    public static getInstance(): BannerTest {
        if (!BannerTest.instance) {
            BannerTest.instance = new BannerTest()
        }
        return BannerTest.instance
    }

    //创建横幅
    public createBanner() {

    }
    //展示横幅
    public showBanner() {
        if (this.bannerFakeBg) {
            console.log("ASCSDK", "Test 已存在测试Banner return");
            return;
        }

        console.log("ASCSDK", "Test showBanner====================");

        var self = this;
        let bannerTexture =
            [
                sdkConfig.ossUrl+'/SDK/SDKImage_1_0/TestRes/FakeNativeBannerBg.png'
            ]
        Laya.loader.load(bannerTexture, Laya.Handler.create(this, function () {
            self.bannerUI = bannerTexture[0];
            this.bannerFakeBg = new Laya.Sprite();
            this.bannerFakeBg.loadImage(this.bannerUI);
            if (Laya.stage.width < Laya.stage.height) {
                this.bannerFakeBg.width = Laya.stage.width;
                this.bannerFakeBg.height = Laya.stage.height * 0.18;
            }
            else {
                this.bannerFakeBg.width = Laya.stage.width / 2;
                this.bannerFakeBg.height = Laya.stage.height * 0.18;
            }
            // this.bannerFakeBg.x =0;
            this.bannerFakeBg.y = Laya.stage.height - this.bannerFakeBg.height;

            Laya.stage.addChild(this.bannerFakeBg);
            this.bannerFakeBg.zOrder = 30000;

        }));
    }
    // 隐藏横幅
    public hideBanner() {
        if (this.bannerFakeBg) {
            console.log("ASCSDK", "Test hideBanner==================");
            this.bannerFakeBg.removeSelf();
            this.bannerFakeBg = null;
        } else {
            console.log("ASCSDK", "Test 不存在测试banner return");
            return;
        }
    }


}

export default BannerTest 