import { ui } from "../ui/layaMaxUI";
import TipManager from "../common/TipManager";
//import PlayerData from "../../data/PlayerData";
import EventManager from "../common/EventManager";
import Display from "../common/Display";
import UtilTool from "../common/UtilTools";
import gSdk from "../common/platformsdk/platform";
import { AdsType, WxBannerScreen } from "../common/platformsdk/plat-def";
import UserData from "../common/data/UserData";
import SignData from "../common/data/SignData";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import AudioManager from "../common/AudioManager";
import { AudioType } from "../common/data/AudioData";
import UITool from "../common/UITool";
import AdTool from "../common/AdTool";
import Utils3D from "../scripts/mgr/Utils3D";
import { SdkHelper } from "../YAHD_LAYA_SDK/SdkHelper";

interface DaySign {
    signTime: number;
    signDay: number;
    prop: number;
}
const adwards = [100, 150, 200, 300, 400, 500, 600];
export default class UISign extends ui.commonUI.UISignUI {
    private static signData: DaySign = null;
    public static isSigned: boolean = false;
    public static mianPoped: boolean = false;       //主界面是否弹出过
    private rareCream: any = null;

    constructor() {
        super();
        this.height = Laya.stage.height;
        this.bg.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, "#23a8ea");
        UISign.onSignDay();
        // if(Platform.isWX){
        //     Platform.showBanner();
        // }
        this.PlatformUIInit();
    }

    onEnable() {
        SignData.inst.sign();
        SdkHelper.getInstacne().ShowInter(false);
    }

    static setMainSceneProped() {
        UISign.signData.prop = 1;
        UISign.saveSign();
        UISign.mianPoped = true;
    }

    static get mainProped() {
        return UISign.mianPoped;
    }

    static onSignDay() {
        if (UISign.signData) { return UISign.isSigned };
        if (null == UISign.signData) {
            UISign.signData = { signTime: 0, signDay: 0, prop: 0 };
            let signTime = Laya.LocalStorage.getItem("sign_time");
            let signDay = Laya.LocalStorage.getItem("sign_day");
            if (signTime && signTime != "") {
                UISign.signData.signTime = parseInt(signTime);
            }
            if (signDay && signDay != "") {
                UISign.signData.signDay = parseInt(signDay);
            }
        }

        //判断签到时间是否是同一天
        let day = UtilTool.Time.dayDiff(UISign.signData.signTime, new Date().getTime());
        let signDay = UISign.signData.signDay;
        if (day > 0) {        //隔天，可以签到
            if (signDay >= 7) {
                UISign.signData.signDay = 0;
            }
            UISign.isSigned = false;
        } else {  //当天已签到
            UISign.isSigned = true;
            //this.updateSigned();
        }
        return UISign.isSigned;
    }

    static saveSign() {
        Laya.LocalStorage.setItem("sign_time", `${UISign.signData.signTime}`);
        Laya.LocalStorage.setItem("sign_day", `${UISign.signData.signDay}`);
    }

    onAwake() {
        this.getMoreBtn.clickHandler = new Laya.Handler(this, this.ongetMore);
        this.getBtn.clickHandler = new Laya.Handler(this, this.onGet);
        this.closeBtn.clickHandler = new Laya.Handler(this, this.closeView);
        if (UISign.isSigned) {
            this.updateSigned();
        }

        for (let i = 0; i < 7; i++) {
            this.setSignedItem(i + 1);
        }

        //this.gold.text = `${UserData.getGold()}`;

        this.adjustLargeUI();
    }

    onDisable() {
        //EventManager.emit(GameEvent.UI_CLOSED);
        SdkHelper.getInstacne().ShowBanner();
    }

    updateSigned() {
        this.getMoreBtn.visible = false;
        this.getBtn.visible = false;
        let signImg = new Laya.Image("common/tips-25.png");
        signImg.anchorX = 0.5;
        signImg.anchorY = 0.5;
        signImg.name = "sign-day-1";
        signImg.x = Laya.stage.width / 2;
        signImg.y = this.frame.y + this.frame.height + signImg.height * 2;
        this.addChild(signImg);
    }

    //设置已签到
    setSignedItem(day) {
        let item = this.frame.getChildByName(`item${day}`) as Laya.Sprite;
        let signDay = UISign.signData.signDay;
        if (signDay >= day) {  //已经签到 
            let dayBg = item.getChildByName('daybg');
            let daytext = dayBg.getChildByName('day') as Laya.Text;
            daytext.text = '已签到';
            if (day < 7) {
                let goldIcon = item.getChildByName('goldIcon') as Laya.Sprite;
                goldIcon.visible = false;
            }
            let gold = item.getChildByName('gold') as Laya.Text;
            gold.visible = false;

            let gouImg = new Laya.Image("common/tips-10.png");
            gouImg.x = day != 7 ? item.width / 2 - gouImg.width / 2 : 196;
            gouImg.y = 25;
            item.addChild(gouImg);
        } else {
            if (day < 7) {
                let gold = item.getChildByName('gold') as Laya.Text;
                gold.text = `${adwards[day - 1]}`;
            }
        }
    }

    signAdward(more: number = 1) {
        UISign.signData.signDay++;
        UISign.signData.signTime = new Date().getTime();
        UISign.saveSign();
        if (UISign.signData.signDay <= 6) {
            let item = this.frame.getChildByName(`item${UISign.signData.signDay}`) as Laya.Sprite;
            let gold = more * adwards[UISign.signData.signDay - 1];
            UserData.inst.setGold(gold);
            //this.gold.text = `${UserData.getGold()}`;
            //PlayerData.Ins.addGold(gold);//根据开发者具体业务修改
            UITool.Ins.goldEffect(new Laya.Vector2(item.x + this.frame.x + item.width / 2, item.y + this.frame.y + item.height / 2), 10, this);
        } else { //第七天的奖励
            let item7 = this.frame.getChildByName('item7');
            let iconBg = item7.getChildByName('iconbg') as Laya.Image;
            let icon = iconBg.getChildByName('icon') as Laya.Image;
            iconBg.skin = "common/tips-11.png"
            // if (this.rareCream.type != CreamType.None) {
            //     UserData.unLockCreams(this.rareCream.type, this.rareCream.id);
            //     TipManager.Ins.showMsgTip('恭喜获得稀有道具');
            //     if (CreamType.Cream == this.rareCream.type) {
            //         icon.skin = `skin/cream_${this.rareCream.id}.png`;
            //     } else if (CreamType.Gun == this.rareCream.type) {
            //         icon.skin = `skin/gun_${this.rareCream.id}.png`;
            //     } else if (CreamType.Cake == this.rareCream.type) {
            //         icon.skin = `skin/cake_${this.rareCream.id}.png`;
            //     }
            //     icon.scale(0.4, 0.4);
            // } else {
            icon.skin = 'common/home-12.png';
            let item = this.frame.getChildByName(`item${UISign.signData.signDay}`) as Laya.Sprite;
            let gold = more * adwards[UISign.signData.signDay - 1];
            UserData.inst.setGold(gold);
            //this.gold.text = `${UserData.getGold()}`;
            //PlayerData.Ins.addGold(gold);//根据开发者具体业务修改
            UITool.Ins.goldEffect(new Laya.Vector2(item.x + this.frame.x + item.width / 2, item.y + this.frame.y + item.height / 2), 10, this);
            //}
        }
        //EventManager.emit(GameEvent.UPDATE_GOLD);
        EventManager.Ins.event("changeGold");
        UISign.isSigned = true;
        this.setSignedItem(UISign.signData.signDay);
        this.updateSigned();
        this.timer.once(2000, this, () => {
            this.closeView();
        });
    }

    goldEffect(pt: Laya.Vector2, goldCount: number = 10, callback: Function = null) {
        let count = goldCount;
        for (let i = 0; i < goldCount; i++) {
            let sprite = new Laya.Sprite();
            sprite.loadImage("common/home-12.png");
            let ptc = UtilTool.Math.randomCircle(10);
            sprite.x = pt.x + ptc.x;
            sprite.y = pt.y + ptc.y;
            sprite.scale(0.8, 0.8);
            this.addChild(sprite);
            Laya.Tween.to(sprite, { x: 20, y: 90 }, 500, Laya.Ease.bounceIn, Laya.Handler.create(this, () => {
                sprite.removeSelf();
                count--;
                if (count <= 0) {
                    callback && callback();
                }
            }), 50 * i);
        }
    }

    ongetMore() {
        SdkHelper.getInstacne().ShowRewardVideo((data:any,isEnd: boolean) => {
            if(!isEnd)
            {
                TipManager.Ins.showMsgTip("观看完整视频才能获得金币");
            }
            else
            {
                this.signAdward(5);
            }

         }, null, this);
    }

    onGet() {
        this.signAdward();
    }

    closeView() {
        Utils3D.bBtnClicked = false;
        this.timer.clearAll(this);
        this.close();
    }

    adjustLargeUI() {
        let uis: Array<Laya.Sprite> = [];
        if (Display.largeScreen()) { //如果是大屏
            uis.push(this.getChildByName('closeBtn') as Laya.Sprite);
            uis.push(this.getChildByName('goldinfo') as Laya.Sprite);
            uis.push(this.getChildByName('title') as Laya.Sprite);
            uis.push(this.getChildByName('frame') as Laya.Sprite);
            uis.push(this.getChildByName('getMoreBtn') as Laya.Sprite);
            uis.push(this.getChildByName('getBtn') as Laya.Sprite);
            let signday = this.getChildByName('sign-day-1') as Laya.Sprite;
            if (signday) {
                signday.y += 120;
            }
            for (let i = 0; i < uis.length; i++) {
                uis[i] && (uis[i].y += 120);
            }
        }
    }

    PlatformUIInit() {
        
        if (gSdk.isWX) {
                this.getChildByName("GoldSp")['y'] = 10;
        }
        
    }
}