    onMouseMove2(tx, ty): void {
        if (!Utils3D.bStart && !Utils3D.bDrawed) return;
        let pos = Utils3D.curTool.transform.position;
        tx = tx / 500;
        if(ty < 0){
            return;
        }
        ty = 0.002//ty / 6000;
        //console.log("callLaterFun  = ==== posZ = "+ty);
        if (this.bCut) {
            AudioManager.inst.playVibrator();
        }

        pos.x = tx;
        pos.y = ty;

        Laya.timer.callLater(this, this.callLaterFun, [pos]);//延迟执行
    }

    _tempT = 0;
    private callLaterFun(pos: Laya.Vector3) {
        if(Utils3D.bDrawed) return;
        //Utils3D.curTool.transform.localPositionX += pos.x;
        Utils3D.curTool.transform.localPositionZ += pos.y;
        let posZ = Utils3D.curTool.transform.localPositionZ;
        //console.log("callLaterFun  = ==== posZ = "+posZ);
        this.onHitRay();
    }

    private _imgW = 512;
    private _imgH = 512;
    _nTemp = 0.8;
    
    private onHitRay(): void {
        let tPos1: Laya.Vector3 = Utils3D.curTool.transform.position;
        if(Utils3D.bDrawed) return;
        let tPos = tPos1;
        //console.log("onHitRay = ==== tPos.z = "+tPos.z);
        if(tPos.z < 0.28) return;

        let posX = 256, posY = Math.abs(tPos.z-0.28) * 2000;
        let w = this._modW, h = this._modH;
        
        if(posY > this._nFinishY && !Utils3D.bDrawed){
            this.checkDraw(); //涂完
            return;
        }

        let x = posX, y = posY, px, py, w2 = w / 2, h2 = h / 2;
        px = 256;

        if (tPos.y < 0) {
            //py = Math.floor(h2 + (y / h * h2));
            py = Math.floor(h2 + (y * this._nTemp));
        } else {
            //py = Math.floor(h2 - (y / h * h2));
            py = Math.floor( (y * 3-40));
        }

        //console.log('中心点px,py  posX, poxY', px, py,posX,posY);
        if (this._prvePos.x == px && this._prvePos.y == py) {
            return;
        }
        let p: Laya.Vector2 = new Laya.Vector2(px, py);
        this._prvePos.x = px;
        this._prvePos.y = py;

        if(px >= this._modW || px <= 0){
            return;
        }
        //console.log("onHitRay = ==== px = "+px+" py == "+py);
        //this.Checking(p);
        let ww= 110;
        //let hh= 240;
        let minPos = new Laya.Vector2(px-ww, py);
        let maxPos = new Laya.Vector2(px+ww, py+12);
        this.Painting(p,minPos,maxPos);

        this._gameScene.updateProgress(344/48);
    }

