        // let stickNode = this._levelNode.getChildByName("Sticker");
        // this.curMode2[atIndex/2-1] = stickNode.getChildAt(atIndex/2-1)  as Laya.MeshSprite3D;
        // let mat2 = this.curMode2[atIndex/2-1].meshRenderer.material as Laya.BlinnPhongMaterial;  
        // mat2.albedoTexture = img2;

