export class PixelItem {
    public r: number;
    public g: number;
    public b: number;
    public a: number;

    constructor(red, green, blue, aval) {
        this.r = red;
        this.g = green;
        this.b = blue;
        this.a = aval;
    }
}

export class PixelItem2 {
    public r: number;
    public g: number;
    public b: number;
    public a: number;
    public status: boolean = false;
    constructor(_r, _g, _b, _a) {
        this.r = _r;
        this.g = _g;
        this.b = _b;
        this.a = _a;
    }
}
export class CoordItem {
    public min: number;
    public max: number;

    constructor(_min, _max) {
        this.min = _min;
        this.max = _max;
    }

}

