    public onTouchDown(e: Laya.Event): void {
        if (Utils3D.bStart == false) return;
        this.curTouchId = e.touchId;
        Utils3D.bMouseMoving = true;
        this.originPiont.setValue(e.stageX, e.stageY);
        this._gameScene.onTouchDown();
    }

    public onMoveEvent(e: Laya.Event): void {
        if (e.touchId != this.curTouchId || Utils3D.bStart == false || !Utils3D.bMouseMoving) return;
        var localPos: Laya.Point = this.globalToLocal(new Laya.Point(Laya.stage.mouseX, Laya.stage.mouseY), false);
        this.deltaX = e.stageX - this.originPiont.x//localPos.x - this.originPiont.x;
        this.deltaY = e.stageY - this.originPiont.y//localPos.y - this.originPiont.y;
        let angle = 180 - Math.atan2(this.deltaX, this.deltaY) * 180 / Math.PI;
        //if(Utils3D.nSteps == Utils3D.nCutLen+1){
        if (Utils3D.nGameState == Constant.State_Draw) {
            if (this._gameScene.drawColor && this.checkPos(this.deltaX, this.deltaY, 0.08, -0.03)) {
                this._gameScene.drawColor.onMouseMove2(this.deltaX, this.deltaY);
            }
        } else if (Utils3D.nGameState == Constant.State_Draw2) {
            if (this._gameScene.drawPaper) {
                this._gameScene.drawPaper.onMouseMove2(this.deltaX, this.deltaY);
            }
        } else {
            this.toolMove(this.deltaX, this.deltaY);
        }

        this.originPiont.setValue(e.stageX, e.stageY);
    }

    private onMouseUp(e: Laya.Event) {
        if (e.touchId != this.curTouchId) return;
        this.curTouchId = -1;
        //var time = Laya.timer.currTimer - this.touchDownTime; 
        Utils3D.bMouseMoving = false;
        this._gameScene.onMouseUp();
    }

    toolMove(dx, dy) {
        if (Utils3D.curTool) {
            var transform = Utils3D.curTool.transform;
            if (transform) {
                if (Utils3D.curTool.active && transform.localPositionX) {
                    if (this.checkPos(dx, dy, 0, -0.07)) {
                        dx = dx / 4000;
                        dy = dy / 4000;
                        transform.localPositionX += dx;
                        transform.localPositionZ += dy;
                    }
                }
            }
        }
    }

    checkPos(dx, dz, rightx, leftx) {
        var transform = Utils3D.curTool.transform;
        if (!transform) return;
        if (transform.localPositionX > rightx) {
            if (dx > 0) {
                return false;
            }
        }
        if (transform.localPositionX < leftx) {
            if (dx < 0) {
                return false;
            }
        }
        if (transform.localPositionZ > 0.38) {
            if (dz > 0) {
                return false;
            }
        }
        if (transform.localPositionZ < 0.23) {
            if (dz < 0) {
                return false;
            }
        }

        return true;
    }

    handUIMove() { //手指按下动画
        if ((Utils3D.nGameState == Constant.State_Mill && Utils3D.nSteps < 4) || (Utils3D.nGameState == 5 && Utils3D.nSteps < 4)) {
            this.imgHand.x = 349;
            this.imgHand.visible = true;
            Laya.Tween.to(this.imgHand, { scaleX: 1.2, scaleY: 1.2 }, 300, null, Laya.Handler.create(this, function () {
                this.imgRing.visible = false;
                Laya.Tween.to(this.imgHand, { scaleX: 1, scaleY: 1 }, 300, null, Laya.Handler.create(this, function () {
                    if ((Utils3D.nGameState == Constant.State_Mill && Utils3D.nSteps < 4) || Utils3D.nGameState == 5 && Utils3D.nSteps < 4) {
                        this.imgRing.visible = true;
                        Laya.Tween.to(this.imgHand, { alpha: 1 }, 500, null, Laya.Handler.create(this, this.handUIMove));
                    } else {
                        this.imgRing.visible = false;
                    }
                }))
            }));
        }
    }

