    RefreshList() {
        if (Utils3D.nGameState == 1 || Utils3D.nGameState == 3) return;
        this.list2.scrollTo(0);
        let datas = [];
        let res = "";
        let len = 6;
        if (Utils3D.nGameState == 2) {
            len = 14;
        } else if (Utils3D.nGameState == 4) {
            len = 11;
        } else if (Utils3D.nGameState == 5) {
            len = 10;
        }
        let taskIndex = 1;
        if (Utils3D.nGameState == Constant.State_Swear) {
            taskIndex = Utils3D._curData["Task"][0];
        } else {
            taskIndex = Utils3D._curData["Task"][Utils3D.nCurFinger];
        }
        for (let i = 1; i < len; i++) {
            if (i == taskIndex) {
                res = "res/image/item/" + Utils3D.nGameState + "_" + i + ".png";
                datas.push({
                    icon: res,
                    name: "item" + i,
                    curIndex: i - 1,
                    type: 1,
                    state: 1
                })
            }
        }

        for (let i = 1; i < len; i++) {
            if (i != taskIndex) {
                res = "res/image/item/" + Utils3D.nGameState + "_" + i + ".png";
                datas.push({
                    icon: res,
                    name: "item" + i,
                    curIndex: i - 1,
                    type: 1,
                    state: Utils3D.tUnlockData[Utils3D.nGameState][i - 1]
                })
            }
        }
        this.list2.array = datas;

        let resTab = "res/image/item/title_" + Utils3D.nGameState + ".png";
        this.imgTitle.skin = resTab;
    }

    onListRender(cell, index: number) {
        if (index > this.list2.array.length) {
            return;
        }

        let data = this.list2.array[index];
        if (!data) {
            return;
        }

        let icon = cell.getChildByName('icon') as Laya.Image;
        icon && (icon.skin = data.icon);

        let select = cell.getChildByName('selectImg') as Laya.Image;
        let imgType = cell.getChildByName("type") as Laya.Image;
        let imgVideo = cell.getChildByName("imgVideo") as Laya.Image;
        let name = cell.getChildByName('porssTxt') as Laya.Label;
        name && (name.text = data.name);

        // if(index == data.curIndex){
        //     select && (select.visible = true);
        //     //name.color = Laya.Color.GREEN;
        // }else{
        //     select && (select.visible = false);
        // }
        select && (select.visible = false);

        if (data.type == 2) {
            imgType && (imgType.visible = true);
        } else {
            imgType && (imgType.visible = false);
        }

        if (imgVideo) {
            if (Utils3D.bTryUse) {
                imgVideo && (imgVideo.visible = false);
            } else {
                if (data.state == 0) { //未解锁
                    imgVideo && (imgVideo.visible = true);
                } else {
                    imgVideo && (imgVideo.visible = false);
                }
            }

        }
    }

    onSelect(e, index) {
        if (e.type == Laya.Event.CLICK) {
            let select = e.target.getChildByName('selectImg') as Laya.Image;
            let imgVideo = e.target.getChildByName("imgVideo") as Laya.Image;
            if (imgVideo && imgVideo.visible) {
                let gate = Math.floor(Math.random() * 40);

                SdkHelper.getInstacne().ShowRewardVideo((data: any, isEnd: boolean) => {
                    if (!isEnd) {
                        TipManager.Ins.showMsgTip("观看完整视频才能解锁");
                    }
                    else {
                        EventManager.Ins.event("cut", gate);
                        //观看视频完回调
                        this.updateCell();
                        //Utils3D.nLookSelect = index;
                        this.blookedVideo = true;
                        let data = this.list2.array[index];
                        Utils3D.nLookSelect = data.curIndex;
                        console.log(" onSelect   nLookSelect ===", Utils3D.nLookSelect);
                        select && (select.visible = true);
                        //this.RefreshList();
                        Laya.timer.once(100, this, this.onListOk);
                    }

                }, null, this);

            } else {
                if (imgVideo) {
                    this.updateCell();
                    this.blookedVideo = false;
                    let data = this.list2.array[index];
                    Utils3D.nLookSelect = data.curIndex;;
                    select && (select.visible = true);
                    //Utils3D.bLookUse = true;
                    //this._gameScene.initLookTool();
                    Laya.timer.once(100, this, this.onListOk);
                }
            }

        }
    }

    updateCell() {
        for (var i = 0; i < this.list2.array.length; i++) {
            let node = this.list2.getCell(i) as Laya.Sprite;
            if (node) {
                let select = node.getChildByName('selectImg') as Laya.Image;
                select && (select.visible = false);
            }
        }
    }

    updateTask(bShow) {
        if (!bShow || (Utils3D.nCurScene == 1 && Utils3D.nGameState > 2 && !Utils3D.bTryUse)) {
            //this.taskBg.visible = false;
            //this.textTask.text = "可任意选择图案";
            this.ani3.stop();
            Laya.Tween.to(this.taskBg, { scaleX: 0, scaleY: 0 }, 300);
            return;
        }
        this.taskBg.visible = bShow;
        this.ani3.play();
        // this.taskBg.scaleX = 0;
        // this.taskBg.scaleY = 0;
        // Laya.Tween.to(this.taskBg,{scaleX:1,scaleY:1},300);
        let index = 0;
        if (Utils3D.nGameState == Constant.State_Swear) {
            index = Utils3D._curData["Task"][0];
            this.taskImg.skin = "res/image/item/2_" + index + ".png";
        } else if (Utils3D.nGameState >= Constant.State_Draw) {
            index = Utils3D._curData["Task"][Utils3D.nCurFinger];
            this.taskImg.skin = "res/image/item/" + Utils3D.nGameState + "_" + index + ".png";
        }
    }

    getTaskScore() { //任务得分
        let len = Utils3D._curData["Task"].length;
        if (len == 1) { //第一关
            if (Utils3D.nGameState == Constant.State_Swear) {
                return 100;
            } else {
                return 0;
            }
        } else {
            if (Utils3D.nGameState == Constant.State_Swear) {
                return 40;
            } else {
                return 60 / (len - 1);
            }
        }
    }

    updateLvPoint(bShow = true) {
        if (bShow) {
            this.barBg.visible = true;
            this.progressBar.width = 0;
            let skin1 = "res/image/state1.png";
            let skin2 = "res/image/state2.png"
            let skin3 = "res/image/state3.png"
            for (var i = 1; i < 5; i++) {
                var name = "img" + i;
                //编辑器name 命名才能获取到?
                var point = this.pointbg.getChildByName(name) as Laya.Image;
                if (i <= Utils3D.nStateLen) {
                    if (i < Utils3D.nStateSteps) {
                        point.skin = skin2;
                    } else if (i == Utils3D.nStateSteps) {
                        point.skin = skin3
                    } else {
                        point.skin = skin1;
                    }
                    point.visible = true;
                    this.pointbg.x = 156;
                } else {
                    point.visible = false;
                    this.pointbg.x = 186;
                }
            }
        } else {
            this.barBg.visible = false;
        }
    }

