    nextGame() {
        //this.close();
        let self = this;
        DialogManager.Ins.sceneClose(this.url)
        Laya.timer.once(50, this, function () {
            if (!Utils3D.bAdtoEnd) {
                self._gameScene.nextGame(Utils3D.bGameWin);
            } else {
                self._gameScene.loadGameState();
                gSdk.sdk.showBannerzs(WxBannerScreen.SCREEN_START);
            }
        })
    }

    onOpened(_data) {
        if (_data) {
            this.GetCoins = _data.gameresult ? (Utils3D.taskScore == 0 ? 30 : Utils3D.taskScore) : this.failcoins;
            this.UpdateUI(_data.gameresult);
            UserData.inst.setLevel();
        }
    }

    /**
      * 判定两个时间是否是同一天
      */
    public isSameDate(timestamp1: number, timestamp2: number): boolean {
        let date1 = new Date(timestamp1);
        let date2 = new Date(timestamp2);
        return date1.getFullYear() == date2.getFullYear()
            && date1.getMonth() == date2.getMonth()
            && date1.getDate() == date2.getDate();
    }

