import SdkTools from "../../tools/SdkTools";

class IntersTest {
    private static instance: IntersTest

    /**
     * 加载错误次数
     */
    public nativeIntersErrorTimes: number = 0;
    /**
     * 测试插屏组件
     */
    public NIUIInfo: any = null;


    /**
     * IntersTest 单例
     */
    public static getInstance(): IntersTest {
        if (!IntersTest.instance) {
            IntersTest.instance = new IntersTest()
        }
        return IntersTest.instance
    }

    /**
     * 展示测试插屏
     */
    public showInters(call) {
        console.log("ASCSDK", "Test showInters===========================")
        this.NIUIInfo =
            {
                layerBg: null,
                exit: null,
            }
        var urlList = [
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/layerBg.png",
            "https://youaihudong.oss-cn-shenzhen.aliyuncs.com/SDK/SDKImage_1_0/NativeInsertRes/nativeClose.png",
        ];

        let self = this;
        var onUIAssetsLoaded = function (callback) {
            self.NIUIInfo.layerBg = urlList[0];
            self.NIUIInfo.exit = urlList[1];

            console.log("ASCSDK", "Test showNativeInters===========================")

            var width = Laya.stage.width;
            var height = Laya.stage.height;

            var layerBg = new Laya.Sprite();
            layerBg.loadImage(self.NIUIInfo.layerBg);
            layerBg.width = width;
            layerBg.height = height;
            layerBg.alpha = 0.8;
            layerBg.zOrder = 30000;
            layerBg.mouseEnabled = true;
            Laya.stage.addChild(layerBg);

            var titleLabel = new Laya.Label();
            titleLabel.x = width / 2;
            titleLabel.y = height / 2;
            titleLabel.text = "这是一个测试插屏,请点击右上角关闭";
            titleLabel.color = "#ff0000";
            titleLabel.fontSize = 50;
            titleLabel.centerX = 0;
            titleLabel.centerY = 0;
            layerBg.addChild(titleLabel);

            var exit: Laya.Sprite = new Laya.Sprite();
            exit.loadImage(self.NIUIInfo.exit);
            exit.x = width * 3 / 4;
            exit.y = width * 2 / 5;
            exit.zOrder = 30010;
            exit.mouseEnabled = true;
            layerBg.addChild(exit);

            exit.on('click', this, (e: Laya.Event) => {
                console.log("ASCSDK", "关闭测试插屏");
                Laya.stage.removeChild(layerBg);
                call && call();
            });

        }

        Laya.loader.load(urlList, Laya.Handler.create(this, onUIAssetsLoaded));


    }


}

export default IntersTest 